import React from 'react'
import ReportServices from '../../../../../Services/Api/Reports/Reports'
import { LoadingService } from 'SharedComponents/LoadingComponent/LoadingComponent'
import classes from '../scss/medicalClinic.module.scss'
import Button from '@material-ui/core/Button'
import PreviewDownload from '../Helpers/PreviewDownload'
import { getNestedObjects } from 'Pages/Protected/Partner/Components/Utils/utils'
import moment from 'moment'
import ReportsTable from '../TableComponent'
import { getDoctorList } from '../Helpers/tableService'
import { affiliatedDoctorHeader } from '../Helpers/ReportsTableHeaderData'

const Hospital = () => {
  const [openPreview, setOpenPreview] = React.useState(false)
  const [datas, setDatas] = React.useState([])
  const [tableData, setTableData] = React.useState([])
  const [filters, setFilters] = React.useState({})
  const [cancelToken, setCancelToken] = React.useState(null)

  React.useEffect(() => {
    fetchData()
  }, [datas])

  const fetchData = async () => {
    LoadingService.loading(true)
    try {
      const newDatas = [...datas]

      const newMapData = newDatas.map(items => {
        return {
          status: getNestedObjects(items, ['status']),
          medical_id: getNestedObjects(items, ['medical_id']),
          full_name: getNestedObjects(items, ['full_name']),
          specialization: getNestedObjects(items, ['specialization']),
          facility_name: getNestedObjects(items, ['facility_name']),
          room: getNestedObjects(items, ['room']),
          sched: getNestedObjects(items, ['sched']),
          street: getNestedObjects(items, ['street']),
          barangay: getNestedObjects(items, ['barangay']),
          district: getNestedObjects(items, ['district']),
          region: getNestedObjects(items, ['region']),
          contact: getNestedObjects(items, ['contact'])
        }
      })
      setTableData(newMapData)
      LoadingService.loading(false)
    } catch (e) {
      console.log('error', e)
      LoadingService.loading(false)
    }
  }

  const headerProps = React.useMemo(() =>
    [
      {
        id: 'Status',
        accessor: 'status',
        sortable: true,
        searchable: true,
        size: '180px'
      },
      {
        id: 'Medical Partner ID',
        accessor: 'medical_id',
        sortable: true,
        searchable: true,
        size: '200px'
      },
      {
        id: 'Doctor Name',
        accessor: 'full_name',
        sortable: true,
        searchable: true,
        size: '200px'
      },
      {
        id: 'Specialization',
        accessor: 'specialization',
        sortable: true,
        searchable: true,
        size: '200px'
      },
      {
        id: 'Facility Name',
        accessor: 'facility_name',
        sortable: true,
        searchable: true,
        size: '200px'
      },
      {
        id: 'Room No.',
        accessor: 'room',
        sortable: true,
        searchable: true,
        size: '200px'
      },
      {
        id: 'Schedule',
        accessor: 'sched',
        sortable: true,
        searchable: true,
        size: '200px'
      },
      {
        id: 'Unit No./Street',
        accessor: 'street',
        sortable: true,
        searchable: true,
        size: '200px'
      },
      {
        id: 'Barangay',
        accessor: 'barangay',
        sortable: true,
        searchable: true,
        size: '200px'
      },
      {
        id: 'Province',
        accessor: 'district',
        sortable: true,
        searchable: true,
        size: '200px'
      }, {
        id: 'Region',
        accessor: 'region',
        sortable: true,
        searchable: true,
        size: '200px'
      },
      {
        id: 'Contact Number',
        accessor: 'contact',
        sortable: false,
        searchable: false,
        size: '180px'
      }
    ], [])

  return (
    <React.Fragment>
      <div className={classes.root}>
        <div id='root' className={classes.tableContainer}>
          <ReportsTable
            getData={(data, token) => {
              const args = { ...data }
              setCancelToken(token)
              return ReportServices.getAllMedicalDoctorsByType(args, token)
            }}
            setDatas={setDatas}
            type={'DOCTOR'}
            headerProps={headerProps}
            datas={tableData}
            setFilters={setFilters}
          />
        </div>
      </div>
      <div className={classes.previewButton}>
        <Button onClick={() => setOpenPreview(true)} variant='contained' className={classes.DownloadPreviewBtn}>
          Download
        </Button>
        {openPreview && <PreviewDownload
          openPreview={openPreview}
          setOpenPreview={setOpenPreview}
          filter={filters}
          headerColumn={affiliatedDoctorHeader}
          mongoSelect={{}}
          filename={`${moment().format('YYYYMMDD')}-doctors.csv`}
          infiniteScrollService={getDoctorList}
          downloadFunc={ReportServices.downloadDoctorList}
          type='DOCTOR'
          cancelToken={cancelToken}
        />}
      </div>
    </React.Fragment>
  )
}

export default Hospital
