import React, { memo, useState, useEffect } from 'react'
import AutoSuggest from 'Pages/Protected/Partner/Components/AutoSuggest/customInput'
import PartnerDoctorService from 'Services/Api/Partner/PartnerDoctorService'
import { setMedicalDentistId, setMedicalDentistPrcNumber } from 'Pages/Protected/Store/actions/Partner/dentalClinicAffilForm'
import classes from './MedicalPartner.module.scss'
import { useDispatch } from 'react-redux'

const MedicalPartnerIdAutoSuggest = props => {
  const dispatch = useDispatch()
  const { partnerId = '', errors = {}, isSubmitted = false, handlePartnerId, setData, data } = props
  const [searchTermID, setSearchTermID] = useState('')
  const [isFetching, setIsFetching] = React.useState(false)
  const [doctorIds, setDoctorIds] = React.useState([])

  useEffect(() => {
    const nData = { ...data }
    nData['medicalPartnerId'] = searchTermID
    setData(nData)
  }, [searchTermID])

  const handleChange = async (id, name) => {
    setSearchTermID(id)
    dispatch(setMedicalDentistId(id))
    if (typeof id === 'string' && id === '') {
      dispatch(setMedicalDentistPrcNumber(''))
      dispatch(setMedicalDentistPrcNumber(''))
    }
    if (typeof id === 'string' && id.length >= 6) {
      setIsFetching(true)
      try {
        const {
          data: { data }
        } = await PartnerDoctorService.getDentistIds({ id })
        setDoctorIds(data)
        setIsFetching(false)
      } catch (error) {
        console.log('error', error)
        setIsFetching(false)
      }
    } else {
      setDoctorIds([])
    }
  }

  return (
    <div id='medicalPartnerID' className={classes.medicalPartnerID}>
      <div id='medicalPartnerIdContainer' className={classes.medicalPartnerIdContainer}>
        <label>Medical Partner ID <span style={{fontSize: 12}}>(*If already affilliated to autofill details)</span></label>
        <AutoSuggest
          suggestions={doctorIds}
          error={errors['medicalPartnerId']}
          disabled={isSubmitted}
          value={partnerId}
          labelName='id'
          name='partnerId'
          searchTerm={searchTermID}
          onChange={handleChange}
          onSelect={handlePartnerId}
          isFetching={isFetching}
          setSearchTerm={setSearchTermID}
          show={6} // length of search string to show auto suggestions
        />
      </div>
    </div>
  )
}

export default memo(MedicalPartnerIdAutoSuggest)
