import React, { useState, useEffect, memo, useMemo } from 'react'
import classes from './AffiliationForm.module.scss'
import DentalCLinicForm from './DentalClinicForm/DentalClinicForm'
import DentistForm from './DentistForm/DentistForm'
import RemarksComponent from 'Pages/Protected/Partner/AffiliationFormEncoder/Components/RemarksComponent'
import TinStatusComponent from 'Pages/Protected/Partner/AffiliationFormEncoder/Components/TinStatusComponent'
import DentalAssociatesAndAffiliation from './AssociatesAndAffiliation'
import { fetchDentistData } from '../../DentalClinicInformation/dentalClinicService'
import { setMedicalDentistId, setMedicalDentistError } from 'Pages/Protected/Store/actions/Partner/dentalClinicAffilForm'
import { setDentistAffiliation } from 'Pages/Protected/Store/actions/affiliationType'

import { useDispatch } from 'react-redux'

const DentalClinicAffiliationForm = (props) => {
  const dispatch = useDispatch()

  const {
    isSubmitted,
    setValid,
    isTovalidate,
    setDentistData,
    setDentalData,
    dentistData,
    affiliatedData,
    dentalData,
    statusData,
    remarksData
  } = props

  const [dentalClinicForm, setDentalClinicForm] = useState({})

  const [dentistForm, setDentistForm] = useState({})

  const [statusForm, setStatusForm] = useState({})

  const [dentalAssociate, setDentalAssociates] = useState({})

  const [remarksForm, setRemarksForm] = useState({})

  const [dentistIsValid, setDentistIsValid] = useState(false)

  const [clinicIsValid, setClinicIsValid] = useState(false)

  const [statusIsValid, setStatusIsValid] = useState(false)

  const [remarksIsValid, setIsRemarksIsValid] = useState(false)

  const [isSelectedPartnerIDFetch, setIsSelectedPartnerIDFetch] = useState(false)

  const [dentistProfileDefaultValue, setDentistProfileDefaultValue] = useState({ ...dentistData })
  const [statusDefaultValue, setStatusDefaultValue] = useState({ ...statusData })

  useEffect(() => {
    const isValid = dentistIsValid && clinicIsValid && statusIsValid && remarksIsValid
    if (isValid) {
      setValid(true)
    } else {
      setValid(false)
    }
  }, [dentistIsValid, clinicIsValid, statusIsValid, remarksIsValid])

  useEffect(() => {
    let dental = Object.assign({}, dentalClinicForm, dentalAssociate, remarksForm)
    let dentist = Object.assign({}, dentistForm, statusForm)
    setDentistData(dentist)
    setDentalData(dental)
  }, [dentalClinicForm, dentalAssociate, remarksForm, dentistForm, statusForm])

  useEffect(() => {
    setDentistProfileDefaultValue(dentistData)
    setStatusDefaultValue(statusData)
  }, [dentistData])

  useEffect(() => {
    if (isSelectedPartnerIDFetch) {
      setTimeout(() => {
        setIsSelectedPartnerIDFetch(false) // reset validation trigger
      }, 300)
    }
  }, [isSelectedPartnerIDFetch])

  const dentist = useMemo(() => {
    return dentistProfileDefaultValue
  }, [dentistProfileDefaultValue])

  const handlePartnerId = async ({ id }) => {
    console.log('id selected', id)
    dispatch(setMedicalDentistId(id))
    dispatch(setMedicalDentistError(''))
    try {
      const { dentistProfile, tinStatus, dentistAffil } = await fetchDentistData(id)
      dispatch(setDentistAffiliation(dentistAffil))
      setDentistProfileDefaultValue(dentistProfile)
      setStatusDefaultValue(tinStatus)
      setIsSelectedPartnerIDFetch(true)
    } catch (error) {
      console.log('partner id error', error)
    }
  }

  return (
    <div id='root' className={classes.root}>
      <div id='topText' className={classes.topText}>
        DENTAL CLINIC INFORMATION
      </div>

      <DentalCLinicForm
        data={dentalClinicForm}
        setData={setDentalClinicForm}
        setIsValid={setClinicIsValid}
        isValid={clinicIsValid}
        isValidate={isTovalidate}
        defaultVal={dentalData}
        isSubmitted={isSubmitted}
      />

      <DentistForm
        data={dentistForm}
        setData={setDentistForm}
        setIsValid={setDentistIsValid}
        setDentistData={setDentistData}
        dentistData={dentistData}
        isValid={dentistIsValid}
        isValidate={isTovalidate}
        defaultVal={dentist}
        isSubmitted={isSubmitted}
        handlePartnerId={handlePartnerId}
        isSelectedPartnerIDFetch={isSelectedPartnerIDFetch}
      />

      <TinStatusComponent
        data={statusForm}
        setData={setStatusForm}
        setIsValid={setStatusIsValid}
        isValid={statusIsValid}
        isValidate={isTovalidate}
        defaultVal={statusDefaultValue}
        isSubmitted={isSubmitted}
      />

      <DentalAssociatesAndAffiliation
        data={dentalAssociate}
        setData={setDentalAssociates}
        defaultVal={affiliatedData}
        isSubmitted={isSubmitted}
      />

      <RemarksComponent
        hasPaddingLeft={false}
        data={remarksForm}
        setData={setRemarksForm}
        isValid={remarksIsValid}
        setIsValid={setIsRemarksIsValid}
        isValidate={isTovalidate}
        defaultVal={remarksData}
        isSubmitted={isSubmitted}
      />
    </div>
  )
}

export default memo(DentalClinicAffiliationForm)
