import React, { useState } from 'react'

import Table from '@material-ui/core/Table'
import TableBody from '@material-ui/core/TableBody'
import TableCell from '@material-ui/core/TableCell'
import TableContainer from '@material-ui/core/TableContainer'
import TableRow from '@material-ui/core/TableRow'
import Button from '@material-ui/core/Button'

import classes from './UploadFIleTable.module.scss'
import Preview from '../../../../AffiliationFormEncoder/Pages/PreviewImage/PreviewImage'
import Axios from 'axios'
import { setAffiliationType } from 'Pages/Protected/Store/actions/affiliationType'
import { useDispatch } from 'react-redux'
import ProfileService from 'Services/Api/Partner/ProfileService'
import GetFile from '../../../../../../../Services/Api/Partner/GetFileUpload'

const URL = `${process.env.REACT_APP_SERVER_API}`

const PhotoOfPhicID = () => {
  return (
    <div id='PhotoOfPhicID' className={classes.WithSubScript}>
      <text>Photo of PHIC ID</text>
      <sub id='subscript-PhotoOfPhicID' className={classes.subscript}>
        *For PHIC Accredited Doctors
      </sub>
    </div>
  )
}

const SwornDeclaration = () => {
  return (
    <div id='SwornDeclaration' className={classes.WithSubScript}>
      <text>Sworn Declaration of Gross Receipts/Sales</text>
      <sub id='subscript-SwornDeclaration' className={classes.subscript}>
        *For New Affiliation of Doctor
      </sub>
    </div>
  )
}

const createData = (description, uploadButton) => {
  return { description, uploadButton }
}

const UploadFIleTable = props => {
  const { buttonText, paramId, medPartnerId, onPreviewFile } = props
  let dispatch = useDispatch()

  const forView = false
  React.useEffect(() => {
    fetchData()
  }, [])
  const [mainFiles, setMainFiles] = useState([])

  const fetchData = async () => {
    try {
      const partnerId = paramId === null || paramId === undefined ? medPartnerId : paramId
      const filesData = await GetFile.getFile(partnerId)
      dispatch(setAffiliationType({ id: partnerId }))
      for (let fileData of filesData.data.data) {
        let tmpFiles = fileData.files
        setMainFiles(previousState => ({
          ...previousState,
          [fileData.file_type]: tmpFiles.map(file =>
            Object.assign(file, {
              preview: `${URL}/uploads/${file.path}`,
              fileType: fileData.file_type
            })
          )
        }))
      }
    } catch (e) {
      console.log('error', e)
    }
  }

  const rows = [
    createData(
      <Preview
        onPreviewFile={onPreviewFile}
        desc={'Doctor Resume'}
        fileType={'doctor-resume'}
        mainFiles={mainFiles}
        setMainFiles={setMainFiles}
        buttonText={'Upload'}
        forView={forView}
      />
    ),
    createData(
      <Preview
        onPreviewFile={onPreviewFile}
        desc={'Copy of Certificate of Fellowship and Diplomate'}
        fileType={'certificate-of-fellowship-diplomate'}
        mainFiles={mainFiles}
        setMainFiles={setMainFiles}
        buttonText={'Upload'}
        forView={forView}
      />
    ),
    createData(
      <Preview
        onPreviewFile={onPreviewFile}
        desc={'Recent 2x2 Photo'}
        fileType={'recent-photo'}
        mainFiles={mainFiles}
        setMainFiles={setMainFiles}
        buttonText={'Upload'}
        forView={forView}
      />
    ),
    createData(
      <Preview
        onPreviewFile={onPreviewFile}
        desc={<PhotoOfPhicID />}
        fileType={'photo-phic-id'}
        mainFiles={mainFiles}
        setMainFiles={setMainFiles}
        buttonText={'Upload'}
        forView={forView}
      />
    ),
    createData(
      <Preview
        onPreviewFile={onPreviewFile}
        desc={'Photocopy of PRC License ID'}
        fileType={'prc-license-id'}
        mainFiles={mainFiles}
        setMainFiles={setMainFiles}
        buttonText={'Upload'}
        forView={forView}
      />
    ),
    createData(
      <Preview
        onPreviewFile={onPreviewFile}
        desc={<SwornDeclaration />}
        fileType={'sworn-declaration'}
        mainFiles={mainFiles}
        setMainFiles={setMainFiles}
        buttonText={'Upload'}
        forView={forView}
      />
    ),
    createData(
      <Preview
        onPreviewFile={onPreviewFile}
        desc={'BIR COR 2303'}
        fileType={'bir-cor-2303'}
        mainFiles={mainFiles}
        setMainFiles={setMainFiles}
        buttonText={'Upload'}
        forView={forView}
      />
    )
  ]

  return (
    <TableContainer id='tableContainer' className={classes.tableContainer}>
      <Table className={classes.table} size='small' aria-label='a dense table'>
        <TableBody id='table-body'>
          {rows.map((row, index) => (
            <TableRow id='TableRow' className={classes['table-row']} key={index}>
              <TableCell id='description-row' className={classes['description-row']} component='th' scope='row'>
                {row.description}
              </TableCell>
            </TableRow>
          ))}
        </TableBody>
      </Table>
    </TableContainer>
  )
}

export default UploadFIleTable
