import React, { useState } from 'react'
import Paper from '@material-ui/core/Paper'
import UploadFileTable from '../../Components/UploadFIleTable/UploadFIleTable'
// import FileDrawer from '../../../../Components/FileDrawer/FileDrawer'
import FileViewer from '../../../../Components/FileViewer/FileViewer'
import classes from './OtherDocuments.module.scss'

const OtherDocuments = props => {
  const [onClickPreview, setOnClickPreview] = React.useState(false)
  const [previewData, setPreviewData] = React.useState({})

  const handleOnPreview = (event, file) => {
    event.preventDefault()
    setPreviewData({
      type: file.mimetype,
      file: file.preview
    })
    setOnClickPreview(true)
  }
  return (
    <div id='root' className={classes.root}>
      {onClickPreview && (
        <FileViewer open={onClickPreview} setOpen={setOnClickPreview} type={previewData.type} file={previewData.file} />
      )}
      <div id='container' className={classes.container}>
        <Paper id='viewFile' className={classes.viewFile}>
          <div id='topText' className={classes.topText}>
            <text>Document Description</text>
          </div>
          <div id='tables'>
            <UploadFileTable buttonText={'Upload'} paramId={props.paramId} onPreviewFile={handleOnPreview} />
          </div>
        </Paper>
      </div>
    </div>
  )
}

export default OtherDocuments
