import React, { useContext } from 'react'
import Button from '@material-ui/core/Button'
import classes from './index.module.scss'
import HospitalInformation from '../PartnerInformation/HospitalInformation/PartnerInformation/PartnerInformation'

import { useParams } from 'react-router-dom'
import HospitalProfileParamsContext from '../context/HospitalProfileParamsContext'
import HospitalProfileContext from './context'
import { useDispatch } from 'react-redux'
import { setListOfAffiliatedContacts } from '../../../../../Store/actions/Partner/listOfAffiliatedContacts'
import EditHospitalInformation from './HospitalInformation/EditPartnerInformation/EditHospitalInformation'
import ProfileService from '../../../../../../../Services/Api/Partner/ProfileService'
import useAsyncHook from '../../../../CustomHooks/useAsyncHook2'
import { getNestedObjects, getNestedArrayOfObjects } from '../../../../Components/Utils/utils'
import InitialState from './HospitalInformation/Utilities/InitialState'
import Destructure from './HospitalInformation/Utilities/Destructure'

const PartnerHospitalInformation = props => {
  const dispatch = useDispatch()
  const { facilityId } = useParams()
  const {
    setParamId,
    isToUpdate,
    setIsToUpdate,
    setChangeName,
    isEdit,
    setIsEdit,
    setHeaderName,
    setStatus,
    status
  } = useContext(HospitalProfileParamsContext)

  const [profile, setProfile] = React.useState({})

  const [discountTypeData, setDiscountType] = React.useState([])

  const [form] = React.useState(InitialState)

  const [dataGet, errorGet, loadingGet, executeAsyncGet] = useAsyncHook((dataID, token) => {
    const args = dataID.facilityId
    return ProfileService.getClinicById(args, token)
  })

  const [dataGetDiscount, errorGet2, loadingGet2, executeAsyncGetDiscount] = useAsyncHook((dataID, token) => {
    const args = dataID.facilityId
    return ProfileService.getFacilityDiscount2(args, token)
  })

  React.useEffect(() => {
    let mounted = true

    if (mounted) {
      fetchAsync()
      setParamId(facilityId)
      setIsToUpdate(false)
    }

    return () => { mounted = false; setIsToUpdate(false) }
  }, [facilityId])

  const fetchAsync = async () => {
    await executeAsyncGet({facilityId})
  }

  React.useEffect(() => {
    if (loadingGet) {
      fetchData()
    }
  }, [dataGet])

  const fetchData = async () => {
    try {
      const newData = getNestedObjects(dataGet, ['data'])
      const callFlow = getNestedObjects(dataGet, ['data', 'callFlow'])
      const contacts = getNestedObjects(dataGet, ['data', 'contacts'])
      await executeAsyncGetDiscount({facilityId})
      let contactsObj = { callFlow, contacts }
      setHeaderName(getNestedObjects(dataGet, ['data', 'name']))

      dispatch(setListOfAffiliatedContacts(contactsObj))

      const dateNow = Date.parse(Date())
      const dissaffiliationDate = getNestedObjects(newData, ['disaffiliation', 'date'])
      const dateOfDissaAffiliation = Date.parse(dissaffiliationDate)
      const forDissafiliaiton = true

      if (dateNow >= dateOfDissaAffiliation && dissaffiliationDate.length > 0 && dissaffiliationDate !== '1970-01-01T00:00:07.000Z') {
        const newDataValue = { ...newData }
        const result = Destructure(form, newDataValue, forDissafiliaiton)
        ProfileService.updateHospitalProfile(facilityId, result)
        newDataValue['status'] = 'Inactive'
        setStatus('Inactive')
        setProfile(newDataValue)
      } else {
        setProfile(newData)
        setStatus(getNestedObjects(dataGet, ['data', 'status']))
      }

      setProfile(newData)
    } catch (e) {
      console.log(e)
    }
  }

  React.useEffect(() => {
    if (loadingGet2) {
      fetchDiscount()
    }
  }, [dataGetDiscount])

  const fetchDiscount = () => {
    const discountData = [ ...getNestedObjects(dataGetDiscount, ['data']) ]
    setDiscountType(discountData.map(items => getNestedArrayOfObjects(items, ['discount_type'])))
  }

  const discountType = React.useMemo(() => discountTypeData, [discountTypeData])

  return (
    <React.Fragment>
      <div id='editButton' className={classes.editButton}>
        <div id='buttonContainer'>
          <Button variant='contained' onClick={() => setIsToUpdate(true)} className={classes.buttonContainer}>
            <div id='insideText' className={classes.insideText}>
              <label> Edit Partner </label>
              <label> Information </label>
            </div>
          </Button>
        </div>
      </div>
      <HospitalProfileContext.Provider
        value={{
          profile,
          setIsToUpdate,
          setProfile,
          setChangeName,
          isEdit,
          setIsEdit,
          setHeaderName,
          setStatus,
          status,
          discountType,
          isToUpdate
        }}
      >
        {!isToUpdate && <HospitalInformation />}
        {isToUpdate && <EditHospitalInformation profile={profile} />}
      </HospitalProfileContext.Provider>
    </React.Fragment>
  )
}

export default React.memo(PartnerHospitalInformation, (prevProps, nextProps) => {
  try {
    if (
      prevProps.HospitalInformationData === nextProps.HospitalInformationData &&
      prevProps.profile === nextProps.profile &&
      prevProps.isEdit === nextProps.isEdit &&
      prevProps.discountType === nextProps.discountType &&
      prevProps.setIsToUpdate === nextProps.setIsToUpdate
    ) {
      return true
    }
    return false
  } catch (err) {
    return false
  }
})
