import moment from 'moment'
import { ValidateDateFormat } from '../../../../../Components/Validators/Validators'
import { YearDateFormat, DateErrorHandler, OfficeLocationDefault, getNestedArrayOfObjects, getNestedObjects, CheckIfstring } from '../../../../../Components/Utils/utils'

const Destructure = (profile, newForms) => {
  const assignments = getNestedArrayOfObjects(profile, ['facility_assignment', 'assignments'])

  newForms['id'] = getNestedObjects(profile, ['id'])
  newForms['firstName'] = getNestedObjects(profile, ['name', 'first'])
  newForms['lastName'] = getNestedObjects(profile, ['name', 'last'])
  newForms['middleName'] = getNestedObjects(profile, ['name', 'middle'])
  newForms['suffix'] = getNestedObjects(profile, ['name', 'suffix'])
  newForms['officeLocation'] = OfficeLocationDefault(getNestedObjects(profile, ['office_location']))

  newForms['birthdate'] = profile.birthdate ? YearDateFormat(profile.birthdate) : ''
  newForms['civilStatus'] = profile.civil_status ? profile.civil_status : ''
  newForms['assignments'] = assignments
  newForms['exclusiveTo'] = getNestedObjects(profile, ['facility_assignment', 'exclusive_to'])
  newForms['payee'] = getNestedObjects(profile, ['treasury', 'payee'])
  newForms['reason'] = getNestedObjects(profile, ['reason'])
  newForms['gender'] = getNestedObjects(profile, ['gender'])
  newForms['mainSpecialization'] = getNestedObjects(profile, ['specialization', 'main'])
  newForms['majorSpecialization'] = getNestedObjects(profile, ['specialization', 'major'])
  newForms['subSpecialization'] = getNestedObjects(profile, ['specialization', 'sub'])
  newForms['otherSpecialization'] = getNestedObjects(profile, ['specialization', 'other'])
  newForms['status'] = getNestedObjects(profile, ['status'])
  newForms['prcNumber'] = getNestedObjects(profile, ['license', 'prc', 'number'])
  newForms['prcExpiration'] = profile.license ? YearDateFormat(profile.license.prc.expiration_date) : ''
  newForms['philhealthNumber'] = getNestedObjects(profile, ['philhealth', 'accreditation_number'])
  newForms['philhealthExpiration'] = profile.philhealth
    ? ValidateDateFormat(moment(profile.philhealth.expiration_date).format('MM/DD/YYYY'))
      ? YearDateFormat(profile.philhealth.expiration_date)
      : ''
    : ''
  newForms['pmaNumber'] = getNestedObjects(profile, ['license', 'pma'])
  newForms['stNumber'] = getNestedObjects(profile, ['license', 's2'])
  newForms['ptrNumber'] = getNestedObjects(profile, ['license', 'ptr'])
  newForms['tinNumber'] = getNestedObjects(profile, ['treasury', 'tin'])
  newForms['vatTaxRate'] = getNestedObjects(profile, ['treasury', 'vat', 'tax_rate'])
  newForms['tinStatus'] = getNestedObjects(profile, ['treasury', 'tin_status'])
  newForms['non_vatable'] = getNestedObjects(profile, ['treasury', 'non_vatable'])
  newForms['processType'] = getNestedObjects(profile, ['process_type'])
  newForms['paymentTerms'] = getNestedObjects(profile, ['treasury', 'payment_terms'])

  newForms['serviceRate'] = CheckIfstring(getNestedObjects(profile, ['professional_fees', 'rate']))
  newForms['withTax'] = getNestedObjects(profile, ['treasury', 'withHoldingTax'])
  newForms['clinicSchedule'] = getNestedObjects(profile, ['facility_info', '0', 'clinic_schedule'])
  newForms['ahmopi'] = getNestedObjects(profile, ['ahmopi'])
  newForms['facilityName'] = getNestedObjects(profile, ['facility_info', '0', 'name'])
  newForms['address'] = getNestedObjects(profile, ['facility_info', '0', 'full_address'])

  newForms['dateSubmitted'] = profile.affiliation_status
    ? ValidateDateFormat(moment(profile.affiliation_status.date_submitted).format('MM/DD/YYYY'))
      ? YearDateFormat(profile.affiliation_status.date_submitted)
      : ''
    : ''
  newForms['dateOfDisaffiliation'] = profile.disaffiliation
    ? ValidateDateFormat(moment(profile.disaffiliation.date).format('MM/DD/YYYY'))
      ? YearDateFormat(profile.disaffiliation.date)
      : ''
    : ''

  return newForms
}

const SavingData = form => {
  let obj = {}

  obj.id = form['id']
  obj.status = form['status']
  obj.officeLocation = form['officeLocation']
  obj.dateSubmitted = DateErrorHandler(form['dateSubmitted'])
  obj.dateOfDisaffiliation = DateErrorHandler(form['dateOfDisaffiliation'])
  obj.assignments = form['assignments']
  obj.exclusiveTo = form['exclusiveTo']
  obj.firstName = form['firstName']
  obj.middleName = form['middleName']
  obj.lastName = form['lastName']
  obj.suffix = form['suffix']
  obj.birthdate = DateErrorHandler(form['birthdate'])
  obj.civilStatus = form['civilStatus']
  obj.gender = form['gender']
  obj.mainSpecialization = form['mainSpecialization']
  obj.majorSpecialization = form['majorSpecialization']
  obj.subSpecialization = form['subSpecialization']
  obj.otherSpecialization = form['otherSpecialization']
  obj.pmaNumber = form['pmaNumber']
  obj.ptrNumber = form['ptrNumber']
  obj.stNumber = form['stNumber']
  obj.prcNumber = form['prcNumber']
  obj.prcExpiration = form['prcExpiration']
  obj.philhealthNumber = form['philhealthNumber']
  obj.philhealthExpiration = DateErrorHandler(form['philhealthExpiration'])
  obj.tinNumber = form['tinNumber']
  obj.ahmopi = form['ahmopi']
  obj.tinStatus = form['tinStatus']
  obj.non_vatable = form['non_vatable']
  obj.vatTaxRate = form['vatTaxRate']
  obj.withTax = form['withTax']
  obj.facilityName = form['facilityName']
  obj.reason = form['reason']
  obj.address = form['address']
  obj.payee = form['payee']
  obj.clinicSchedule = form['clinicSchedule']
  obj.paymentTerms = form['paymentTerms']
  obj.serviceRate = form['serviceRate']
  obj.processType = form['processType']
  obj.type = 'DOCTOR'

  return obj
}

export { Destructure, SavingData }
