const InitialState = {
  data: [],
  pageData: {
    offset: 0,
    limit: 10,
    page: 1,
    count: 0
  },
  filter: {},
  sort: {
    isSort: true,
    field: '',
    direction: 'asc'
  }

}

const TableReducers = (state = InitialState, action) => {
  switch (action.type) {
    case 'SET_DATA':
      return { ...state, data: action.payload }

    case 'SET_PAGE':
      const offset = action.payload.offset
      const limit = action.payload.limit
      const page = action.payload.page
      const count = action.payload.count
      return { ...state, pageData: { ...state.pageData, offset: offset, limit: limit, page: page, count: count } }

    case 'SET_FILTER':
      const name = action.payload.name
      const value = action.payload.value

      if (typeof name === 'undefined' || typeof value === 'undefined') return {...state, filter: {}}
      return { ...state, filter: { ...state.filter, [name]: value }, pageData: { ...state.pageData, offset: 0, page: 1 } }

    case 'SET_SORT':
      if (state.data.length === 0) return {...state}

      const nameToSort = action.payload
      let newSort

      if (state.sort.isSort === true) {
        newSort = { ...state.sort, field: nameToSort, direction: 'asc', isSort: false }
      } else {
        newSort = { ...state.sort, field: nameToSort, direction: 'desc', isSort: true }
      }

      return { ...state, sort: newSort }

    default:
      return {...state}
  }
}

export default TableReducers
