import React from 'react'
import classes from './index.module.scss'
import { Table, TableContainer, TableBody, TableHead, FormControl, Select, MenuItem } from '@material-ui/core'
import useAsyncHook from 'Pages/Protected/Partner/CustomHooks/useAsyncHook2'
import Pagination from '@material-ui/lab/Pagination'
import Header from '../Header'
import Body from '../Body'
import { getNestedArrayOfObjects, getNestedObjects } from 'Pages/Protected/Partner/Components/Utils/utils'
import { SET_PAGE, SET_DATA, SET_FILTER } from '../Store/actions'
import { useDispatch, useSelector } from 'react-redux'
import useDebounce from 'Pages/Protected/Partner/CustomHooks/useDebounce'
import { LoadingService } from 'SharedComponents/LoadingComponent/LoadingComponent'

const TableDisplay = props => {
  const state = useSelector(state => state.reportsTable)
  const dispatch = useDispatch()
  const filterDebounce = useDebounce(state.filter, 300)

  const { headerProps = [], getData = {}, datas = [], type = '', setDatas = null, contractRenewal = false, setFilters, ahmopiType = false } = props

  const [dataGet, errorGet, loadingGet, executeAsyncGet] = useAsyncHook(getData)

  const FetchAsyncData = async () => {
    await executeAsyncGet({
      limit: state.pageData.limit,
      filter: state.filter,
      offset: (state.pageData.page - 1) * state.pageData.limit,
      type: type,
      ahmopiType: ahmopiType,
      sort: `${state.sort.field}|${state.sort.direction}`,
      page: state.pageData.page
    })
  }

  React.useEffect(() => {
    let mounted = true

    if (mounted) {
      FetchAsyncData()
    }

    return () => {
      setDatas([])
      dispatch(SET_DATA([]))
      dispatch(SET_FILTER({}))
      mounted = false
    }
  }, [])

  const list = React.useMemo(() => getNestedArrayOfObjects(dataGet, ['data', 'list']), [loadingGet, dataGet])

  const fetchDataIfLoading = () => {
    list.length > 0 || state.data.length > 0 ? LoadingService.loading(false) : LoadingService.loading(true)
    setTimeout(() => {
      if (list.length === 0) {
        LoadingService.loading(false)
      }
    }, 1000)
  }

  React.useEffect(() => {
    fetchDataIfLoading()
    state.data.length > 0 ? LoadingService.loading(false) : LoadingService.loading(true)
  }, [loadingGet, dataGet, state.data])

  React.useEffect(() => {
    const list = getNestedArrayOfObjects(dataGet, ['data', 'list'])
    const count = getNestedObjects(dataGet, ['data', 'count'])
    const offset = getNestedObjects(dataGet, ['data', 'offset'])
    const limit = getNestedObjects(dataGet, ['data', 'limit'])
    LoadingService.loading(true)

    if (loadingGet || list.length > 0) {
      dispatch(SET_PAGE({
        offset: offset,
        limit: limit,
        page: state.pageData.page,
        count: Math.ceil(count / limit)
      }))
      dispatch(SET_DATA(list))
      LoadingService.loading(false)
    } else {
      dispatch(SET_DATA([]))
    }
  }, [dataGet])

  React.useMemo(() => {
    FetchAsyncData()
  }, [state.pageData.page, state.pageData.offset, state.pageData.limit, state.pageData.count, filterDebounce, state.sort])

  React.useEffect(() => {
    if (contractRenewal || ahmopiType) {
      FetchAsyncData()
    }
  }, [type, ahmopiType])

  React.useEffect(() => {
    if (state.data.length > 0) {
      setDatas(state.data)
    } else {
      setDatas([])
    }
  }, [state.data])

  React.useEffect(() => {
    if (setFilters) {
      setFilters(state.filter)
    }
  }, [state.filter])

  return (
    <div id='root' className={classes.root}>
      <TableContainer id='table-root' className={classes.tableRoot}>
        <Table id='tableWrapper' className={classes.tableWrapper}>
          <TableHead id='tableHead' className={classes.tableHead}>
            {
              <Header
                headerProps={headerProps}
              />
            }
          </TableHead>
          <TableBody id='tableBody' className={classes.tableBody}>
            <React.Fragment>
              {

                <Body
                  headerProps={headerProps}
                  datas={datas}
                />

              }
            </React.Fragment>
          </TableBody>
        </Table>
      </TableContainer>
      <div id='pagination' className={classes.pagination}>
        <div id='tableLabel' className={classes.tableLabel}>
          <span />
        </div>
        <div className={classes.options}>
          <span id='showText' className={classes.showText}>
            <p>Show</p>
          </span>
          <FormControl size='small' variant='outlined'>
            <Select
              style={{ backgroundColor: 'white' }}
              value={state.pageData.limit}
              onChange={e => dispatch(SET_PAGE({
                page: state.pageData.page,
                limit: Number(e.target.value),
                offset: state.pageData.offset,
                count: state.pageData.count
              }))}
            >
              <MenuItem value={10}>10</MenuItem>
              <MenuItem value={15}>15</MenuItem>
              <MenuItem value={20}>20</MenuItem>
            </Select>
          </FormControl>
          <Pagination
            count={state.pageData.count}
            page={state.pageData.page}
            onChange={(e, value) => dispatch(SET_PAGE({ page: value, offset: value * state.pageData.offset, limit: state.pageData.limit, count: state.pageData.count }))}
            showFirstButton
            showLastButton
          />
        </div>
      </div>
    </div>
  )
}

export default React.memo(TableDisplay)
