import React from 'react'
import FileViewer from 'react-file-viewer'
import Modal from '@material-ui/core/Modal'
import Button from '@material-ui/core/Button'

import classes from './FileViewer.module.scss'

const NotSupportedComponent = props => {
  return <p>File is not supported for preview.</p>
}

const UploadFileViewer = props => {
  const { open = false, setOpen = () => {}, type = '', file = '' } = props
  const [mimeType, setMimeType] = React.useState('')

  React.useEffect(() => {
    console.log('File', type, file)
    const mimeTypes = [
      'png',
      'jpg',
      'jpeg',
      'gif',
      'bmp',
      'pdf',
      'csv',
      'xlsx',
      'docx',
      'mp4',
      'webm',
      'mp3',
      'mpeg',
      'wordprocessingml.document',
      'spreadsheetml.sheet'
    ]
    let _type = mimeTypes.find(_type => type.includes(_type))
    if (_type === 'jpeg') {
      _type = 'png'
    } else if (_type === 'mpeg') {
      _type = 'mp3'
    } else if (_type === 'wordprocessingml.document') {
      _type = 'docx'
    } else if (_type === 'spreadsheetml.sheet') {
      _type = 'xlsx'
    }

    setMimeType(_type)
  }, [])

  return (
    <Modal className={classes.root} open={open} onClose={() => setOpen(false)}>
      <div className={classes.container}>
        <Button className={classes.btn} variant='contained' color='secondary' onClick={() => setOpen(false)}>
          Close
        </Button>
        <FileViewer
          className={classes.fileViewer}
          fileType={mimeType}
          filePath={file}
          unsupportedComponent={NotSupportedComponent}
          onError={e => console.log('Error', e)}
        />
      </div>
    </Modal>
  )
}

export default UploadFileViewer
