import React from 'react'
import MaintenanceTable from '../Helper/Table'
import RoomTypeService from '../../../../../Services/Api/RoomType/RoomType'
import AddRoomType from './AddRoomType'
import { useSelector } from 'react-redux'

const breadcrumbs = {
  Medical: {
    text: 'MEDICAL COMPLIANCE ASSISTANT MANAGER',
    to: '/'
  },
  Maintenance: {
    text: 'MAINTENANCE TABLE',
    to: '/'
  },
  Table: {
    text: 'ROOM TYPE MAINTENANCE',
    to: '/RoomTypeMaintenance'
  }
}

const RoomTypes = props => {
  const stateValue = useSelector(state => state.MaintenanceTable)

  return (
    <React.Fragment>
      <AddRoomType stateValue={stateValue} />
      <MaintenanceTable
        getData={(data, token) => {
          const args = { ...data }
          return RoomTypeService.getRoomTypes(args, token)
        }}
        updateData={(data, token) => {
          const args = { ...data }
          return RoomTypeService.updateRoomType(args)
        }}

        tableHeaderProps={[
          { name: 'Room Code', id: 'code', inputFields: true, sortable: true, editable: false, size: '90%', inputType: 'none', type: 'text', multiline: false },
          { name: 'Description', id: 'desc', inputFields: true, sortable: true, editable: true, size: '100%', inputType: 'input', type: 'text', multiline: true },
          { name: 'Status', id: 'status', inputFields: false, sortable: false, editable: true, size: '80%', inputType: 'dropDown', type: 'text', multiline: false },
          { name: 'Remarks', id: 'remarks', inputFields: true, sortable: true, editable: true, size: '100%', inputType: 'input', type: 'text', multiline: true }
        ]}
        open={props.open}
        setOpen={props.setOpen}
        headerTitle={'Room Type Maintenance'}
        breadcrumbs={breadcrumbs}
        addButtonTitle={'Add Room Type'}
        updateButtonTitle={'Update'}
        paginationlabel={'Room Types'}
      />

    </React.Fragment>
  )
}

export default RoomTypes
