import React from 'react'
import Button from '@material-ui/core/Button'
import classes from './index.module.scss'
import DentalClinicInformation from '../PartnerInformation/DentalClinicInformation/PartnerInformation/PartnerInformation'
import UpdateDentalClinicInformation from '../PartnerInformation/DentalClinicInformation/EditPartnerInformation/EditPartnerInformation'
import ClinicProfileContext from './context'
import { GetDentalClinicProfile } from './services/DentalClinicService'
import ProfileService from '../../../../../../../Services/Api/Partner/ProfileService'
import { useParams } from 'react-router-dom'
import DentalClinicProfileParamsContext from '../context/DentalClinicProfileParamsContext'
import { useDispatch } from 'react-redux'
import { setListOfAffiliatedContacts } from '../../../../../Store/actions/Partner/listOfAffiliatedContacts'
import { getNestedObjects, getNestedArrayOfObjects } from '../../../../Components/Utils/utils'
import useAsyncHook from '../../../../CustomHooks/useAsyncHook2'
import AffiliationEncoderService from '../../../../../../../Services/Api/Partner/affiliationEncoderService'
import SetData from './utilities/SetData'

const PartnerDentalClinicInformation = props => {
  const { id } = useParams()
  const {
    setParamId,
    isToUpdate,
    setIsToUpdate,
    setDentalClinicName,
    setCityName,
    setCurrentStatus,
    setChangeName,
    isEdit,
    setIsEdit,
    setOpen
  } = React.useContext(DentalClinicProfileParamsContext)

  const dispatch = useDispatch()
  const [profile, setProfile] = React.useState({})
  const [additionalInfo, setAdditionalProfilInfo] = React.useState({})
  const [discountTypeData, setDiscountType] = React.useState([])
  const [form] = React.useState({
    facilityAssignment: '',
    id: '',
    formerName: '',
    name: '',
    assignments: [],
    reason: '',
    status: '',
    exclusiveTo: '',
    tvs: '',
    officeLocation: '',
    street: '',
    region: '',
    district: '',
    city: '',
    barangay: '',
    zipCode: '',
    years: '',
    months: '',
    classification: '',
    clinicSchedule: '',
    philhealthNumber: '',
    philhealthExpiration: '',
    payee: '',
    processType: '',
    creditLimit: '',
    paymentTerms: '',
    tinStatus: '',
    vatTaxRate: '',
    facilityDiscount: '',
    dateOfAffiliation: '',
    dateOfDisaffiliation: '',
    specialInstructions: ''
  })

  const [dataGet, errorGet, loadingGet, executeAsyncGet] = useAsyncHook((dataID, token) => {
    const args = dataID.id
    return AffiliationEncoderService.getSingleDentalClinic(args, token)
  })

  const [dataGetDiscount, errorGet2, loadingGet2, executeAsyncGetDiscount] = useAsyncHook((dataID, token) => {
    const args = dataID.id
    return ProfileService.getFacilityDiscount2(args, token)
  })

  React.useEffect(() => {
    let mounted = true

    if (mounted) {
      fetchAsync()
      setParamId(id)
      setIsToUpdate(false)
    }

    return () => { mounted = false }
  }, [id])

  const fetchAsync = async () => {
    await executeAsyncGet({id})
  }

  React.useEffect(() => {
    if (loadingGet) {
      fetchData()
    }
  }, [dataGet])

  const fetchData = async () => {
    await executeAsyncGetDiscount({id})
    const newData = getNestedObjects(dataGet, ['data'])

    const { profile, additionalProfileInfo } = await GetDentalClinicProfile(newData)

    if (profile) {
      setDentalClinicName(profile.partnerName)
      const callFlow = getNestedObjects(profile, ['callFlow'])
      const contacts = getNestedArrayOfObjects(profile, ['contacts'])
      const contactObj = { callFlow, contacts }
      dispatch(setListOfAffiliatedContacts(contactObj))
      setCityName(getNestedObjects(profile, ['city']))
      const dateNow = Date.parse(Date())
      const dissaffiliationDate = getNestedObjects(newData, ['disaffiliation', 'date'])
      const dateOfDissaAffiliation = Date.parse(dissaffiliationDate)

      if (dateNow >= dateOfDissaAffiliation && dissaffiliationDate.length > 0 && dissaffiliationDate !== '1970-01-01T00:00:07.000Z') {
        const newDataValue = { ...newData }
        const result = SetData(form, newDataValue)
        ProfileService.updateDentalClinicProfile(id, result)
        newDataValue['status'] = 'Inactive'
        setCurrentStatus('Inactive')
        setProfile(newDataValue)
      } else {
        setCurrentStatus(getNestedObjects(profile, ['status']))
        setProfile(newData)
      }

      setProfile(profile)

      setAdditionalProfilInfo(additionalProfileInfo)
    }
  }

  React.useEffect(() => {
    if (loadingGet2) {
      fetchDiscount()
    }
  }, [dataGetDiscount])

  const fetchDiscount = () => {
    const discountData = [ ...getNestedObjects(dataGetDiscount, ['data']) ]
    setDiscountType(discountData.map(items => getNestedArrayOfObjects(items, ['discount_type'])))
  }

  const discountType = React.useMemo(() => discountTypeData, [discountTypeData])

  if (profile) {
    return (
      <React.Fragment>
        <div id='editButton' className={classes.editButton}>
          <div id='buttonContainer'>
            <Button variant='contained' onClick={() => setIsToUpdate(true)} className={classes.buttonContainer}>
              <div id='insideText' className={classes.insideText}>
                <label>Edit Partner</label>
                <label>Information</label>
              </div>
            </Button>
          </div>
        </div>
        <ClinicProfileContext.Provider
          value={{
            profile,
            setIsToUpdate,
            setProfile,
            setOpen,
            setChangeName,
            isEdit,
            setIsEdit,
            setDentalClinicName,
            additionalInfo,
            discountType,
            fetchAsync
          }}
        >
          {!isToUpdate && <DentalClinicInformation />}
          {isToUpdate && <UpdateDentalClinicInformation />}
        </ClinicProfileContext.Provider>
      </React.Fragment>
    )
  }
}

export default PartnerDentalClinicInformation
