import React from 'react'

// Components
import HeaderWithBreadCrumbs from '../../../../Components/HeaderWithBreadCrumbs/HeaderWithBreadCrumbs'
import UploadFileTable from '../Components/UploadFIleTable/UploadFIleTable'
import FacilityInformation from '../../../Components/FacilityInformation/FacilityInformation'
import FileViewer from '../../../../Components/FileViewer/FileViewer'
// shared components
import { BreadcrumbsService } from 'SharedComponents/BreadcrumbsComponent/BreadcrumbsComponent'

import { useSelector, useDispatch } from 'react-redux'

// Material UI

// styles
import classes from './ApplicationRequirements.module.scss'
import { setAffiliationType, setDentistAffiliation } from '../../../../../Store/actions/affiliationType'

const breadcrumbs = {
  Medical: {
    text: 'MEDICAL DATA SUPPORT',
    to: '/'
  },
  MedicalPartner: {
    text: 'MEDICAL PARTNER  AFFILIATION',
    to: '/'
  },
  Doctor: {
    text: 'DENTAL CLINIC AFFILIATION',
    to: '/dental-clinic-affiliation-form'
  }
}

const ApplicationRequirements = ({ history }) => {
  const dispatch = useDispatch()

  const { dentalClinicForm, dentistProfile } = useSelector(state => state.dentalClinicForm)
  const { affiliationType, dentistAffiliationType } = useSelector(state => state.affiliationType)
  const { stageName } = affiliationType
  const [isSubmitted, setIsSubmitted] = React.useState(false)
  const [isOpen, setIsOpen] = React.useState(false)
  const [disable, setDisable] = React.useState(false)
  const [isApproved, setIsApproved] = React.useState(false)
  const [isClickedSave, setIsClickSave] = React.useState(false)
  const [saveButtonDisabled, setSaveButtonDisabled] = React.useState(false)
  const [formStepperCount, setFormStepperCount] = React.useState(2)
  const [onClickPreview, setOnClickPreview] = React.useState(false)
  const [previewData, setPreviewData] = React.useState({})

  React.useEffect(() => {
    if (stageName === 'AFFILIATION_APPROVAL_FOR_OIC') {
      setIsSubmitted(true)
      setDisable(true)
      setSaveButtonDisabled(true)
      setFormStepperCount(3)
    }
  }, [stageName])

  const [isSuccess, setIsSuccess] = React.useState(false)
  let stepDetails = ['Fill-Out Form', 'Upload Documents', 'Submit']
  let headerTitle = 'Dental Clinic Affiliation Form'
  BreadcrumbsService.setItems(breadcrumbs)
  BreadcrumbsService.setSelectedItemsWithoutHome(['Medical', 'MedicalPartner', 'Doctor'])

  const handleOnPreview = (event, file) => {
    event.preventDefault()
    setPreviewData({
      type: file.mimetype,
      file: file.preview
    })
    setOnClickPreview(true)
  }

  const handleProceed = async () => {
    try {
      const dentalAffil = { ...affiliationType }
      const dentistAffil = { ...dentistAffiliationType }
      dentalAffil.dateSubmitted = new Date().toISOString()
      dentalAffil.affiliationStatus = 'Submitted'
      dentalAffil.stageName = 'AFFILIATION_APPROVAL_FOR_OIC'
      if (dentistAffil.affiliationStatus !== 'Approved') {
        dentistAffil.affiliationStatus = 'Submitted'
      }

      await (await apiUtils()).updateDentistForm(dentistProfile.id, dentistAffil, dentistProfile)
      await (await apiUtils()).updateDentalForm(affiliationType.id, dentistProfile.id, dentalAffil, dentalClinicForm)
      setFormStepperCount(3)
      dispatch(setAffiliationType(dentalAffil))
      dispatch(setDentistAffiliation(dentistAffil))
    } catch (error) {
      console.log('secode page error', error)
    }
  }

  const save = async () => {
    setIsClickSave(true)
    if (!disable) {
      try {
        const dentalAffil = { ...affiliationType }
        const dentistAffil = { ...dentistAffiliationType }
        await (await apiUtils()).updateDentistForm(dentistProfile.id, dentistAffil, dentistProfile)
        await (await apiUtils()).updateDentalForm(affiliationType.id, dentistProfile.id, dentalAffil, dentalClinicForm)
        setIsSuccess(true)
      } catch (error) {
        console.log('error occured on updating', error)
      }
    }
  }

  const utils = async () => {
    return await import('../DentalClinicInformation/dentalClinicService')
  }

  const apiUtils = async () => {
    return await import('../DentalClinicInformation/dentalClinicService')
  }

  return (
    <div id='root' className={classes.root}>
      {onClickPreview && (
        <FileViewer open={onClickPreview} setOpen={setOnClickPreview} type={previewData.type} file={previewData.file} />
      )}
      <HeaderWithBreadCrumbs
        id='HeaderWithBreadCrumbs'
        HeaderTitle={headerTitle}
        stepDetails={stepDetails}
        currentStep={formStepperCount}
      />
      <div id='contentWrapper' className={classes.contentWrapper}>
        <FacilityInformation
          history={history}
          returnPage={'/dental-clinic-affiliation-form'}
          dialogText={'Dental Clinic Affiliation'}
          titleText={'Dental Clinic Profile'}
          uploadFileTable={
            <UploadFileTable
              buttonText={'Upload'}
              setDisable={setDisable}
              disable={isApproved}
              setIsApproved={setIsApproved}
              isClickedSave={isClickedSave}
              onPreviewFile={handleOnPreview}
            />
          }
          handleProceed={handleProceed}
          isSuccess={isSuccess}
          setIsSuccess={setIsSuccess}
          isSubmitted={isSubmitted}
          save={save}
          isOpen={isOpen}
          disable={disable}
          setIsApproved={setIsApproved}
          setDisable={setDisable}
          saveButtonDisabled={saveButtonDisabled}
          setFormStepperCount={setFormStepperCount}
        />
      </div>
    </div>
  )
}

export default ApplicationRequirements
