import React, { useState } from 'react'

import Table from '@material-ui/core/Table'
import TableBody from '@material-ui/core/TableBody'
import TableCell from '@material-ui/core/TableCell'
import TableContainer from '@material-ui/core/TableContainer'
import TableRow from '@material-ui/core/TableRow'
import Preview from '../../../PreviewImage/PreviewImage'

import classes from './UploadFIleTable.module.scss'
import UploadFileContext from '../../../../Components/context/fileUploadContext'
import { useSelector } from 'react-redux'
import validateForm from './validation'
import validateFormWithDental from './validation-with-dental'
import AffiliationEncoderService from 'Services/Api/Partner/affiliationEncoderService'

const DescriptionWithSubscript = ({ mainText, subscript, errors = {}, fileType = '' }) => {
  return (
    <div id='DescriptionWithSubscript' className={classes.WithSubScript}>
      <text>{mainText} </text>
      <sub id='subscript-PhotoOfPhicID' className={classes.subscript}>
        {subscript}
      </sub>
    </div>
  )
}

const createData = (description, uploadButton) => {
  return { description, uploadButton }
}

const URL = `${process.env.REACT_APP_SERVER_API}`

const UploadFIleTable = props => {
  const { forView, disable, setDisable, setIsApproved, isClickedSave, onPreviewFile } = props
  const [mainFiles, setMainFiles] = useState([])
  const { affiliationType } = useSelector(state => state.affiliationType)
  const [errors, setErrors] = React.useState({})
  const [dentalForm, setDentalForm] = React.useState({})
  const reduxState = useSelector(state => state.dentalClinicForm)
  const [form, setForm] = useState({
    'dental-clinic-profile': 0,
    'list-service-offered-with-rates': 0,
    'location-map': 0,
    'business-permit': 0,
    'bir-cor-2303': 0,
    'sec-dti-registration': 0,
    'dentist-updated-resume': 0,
    'dentist-recent-picture': 0,
    'dentist-copy-of-diploma': 0,
    'dentist-prc-license-id': 0,
    'dentist-bir-cor-2303': 0,
    'declaration-gross-sales': 0,
    'dental-associate-updated-resume': 0,
    'dental-associate-copy-of-diploma': 0,
    'dental-associate-prc-license-id': 0
    // 'dentist-requirements': 0
  })

  const validate = async () => {
    const { errors, isValid } =
      dentalForm.dentalAssociates && dentalForm.dentalAssociates.length > 0
        ? await validateFormWithDental(form)
        : await validateForm(form)
    if (isValid) {
      setErrors({})
      if (setDisable) {
        setDisable(false)
      }

      return true
    } else {
      if (setDisable) {
        setDisable(true)
      }
      setErrors(errors)
      return false
    }
  }

  // React.useEffect(() => {
  //   validate()
  // }, [form, mainFiles])

  React.useEffect(() => {
    if (affiliationType['affiliationStatus'] === 'Submitted') {
      setDisable(true)
      setIsApproved(true)
    } else {
      validate()
    }
  }, [form, mainFiles, affiliationType.affiliationStatus])

  React.useEffect(() => {
    if (props.affiliationTypeId) {
      fetchData(props.affiliationTypeId)
    }
    if (affiliationType.id) {
      fetchData()
    }
  }, [])

  React.useEffect(() => {
    setDentalForm(reduxState.dentalClinicForm)
  }, [reduxState.dentalClinicForm])

  React.useEffect(() => {
    validate()
  }, [dentalForm])

  const fetchData = async affiliationTypeId => {
    try {
      let id = affiliationType.id
      if (affiliationTypeId) {
        id = affiliationTypeId
      }
      const filesData = await AffiliationEncoderService.getUploadFile(id)
      // const filesData = await Axios.get(urlRequest)
      const updateForm = { ...form }
      for (let fileData of filesData.data.data) {
        let tmpFiles = fileData.files
        setMainFiles(previousState => ({
          ...previousState,
          [fileData.file_type]: tmpFiles.map(file =>
            Object.assign(file, {
              preview: `${URL}/uploads/${file.path}`,
              fileType: fileData.file_type
            })
          )
        }))
        if (form && form.hasOwnProperty(fileData.file_type) && tmpFiles.length > 0) {
          updateForm[fileData.file_type] = tmpFiles.length
        }
      }
      setForm(updateForm)
    } catch (e) {
      console.log('error', e)
    }
  }

  const rows = [
    createData('Dental Clinic Requirements'),
    createData(
      <Preview
        onPreviewFile={onPreviewFile}
        desc={'Dental Clinic Profile *'}
        fileType={'dental-clinic-profile'}
        mainFiles={mainFiles}
        setMainFiles={setMainFiles}
        buttonText={'Upload'}
        forView={forView}
        form={form}
        setForm={setForm}
        errors={errors}
      />
    ),
    createData(
      <Preview
        onPreviewFile={onPreviewFile}
        desc={'Updated List of Services offered with Rates *'}
        fileType={'list-service-offered-with-rates'}
        mainFiles={mainFiles}
        setMainFiles={setMainFiles}
        buttonText={'Upload'}
        forView={forView}
        form={form}
        setForm={setForm}
        errors={errors}
      />
    ),
    createData(
      <Preview
        onPreviewFile={onPreviewFile}
        desc={
          <DescriptionWithSubscript mainText={'Location Map *'} subscript={'*Preferably screenshot from google maps'} />
        }
        fileType={'location-map'}
        mainFiles={mainFiles}
        setMainFiles={setMainFiles}
        buttonText={'Upload'}
        forView={forView}
        form={form}
        setForm={setForm}
        errors={errors}
      />
    ),
    createData(
      <Preview
        onPreviewFile={onPreviewFile}
        desc={
          <DescriptionWithSubscript
            mainText={'Actual Image of Areas in the Facility'}
            subscript={'*Dental Equipment,Dental Chairs,Front Area/Entrance of the Dental Clinic.'}
          />
        }
        fileType={'actual-image-of-areas-facility'}
        mainFiles={mainFiles}
        setMainFiles={setMainFiles}
        buttonText={'Upload'}
        forView={forView}
        form={form}
        setForm={setForm}
        errors={errors}
      />
    ),
    createData(
      <Preview
        onPreviewFile={onPreviewFile}
        desc={'Business Permit *'}
        fileType={'business-permit'}
        mainFiles={mainFiles}
        setMainFiles={setMainFiles}
        buttonText={'Upload'}
        forView={forView}
        form={form}
        setForm={setForm}
        errors={errors}
      />
    ),
    createData(
      <Preview
        onPreviewFile={onPreviewFile}
        desc={'BIR COR 2303 *'}
        fileType={'bir-cor-2303'}
        mainFiles={mainFiles}
        setMainFiles={setMainFiles}
        buttonText={'Upload'}
        forView={forView}
        form={form}
        setForm={setForm}
        errors={errors}
      />
    ),
    createData(
      <Preview
        onPreviewFile={onPreviewFile}
        desc={'SEC/DTI Registration *'}
        fileType={'sec-dti-registration'}
        mainFiles={mainFiles}
        setMainFiles={setMainFiles}
        buttonText={'Upload'}
        forView={forView}
        form={form}
        setForm={setForm}
        errors={errors}
      />
    ),
    createData('Dentist Requirements'),
    createData(
      <Preview
        onPreviewFile={onPreviewFile}
        desc={'Updated Resume *'}
        fileType={'dentist-updated-resume'}
        mainFiles={mainFiles}
        setMainFiles={setMainFiles}
        buttonText={'Upload'}
        forView={forView}
        form={form}
        setForm={setForm}
        errors={errors}
      />
    ),
    createData(
      <Preview
        onPreviewFile={onPreviewFile}
        desc={'Recent 2x2 Picture *'}
        fileType={'dentist-recent-picture'}
        mainFiles={mainFiles}
        setMainFiles={setMainFiles}
        buttonText={'Upload'}
        forView={forView}
        form={form}
        setForm={setForm}
        errors={errors}
      />
    ),
    createData(
      <Preview
        onPreviewFile={onPreviewFile}
        desc={'Copy of Diploma *'}
        fileType={'dentist-copy-of-diploma'}
        mainFiles={mainFiles}
        setMainFiles={setMainFiles}
        buttonText={'Upload'}
        forView={forView}
        form={form}
        setForm={setForm}
        errors={errors}
      />
    ),
    createData(
      <Preview
        onPreviewFile={onPreviewFile}
        desc={'BIR COR 2303 *'}
        fileType={'dentist-bir-cor-2303'}
        mainFiles={mainFiles}
        setMainFiles={setMainFiles}
        buttonText={'Upload'}
        forView={forView}
        form={form}
        setForm={setForm}
        errors={errors}
      />
    ),
    createData(
      <Preview
        onPreviewFile={onPreviewFile}
        desc={'Photocopy of PRC License ID *'}
        fileType={'dentist-prc-license-id'}
        mainFiles={mainFiles}
        setMainFiles={setMainFiles}
        buttonText={'Upload'}
        forView={forView}
        form={form}
        setForm={setForm}
        errors={errors}
      />
    ),
    createData(
      <Preview
        onPreviewFile={onPreviewFile}
        desc={'Sworn Declaration of Gross Receipts/Sales *'}
        fileType={'declaration-gross-sales'}
        mainFiles={mainFiles}
        setMainFiles={setMainFiles}
        buttonText={'Upload'}
        forView={forView}
        form={form}
        setForm={setForm}
        errors={errors}
      />
    ),
    createData('Dental Associate Requirements'),
    createData(
      <Preview
        onPreviewFile={onPreviewFile}
        desc={'Updated Resume *'}
        fileType={'dental-associate-updated-resume'}
        mainFiles={mainFiles}
        setMainFiles={setMainFiles}
        buttonText={'Upload'}
        forView={forView}
        form={form}
        setForm={setForm}
        errors={errors}
      />
    ),
    createData(
      <Preview
        onPreviewFile={onPreviewFile}
        desc={'Copy of Diploma *'}
        fileType={'dental-associate-copy-of-diploma'}
        mainFiles={mainFiles}
        setMainFiles={setMainFiles}
        buttonText={'Upload'}
        forView={forView}
        form={form}
        setForm={setForm}
        errors={errors}
      />
    ),
    createData(
      <Preview
        onPreviewFile={onPreviewFile}
        desc={'Photocopy of PRC License ID *'}
        fileType={'dental-associate-prc-license-id'}
        mainFiles={mainFiles}
        setMainFiles={setMainFiles}
        buttonText={'Upload'}
        forView={forView}
        form={form}
        setForm={setForm}
        errors={errors}
      />
    )
  ]

  return (
    <TableContainer id='tableContainer' className={classes.tableContainer}>
      <UploadFileContext.Provider value={{ disabled: disable, isClickedSave }}>
        <Table className={classes.table} size='small' aria-label='a dense table'>
          <TableBody id='table-body'>
            {rows.map((row, index) => (
              <TableRow id='TableRow' className={classes['table-row']} key={index}>
                <TableCell id='description-row' className={classes['description-row']} component='th' scope='row'>
                  {row.description}
                </TableCell>
              </TableRow>
            ))}
          </TableBody>
        </Table>
      </UploadFileContext.Provider>
    </TableContainer>
  )
}

export default UploadFIleTable
