import React from 'react'
import FacilityGroupingService from 'Services/Api/RoomType/RoomType'
import TableList from '../../../../Components/TableList'
import RoomAndBoardService from 'Services/Api/Partner/RoomAndBoardService'
import RoomType from './Components/RoomType/RoomType'
import moment from 'moment'

const RoomAndBoard = props => {
  const { paramId, type, open, setOpen } = props
  const [roomType, setRoomType] = React.useState([{ text: '' }])
  const [roomTypeCode, setRoomTypeCode] = React.useState()
  const [isLoadedRoomType, setIsLoadedRoomType] = React.useState([])
  const [openRoomType, setOpenRoomType] = React.useState(false)
  const triggerFunc = React.useRef()

  React.useEffect(() => {
    fetchRoomTypes(() => {
      setIsLoadedRoomType(true)
    })
  }, [])

  const fetchRoomTypes = async cb => {
    const { data } = await FacilityGroupingService.get()
    if (data.data.length > 0) {
      const roomsList = data.data.map(room => ({ text: room.desc }))
      setRoomType(roomsList)
      setRoomTypeCode(data.data)
    }
    cb()
  }

  return (
    <React.Fragment>
      {isLoadedRoomType && roomTypeCode && (
        <React.Fragment>
          <RoomType
            id={paramId}
            roomTypes={roomTypeCode}
            open={openRoomType}
            setOpen={setOpenRoomType}
            triggerFunction={triggerFunc.current}
          />
          <TableList
            saveAsyncFunc={(data, token) => {
              const args = { ...data, id: paramId }
              console.log('args', args)
              return RoomAndBoardService.saveRoomAndBoard(args, token)
            }}
            getAsyncFunc={(data, token) => {
              const args = { ...data, id: paramId }
              return RoomAndBoardService.getRoomAndBoard(args, token)
            }}
            readOnlyHeadersButtonsProps={[
              {
                text: 'Add Room Type',
                style: { backgroundColor: '#1ab1c4' },
                onClick: ({ getTriggerFunction }) => {
                  triggerFunc.current = getTriggerFunction
                  setOpenRoomType(true)
                }
              },
              {
                text: 'Update',
                style: { backgroundColor: '#3ab77d' },
                onClick: ({ setReadOnly }) => setReadOnly(false)
              }
            ]}
            readOnlyFooterButtonsProps={[
              {
                text: 'Close',
                style: { backgroundColor: '#fd5451' },
                onClick: () => setOpen(false)
              }
            ]}
            onUpdateFooterButtonsProps={[
              {
                text: 'Cancel',
                style: { backgroundColor: '#fd5451' },
                onClick: ({ setIsCancel }) => setIsCancel(true)
              },
              {
                text: 'Save',
                style: { backgroundColor: '#3ab77d' },
                onClick: ({ setIsSubmit }) => setIsSubmit(true)
              }
            ]}
            readOnlyHeaderTableProps={[
              {
                text: 'Room Code',
                type: 'text',
                field: 'roomCode',
                customStyle: { width: '150px' }
              },
              {
                text: 'Room Type',
                type: 'text',
                field: 'roomType',
                customStyle: { width: '150px' }
              },
              {
                text: 'Room Description',
                type: 'text',
                field: 'description',
                customStyle: { width: '250px' }
              },
              {
                text: 'Price',
                enableSort: false,
                field: 'price',
                customStyle: { width: '120px' }
              },
              {
                text: 'Status',
                enableSort: false,
                field: 'status',
                customStyle: { width: '120px' }
              },
              {
                text: 'Remarks',
                enableSort: false,
                field: 'remarks',
                customStyle: { width: '200px' }
              }
            ]}
            onUpdateHeaderTableProps={[
              {
                text: 'Room Code',
                type: 'text',
                field: 'roomCode',
                customStyle: { width: '150px' }
              },
              {
                text: 'Room Type',
                type: 'text',
                field: 'roomType',
                customStyle: { width: '150px' }
              },
              {
                text: 'Room Description',
                type: 'text',
                field: 'description',
                customStyle: { width: '250px' }
              },
              {
                text: 'Price',
                enableSort: false,
                field: 'price',
                customStyle: { width: '120px' }
              },
              {
                text: 'Status',
                enableSort: false,
                field: 'status',
                customStyle: { width: '120px' }
              },
              {
                text: 'Effectivity Date',
                enableSort: false,
                field: 'effectivityDate',
                customStyle: { minWidth: '170px' }
              },
              {
                text: 'Remarks',
                enableSort: false,
                field: 'remarks',
                customStyle: { width: '200px' }
              }
            ]}
            dataStructure={{
              roomCode: { editable: false },
              roomType: {
                type: 'suggest',
                suggestions: roomType,
                entries: roomTypeCode,
                key: 'desc',
                target: 'code',
                relation: 'roomCode'
              },
              description: { type: 'text' },
              price: { type: 'price' },
              status: {
                type: 'select',
                defaultMenu: 'Active',
                menu: [
                  { text: 'Active', value: 'Active' },
                  { text: 'Inactive', value: 'Inactive' }
                ]
              },
              effectivityDate: { type: 'date' },
              remarks: { type: 'text' }
            }}
            filter={{
              roomCode: '',
              roomType: '',
              description: ''
            }}
            sort={{
              roomCode: '',
              roomType: '',
              description: ''
            }}
            validation={{
              rowDuplicate: { enable: false, checkFields: ['roomCode', 'roomType'] },
              requiredField: {
                enable: true,
                checkFields: ['roomCode', 'roomType', 'description', 'price', 'effectivityDate']
              }
            }}
            errorList={{
              Duplicate: 'Duplicate Data',
              'Invalid Date': 'Invalid Date',
              'Error Conflict': 'Error Conflict'
            }}
            openModal={open}
            roomAndBoards
            setOpenModal={setOpen}
            paginationText='Room Types'
          />
        </React.Fragment>
      )}
    </React.Fragment>
  )
}

export default RoomAndBoard
