import React, { useState, useEffect, useMemo } from 'react'
import AddressDrowpdown from 'Pages/Protected/Partner/Components/AddressDrowpdown'
import TextField from 'Pages/Protected/Partner/AffiliationFormEncoder/Components/EncoderTextField'
import { useSelector } from 'react-redux'

import validation from './validation'
import classes from './sectionThree.module.scss'


const HospitalSectionThreeForm = props => {
  const { regions, cities: newCities, barangays: newBarangays, provinces: newProvinces } =  useSelector(state => state.addressReducer)
  const { isSubmitted = false, isValidate, setData, data, setIsValid, defaultVal } = props
  const [errors, setErrors] = useState({})
  const [form, setForm] = useState({})

  const [isFetching, setIsFetching] = React.useState({})
  const [provinces, setProvinces] = React.useState([...newProvinces])
  const [cities, setCities] = React.useState([...newCities])
  const [barangays, setBarangays] = React.useState([...newBarangays])


  useEffect(() => {
    if (isValidate) {
      validate()
    }
  }, [isValidate])

  useEffect(() => {
    const newData = { ...data }
    setData(Object.assign(newData, form))
  }, [form])

  useEffect(() => {
    let nForm = { ...form }
    // setForm(Object.assign(nForm, defaultVal))
    setForm({...nForm, ...defaultVal})
  }, [defaultVal])


  const isValidForm = useMemo(() => {
    const { isValid } = validation(form)
    if (isValid) {
      return true
    } else {
      return false
    }
  }, [form])

  const validate = () => {
    const { errors, isValid } = validation(form)
    console.log('errors', errors)
    const nErrors = { ...errors }
    if (isValid) {
      return true
    } else {
      setErrors(Object.assign( nErrors, errors))
      return false
    }
  }

  useEffect(() => {
    setIsValid(isValidForm)
  }, [isValidForm])

  const handleOnChange = ({target: { value, name }}) => {
    const e = {...errors}
    let nform = {...form}
    nform[name] = value
    delete e[name]
    setErrors(e)
    setForm(nform)
  }

  const utils = async () => {
    return await import('Pages/Protected/Partner/Components/Utils/utils')
  }

  const handleAddressChainingDropdown = ({ target: {name, value} }) => {
    const dentalClinic = { ...form }
    dentalClinic[name] = value
    delete errors[name]
    setErrors(errors)
    setForm(dentalClinic)
    callHandleAddressChaining(name, value)
  }

  const callHandleAddressChaining = async (name, value) => {
    (await utils()).handleAddressChaining(name, value, form, setForm, setBarangays, setCities, setProvinces, setIsFetching, isFetching)
  }

  return (
    <div className={classes.SecondPartInputFields}>
      <div id="firstInputLayerFields" className={classes.firstInputLayerFields}>
        <div>
          <label>
            Hospital Address <br />
            Unit No./Floor/Bldg.No.
          </label>
          <TextField
            name="unitFlrBldg"
            type={'text'}
            onChange={handleOnChange}
            disabled={isSubmitted}
            defaultValue={defaultVal['unitFlrBldg']}
          />
        </div>
        <div>
          <label>
            <div /> <br /> Building Name
          </label>
          <TextField
            name="bldgName"
            type={'text'}
            onChange={handleOnChange}
            disabled={isSubmitted}
            defaultValue={defaultVal['bldgName']}
          />
        </div>
        <div>
          <label>
            <div /> <br /> Street Name *
          </label>
          <TextField
            name="street"
            error={errors['street']}
            type={'text'}
            onChange={handleOnChange}
            disabled={isSubmitted}
            defaultValue={defaultVal['street']}
          />
        </div>
      </div>
      <div id="SedcondInputLayerFields" className={classes.SecondInputLayerFields}>
        <div>
          <label>Subdivision</label>
          <TextField
            name="subdivision"
            type="text"
            onChange={handleOnChange}
            disabled={isSubmitted}
            defaultValue={defaultVal['subdivision']}
          />
        </div>
        <div>
          <label>Region *</label>
          <AddressDrowpdown
            disabled={isSubmitted}
            id="region"
            items={regions}
            value={form['region'] || ''}
            error={errors['region']}
            name="region"
            onChange={handleAddressChainingDropdown}
          />
        </div>
        <div>
          <label>District / Province *</label>
          {isFetching['province'] && <span className={classes['fetching-indicator']}> fetching...</span>}
          <AddressDrowpdown
            disableComponents={
              isSubmitted || (provinces instanceof Array && provinces.length <= 0) || isFetching['province']
            }
            id="district"
            items={provinces}
            value={form['district']}
            error={errors['district']}
            name="district"
            onChange={handleAddressChainingDropdown}
          />
        </div>
      </div>
      <div id="ThirdInputLayerFields" className={classes.ThirdInputLayerFields}>
        <div>
          <label>Barangay *</label>
          {isFetching['barangay'] && <span className={classes['fetching-indicator']}> fetching... </span>}
          <AddressDrowpdown
            disableComponents={
              isSubmitted || (barangays instanceof Array && barangays.length <= 0) || isFetching['barangay']
            }
            id="barangay"
            name="barangay"
            value={form['barangay']}
            error={errors['barangay']}
            items={barangays}
            onChange={handleAddressChainingDropdown}
          />
        </div>
        <div>
          <label>City / Municipality *</label>
          {isFetching['city'] && <span className={classes['fetching-indicator']}> fetching... </span>}
          <AddressDrowpdown
            disableComponents={isSubmitted | (cities instanceof Array && cities.length <= 0) || isFetching['city']}
            id="city"
            items={cities}
            value={form['city']}
            name="city"
            error={errors['city']}
            onChange={handleAddressChainingDropdown}
          />
        </div>
        <div>
          <label>Zip Code *</label>
          <TextField
            name="zipCode"
            error={errors['zipCode']}
            type={'noSpecialChar'}
            specialValue={form['zipCode']}
            onChange={handleOnChange}
            disabled={isSubmitted}
            inputProps={{ maxLength: 4 }}
            defaultValue={defaultVal['zipCode']}
          />
        </div>
      </div>
    </div>
  )
}

export default HospitalSectionThreeForm
