import { DateErrorHandler, YearDateFormat, getNestedObjects, OfficeLocationDefault, getNestedArrayOfObjects, CheckIfstring } from '../../../../../../Components/Utils/utils'

const SetData = (newForms, setForm, profile) => {
  newForms['firstName'] = getNestedObjects(profile, ['name', 'first'])
  newForms['lastName'] = getNestedObjects(profile, ['name', 'last'])
  newForms['middleName'] = getNestedObjects(profile, ['name', 'middle'])
  newForms['suffix'] = getNestedObjects(profile, ['name', 'suffix'])
  newForms['id'] = getNestedObjects(profile, ['id'])
  newForms['officeLocation'] = OfficeLocationDefault(getNestedObjects(profile, ['office_location']))
  newForms['birthdate'] = getNestedObjects(profile, ['birthdate'])
  newForms['civilStatus'] = getNestedObjects(profile, ['civil_status'])
  newForms['assignments'] = getNestedArrayOfObjects(profile, ['facility_assignment', 'assignments'])
  newForms['reason'] = getNestedObjects(profile, ['reason'])
  newForms['exclusiveTo'] = getNestedObjects(profile, ['facility_assignment', 'exclusive_to'])
  newForms['gender'] = getNestedObjects(profile, ['gender'])
  newForms['mainSpecialization'] = getNestedObjects(profile, ['specialization', 'main'])
  newForms['majorSpecialization'] = getNestedObjects(profile, ['specialization', 'major'])
  newForms['status'] = getNestedObjects(profile, ['status'])
  newForms['subSpecialization'] = getNestedObjects(profile, ['specialization', 'sub'])
  newForms['otherSpecialization'] = getNestedObjects(profile, ['specialization', 'other'])
  newForms['prcNumber'] = getNestedObjects(profile, ['license', 'prc', 'number'])
  newForms['prcExpiration'] = profile
    ? profile.license
      ? profile.license.prc
        ? profile.license.prc.expiration_date
          ? YearDateFormat(profile.license.prc.expiration_date)
          : ''
        : ''
      : ''
    : ''

  newForms['philhealthNumber'] = getNestedObjects(profile, ['philhealth', 'accreditation_number'])
  newForms['philhealthExpiration'] = profile
    ? profile.philhealth
      ? profile.philhealth.expiration_date
        ? YearDateFormat(profile.philhealth.expiration_date)
        : ''
      : ''
    : ''
  newForms['pdaNumber'] = getNestedObjects(profile, ['license', 'pda'])
  newForms['tiNumber'] = getNestedObjects(profile, ['treasury', 'tin'])
  newForms['tinStatus'] = getNestedObjects(profile, ['treasury', 'tin_status'])
  newForms['non_vatable'] = getNestedObjects(profile, ['treasury', 'non_vatable'])
  newForms['vatTaxRate'] = getNestedObjects(profile, ['treasury', 'vat', 'tax_rate'])
  newForms['processType'] = getNestedObjects(profile, ['process_type'])
  newForms['paymentTerms'] = getNestedObjects(profile, ['treasury', 'payment_terms'])
  newForms['serviceRate'] = CheckIfstring(getNestedObjects(profile, ['professional_fees', 'rate']))
  newForms['withTax'] = getNestedObjects(profile, ['treasury', 'withHoldingTax'])
  newForms['facilityName'] = getNestedObjects(profile, ['facility_info', '0', 'name'])
  newForms['address'] = getNestedObjects(profile, ['facility_info', '0', 'full_address'])
  newForms['dateSubmitted'] = profile
    ? profile.affiliation_status
      ? profile.affiliation_status.date_submitted
        ? YearDateFormat(profile.affiliation_status.date_submitted)
        : ''
      : ''
    : ''
  newForms['clinicSchedule'] = getNestedObjects(profile, ['facility_info', '0', 'clinic_schedule'])
  newForms['dateOfDisaffiliation'] = profile
    ? profile.disaffiliation
      ? profile.disaffiliation.date
        ? YearDateFormat(profile.disaffiliation.date)
        : ''
      : ''
    : ''
  setForm(newForms)

  return newForms
}

const SaveData = newForm => {
  let obj = {}

  obj.lastName = newForm['lastName']
  obj.firstName = newForm['firstName']
  obj.middleName = newForm['middleName']
  obj.suffix = newForm['suffix']
  obj.id = newForm['id']
  obj.officeLocation = newForm['officeLocation']
  obj.status = newForm['status']
  obj.stageName = 'PROFILE_GENERATED'
  obj.dateSubmitted = DateErrorHandler(newForm['dateSubmitted'])
  obj.dateOfDisaffiliation = DateErrorHandler(newForm['dateOfDisaffiliation'])
  obj.assignments = newForm['assignments']
  obj.exclusiveTo = newForm['exclusiveTo']
  obj.reason = newForm['reason']
  obj.birthdate = newForm['birthdate']
  obj.civilStatus = newForm['civilStatus']
  obj.gender = newForm['gender']
  obj.mainSpecialization = newForm['mainSpecialization']
  obj.majorSpecialization = newForm['majorSpecialization']
  obj.subSpecialization = newForm['subSpecialization']
  obj.otherSpecialization = newForm['otherSpecialization']
  obj.pdaNumber = newForm['pdaNumber']
  obj.withTax = newForm['withTax']
  obj.prcNumber = newForm['prcNumber']
  obj.prcExpiration = DateErrorHandler(newForm['prcExpiration'])
  obj.philhealthNumber = newForm['philhealthNumber']
  obj.philhealthExpiration = DateErrorHandler(newForm['philhealthExpiration'])
  obj.tinNumber = newForm['tiNumber']
  obj.processType = newForm['processType']
  obj.tinStatus = newForm['tinStatus']
  obj.non_vatable = newForm['non_vatable']
  obj.vatTaxRate = newForm['vatTaxRate']
  obj.facilityName = newForm['facilityName']
  obj.address = newForm['address']
  obj.clinicSchedule = newForm['clinicSchedule']
  obj.paymentTerms = newForm['paymentTerms']
  obj.serviceRate = newForm['serviceRate']
  obj.type = 'DENTIST'

  return obj
}

export { SetData, SaveData }
