import React from 'react'
import Table from './TableData'
import orderBy from 'lodash/orderBy'

const AffiliationTable = props => {
  const { dataheader, dataValue, handleRemove, isSubmitted, hasError } = props

  const [data, setData] = React.useState(dataValue)
  const [isSubmiteStatus, setIsSubmitted] = React.useState(false)
  React.useEffect(() => {
    setData(data)
  }, [data])

  React.useEffect(() => {
    if (isSubmitted) {
      setIsSubmitted(isSubmitted)
    }
  }, [isSubmitted])

  return (
    <div style={{display: 'flex', flexDirection: 'column', width: 'auto', height: '50%'}}>
      <div style={hasError ? {border: '2px solid red'} : null}>
        <label style={{ fontWeight: '600', color: 'black', marginBottom: '1em' }}>Facilities Included in the Group</label>
      </div>
      <div style={{ display: 'flex', width: '100%', height: '180px', overflowY: 'auto' }}>
        <Table
          handleRemove={handleRemove}
          data={orderBy(dataValue)}
          header={dataheader}
          isSubmitted={isSubmiteStatus}
          hasError={hasError}
        />
      </div>
    </div>
  )
}

export default React.memo(AffiliationTable)
