import React from 'react'

import Button from '@material-ui/core/Button'
import Dialog from '@material-ui/core/Dialog'
import DialogActions from '@material-ui/core/DialogActions'
import DialogContent from '@material-ui/core/DialogContent'
import TextField from '../../../Components/JoiValidationInputField'
import DialogTitle from '@material-ui/core/DialogTitle'

import classes from './index.module.scss'
import AddMultiplierValidation from '../validation'
import FormHelperText from '@material-ui/core/FormHelperText'
import { LoadingService } from 'SharedComponents/LoadingComponent/LoadingComponent'
import DropDown from '../../../Components/CustomDrowpdown'
import { useDispatch, useSelector } from 'react-redux'
import { SET_OPEN_MODAL, SET_NEW_DATA, SET_ADD_SUCCESS } from '../../../../Store/actions/Partner/MaintenanceTable'
import RoomTypeService from '../../../../../../Services/Api/RoomType/RoomType'
import PfMultiplierService from '../../../../../../Services/Api/Partner/PfMultiplierServices'
import CustomAlerSnackBar from '../../../Components/CustomSnackBar/CustomAlertSnackbar'
import useAsyncHook from '../../../CustomHooks/useAsyncHook2'
import { getNestedArrayOfObjects, getNestedObjects } from '../../../Components/Utils/utils'

const AddRoomMultiplierModal = props => {
  const { stateValue = {} } = props

  const dispatch = useDispatch()

  const [errors, setErrors] = React.useState({})
  const [roomTypes, setRoomTypes] = React.useState([])
  const [form, setForm] = React.useState({
    'amhopi_society': '',
    'pf_type': '',
    'room_type': '',
    'rate': '',
    'code': ''
  })

  const [dataAdded, errorGetSave, loadingGetAdded, executeAsyncAdd] = useAsyncHook((data, token) => {
    const argsData = data.data
    return PfMultiplierService.add(argsData)
  })

  const [dataAhmopiCount, errorGetAhmopi, loadingGetAhmopi, executeAsyncAhmopi] = useAsyncHook((data, token) => {
    const argsData = data.data
    return PfMultiplierService.getAhmopiType(argsData, token)
  })

  React.useEffect(() => {
    fetchRoomType()
  }, [])

  const handleSubmit = (e) => {
    e.preventDefault()
    validate()
  }

  const validate = () => {
    const { errors, isValid } = AddMultiplierValidation(form)
    if (isValid) {
      setErrors({})
      save()
    } else {
      setErrors(errors)
    }
  }

  const save = async () => {
    try {
      await executeAsyncAdd({
        data: form
      })
    } catch (error) {
      LoadingService.loading(false)
    }
  }

  React.useEffect(() => {
    const message = getNestedObjects(dataAdded, ['message'])
    if (loadingGetAdded && message === 'Created successfully') {
      const newData = getNestedArrayOfObjects(dataAdded, ['data'])
      dispatch(SET_NEW_DATA(newData))
      dispatch(SET_OPEN_MODAL(false))
      dispatch(SET_ADD_SUCCESS(true))
      setForm({
        'amhopi_society': '',
        'pf_type': '',
        'room_type': '',
        'rate': '',
        'code': ''
      })
    }
  }, [dataAdded])

  const handleChange = (e) => {
    const { name, value } = e.target
    const contactForm = { ...form }
    contactForm[name] = value
    setForm(contactForm)
  }

  const setCounterCode = async (type) => {
    await executeAsyncAhmopi({
      data: type
    })
  }

  React.useEffect(() => {
    if (loadingGetAhmopi) {
      getCount()
    }
  }, [dataAhmopiCount])

  const getCount = () => {
    const updateForm = { ...form }
    let counter = 1
    const typeData = getNestedObjects(dataAhmopiCount, ['data', 'resultValue'])
    const type = getNestedObjects(dataAhmopiCount, ['data', 'ahmopiType'])
    counter = typeData + 1

    let typeText = ''
    switch (type) {
      case 'PCS':
        typeText = 'PFPCS'
        break
      case 'POGS':
        typeText = 'PFPOG'
        break
      case 'PPS':
        typeText = 'PFPPS'
        break
      case 'PSA':
        typeText = 'PFPSA'
        break
      case 'Regular':
        typeText = 'PFREG'
        break
      case 'UDAA':
        typeText = 'PFUDA'
        break
      case 'UHDA':
        typeText = 'PFUHD'
        break
      case 'MACU':
        typeText = 'PFMAC'
        break
    }
    const pad = '0000'
    const counterText = (pad + counter).slice(-pad.length)
    updateForm['code'] = `${typeText}${counterText}`
    setForm(updateForm)
  }

  const amhopiSociety = [
    { id: 1, text: 'PCS', value: 'PCS' },
    { id: 2, text: 'POGS', value: 'POGS' },
    { id: 3, text: 'PPS', value: 'PPS' },
    { id: 4, text: 'PSA', value: 'PSA' },
    { id: 5, text: 'Regular', value: 'Regular' },
    { id: 6, text: 'UDAA', value: 'UDAA' },
    { id: 7, text: 'UHDA', value: 'UHDA' },
    { id: 8, text: 'MACU', value: 'MACU' }
  ]

  const pfTypes = [
    { id: 1, text: 'Surgical', value: 'Surgical' },
    { id: 2, text: 'Out-Patient', value: 'Out-Patient' },
    { id: 2, text: 'Daily Visit', value: 'Daily Visit' }
  ]

  const fetchRoomType = async () => {
    const { data } = await RoomTypeService.get()
    if (data.data) {
      let roomTypesData = []
      for (let tmp of data.data) {
        roomTypesData.push({
          id: tmp.code,
          text: tmp.desc,
          value: tmp.desc
        })
      }

      setRoomTypes(roomTypesData)
    }
  }

  const handleDropdown = ({ target: select }) => {
    const updateForm = { ...form }
    updateForm[select.name] = select.value
    console.log('updateForm', updateForm)
    if (select.name == 'amhopi_society') {
      setCounterCode(select.value)
    }
    setForm(updateForm)
  }

  return (
    <React.Fragment >
      <Dialog fullWidth maxWidth={'xs'} open={stateValue.openModal} aria-labelledby='dialogTitle'>
        <DialogTitle id='dialogTitle' style={{ color: '#9ea3ac', marginTop: '2%' }}>ADD MULTIPLIER</DialogTitle>
        <DialogContent>
          <form className={classes.root} onSubmit={handleSubmit} noValidate autoComplete='off'>
            <div id='firstInput' className={classes.firstInput}>
              <div id='amhopi'>
                <DropDown name='amhopi_society' error={errors['amhopi_society']} onChange={handleDropdown} items={amhopiSociety} value={form.amhopi_society} label='AHMOPI Society*' />
              </div>
              <div id='pfType'>
                <DropDown name='pf_type' onChange={handleDropdown} error={errors['pf_type']} items={pfTypes} value={form.pf_type} label='PF Type*' />
              </div>
              <div id='roomType'>
                <DropDown name='room_type' onChange={handleDropdown} error={errors['room_type']} items={roomTypes} value={form.room_type} label='Room Type*' />
              </div>
              <div id='rate'>
                <TextField name='rate' onChange={handleChange} customErrorStyle={{fontSize: '9px'}} type={'number'} error={errors['rate']} label='Rate*' value={form.rate} />
                {/* <FormHelperText style={{ color: 'red' }}>{errors['rate']} </FormHelperText> */}
              </div>
            </div>
          </form>
        </DialogContent>
        <DialogActions className={classes.DialogActions}>
          <Button
            onClick={() => {
              dispatch(SET_OPEN_MODAL(false))
              setForm({
                'amhopi_society': '',
                'pf_type': '',
                'room_type': '',
                'rate': '',
                'code': ''
              })
              setErrors({})
            }}
            variant='contained'
            className={classes.Cancel}
          >
            Cancel
          </Button>
          <Button variant='contained' onClick={handleSubmit} className={classes.Add}>
            Create
          </Button>
        </DialogActions>
      </Dialog>
      <CustomAlerSnackBar isSave={stateValue.addSucces} setIsSave={() => dispatch(SET_ADD_SUCCESS(false))} />
    </React.Fragment>
  )
}

export default React.memo(AddRoomMultiplierModal)
