import React from 'react'
import DialogTitle from '@material-ui/core/DialogTitle'
import DialogContent from '@material-ui/core/DialogContent'
import DisplayFacilitiesAdded from '../Components/DisplayFaciliitesAdded'
import DialogActions from '@material-ui/core/DialogActions'
import Dialog from '@material-ui/core/Dialog'
import Button from '@material-ui/core/Button'
import AutoSuggest from 'Pages/Protected/Partner/Components/AutoSuggest/customInput'
import classes from './index.module.scss'
import AffiliationEncoderService from '../../../../../../Services/Api/Partner/affiliationEncoderService'
import { useDispatch, useSelector } from 'react-redux'
import CustomDialog from '../../../Components/CustomDialog/CustomDialog'
import { SET_OPEN_CUSTOM_INPUT, SET_UPDATE_FACILITY_NAMES } from '../../../../Store/actions/Partner/MaintenanceTable'
import { getNestedArrayOfObjects, getNestedObjects } from '../../../Components/Utils/utils'

const EditFacilityName = props => {
  const dispatch = useDispatch()
  const { stateValue = {} } = props
  const [facilityNames, setFacilityNames] = React.useState([])
  const [editFacilityNamesIndex, setEditFacilityNamesIndex] = React.useState('')
  const [facilityNamesByType, setFacilityNamesByType] = React.useState([])
  const [searchTerm, setSearchTerm] = React.useState('')
  const [isFetching, setIsFetching] = React.useState(false)
  const [tmpFacilityName, setTmpFacilityName] = React.useState({
    '_id': '',
    'name': ''
  })
  const [form, setForm] = React.useState({
    facility_name: ''
  })

  const [uniqueId, setUniqueID] = React.useState('')

  const [facilityType, setFacilityType] = React.useState('')
  const [ duplicate, setDuplicate ] = React.useState(null)

  const dataheader = [
    { name: 'Facilities Included in the Group', prop: 'name' }
  ]

  React.useEffect(() => {
    const facilityGroups = getNestedArrayOfObjects(stateValue, ['facilityNames', 'facilityGroup'])
    const typeFacility = getNestedArrayOfObjects(stateValue, ['facilityNames', 'facilityType'])
    const idToUsed = getNestedArrayOfObjects(stateValue, ['facilityNames', '_id'])
    const ItemIndex = getNestedObjects(stateValue, ['facilityNames', 'indexValue'])

    if (facilityGroups.length > 0) {
      setFacilityNames(facilityGroups)
      typeofFacility(typeFacility)
      setUniqueID(idToUsed)
      setEditFacilityNamesIndex(ItemIndex)
    }
  }, [stateValue.facilityNames])

  const typeofFacility = (type) => {
    switch (type) {
      case 'Medical Clinic':
        setFacilityType('CLINIC')
        break
      case 'Dental Clinic':
        setFacilityType('DENTAL_CLINIC')
        break
      default:
        setFacilityType('HOSPITAL')
        break
    }
  }

  const handleRemoveFacility = index => {
    const newArray = [...facilityNames]
    const res = newArray.filter((facilityNames, i) => i !== index)
    setFacilityNames(res)
  }

  const handleFacilityNameChange = async (value, name) => {
    setSearchTerm(value)
    setIsFetching(true)
    if (typeof value === 'string' && value.length >= 1) {
      try {
        const {
          data: { data }
        } = await AffiliationEncoderService.getFacilityByType({ name: value, type: facilityType })
        setFacilityNamesByType(data)
        setIsFetching(false)
      } catch (error) {
        setIsFetching(false)
        console.log('error occured', error)
      }
    }
  }

  const HandleSelectFacility = ({_id, name}) => {
    setTmpFacilityName({ _id, name })
  }

  const addFacility = () => {
    if (tmpFacilityName.name !== '') {
      const result = facilityNames.some(items => tmpFacilityName['name'] === items.name)
      if (result) {
        setDuplicate(true)
        form.facility_name = ''
        setTmpFacilityName({
          _id: '',
          name: ''
        })
      } else {
        setDuplicate(false)
        const newArray = facilityNames.slice() // Create a copy
        newArray.push(tmpFacilityName) // Push the object
        setFacilityNames(newArray)
        form.facility_name = ''
        setTmpFacilityName({
          _id: '',
          name: ''
        })
      }
    }
  }

  const handleSubmit = e => {
    e.preventDefault()
    dispatch(SET_UPDATE_FACILITY_NAMES({ facilityNames, _id: uniqueId, index: editFacilityNamesIndex }))
    dispatch(SET_OPEN_CUSTOM_INPUT(false))
  }

  return (
    <React.Fragment>
      <Dialog fullWidth maxWidth={'xs'}
        open={stateValue.customInput}
        aria-labelledby='dialogTitle'>
        <DialogTitle id='dialogTitle' style={{ color: '#1e2071', marginTop: '2%', minHeight: '400px' }}>UPDATE FACILITY GROUP</DialogTitle>
        <DialogContent>
          <form className={classes.root} noValidate autoComplete='off' style={{ marginTop: '65px', paddingLeft: '30px' }}>
            <label style={{fontWeight: '600'}}>Facility Name</label>
            <div style={{ 'display': 'inline-flex', paddingRight: '47px' }}>
              <div id='facilityName' className={classes.facilityField}>
                <AutoSuggest
                  suggestions={facilityNamesByType}
                  error={''}
                  value={tmpFacilityName['name'] ? tmpFacilityName['name'] : ''}
                  labelName='name'
                  name='facilityName'
                  searchTerm={searchTerm}
                  onChange={handleFacilityNameChange}
                  onSelect={HandleSelectFacility}
                  isFetching={isFetching}
                  setSearchTerm={setSearchTerm}
                  show={1} // length of search string to show auto suggestions
                />
              </div>
              <a style={{ margin: '12px 0 0 10px', cursor: 'pointer', color: '#3ab77d' }}
                onClick={addFacility}
              >
                  +Add Facility</a>
            </div>
            <div style={{ paddingRight: '47px', display: 'flex', width: '100%', height: 'auto' }}>
              <DisplayFacilitiesAdded
                dataheader={dataheader}
                dataValue={facilityNames.length > 0 ? facilityNames : []}
                handleRemove={handleRemoveFacility}
              />
            </div>
          </form>
        </DialogContent>
        <DialogActions className={classes.DialogActions} style={{paddingTop: '73px', paddingLeft: '47px'}}>
          <Button
            onClick={() => {
              dispatch(SET_OPEN_CUSTOM_INPUT(false))
            }}
            variant='contained'
            className={classes.Cancel}
          >
              Cancel
          </Button>
          <Button variant='contained'
            disabled={facilityNames.length <= 0}
            onClick={handleSubmit}
            className={classes.Add}>
              Save
          </Button>
        </DialogActions>
      </Dialog>
      <div>
        <CustomDialog id='cancelCustomDialog' isCancel={duplicate} setIsCancel={() => setDuplicate(false)}>
          <div id='cancelCustomDialogContainer' className={classes.dialogContainer}>
            <h3 id='cancelCustomDialogMessage'>Facility name exist already</h3>
            <div id='cancelCustomDialogBtnGroup' className={classes.btnGroup}>
              <Button
                onClick={() => setDuplicate(false)}
                id='cancelCustomDialogBtnYes'
                variant='contained'
                style={{ backgroundColor: '#48b77c', color: 'white', marginLeft: '95px' }}
              >
                OKAY
              </Button>
            </div>
          </div>
        </CustomDialog>
      </div>
    </React.Fragment>
  )
}

export default EditFacilityName
