import React from 'react'

import Button from '@material-ui/core/Button'
import Dialog from '@material-ui/core/Dialog'
import DialogActions from '@material-ui/core/DialogActions'
import DialogContent from '@material-ui/core/DialogContent'
import TextField from '../../../Components/JoiValidationInputField'
import DialogTitle from '@material-ui/core/DialogTitle'

import classes from './index.module.scss'
import CustomDialog from '../../../Components/CustomDialog/CustomDialog'
import DisplayFacilitiesAdded from '../AddGroupComponents/DisplayFaciliitesAdded'
import AddFacilityGroup from '../validations'
import DropDown from '../../../Components/CustomDrowpdown'
import { LoadingService } from 'SharedComponents/LoadingComponent/LoadingComponent'
import AffiliationEncoderService from 'Services/Api/Partner/affiliationEncoderService'
import FacilityGroupingService from '../../../../../../Services/Api/FacilityGrouping/FacilityGrouping'
import CustomAlerSnackBar from '../../../Components/CustomSnackBar/CustomAlertSnackbar'
import AutoSuggest from 'Pages/Protected/Partner/Components/AutoSuggest/customInput'
import { SET_OPEN_MODAL, SET_NEW_DATA, SET_ADD_SUCCESS } from '../../../../Store/actions/Partner/MaintenanceTable'
import { getNestedArrayOfObjects, getNestedObjects } from '../../../Components/Utils/utils'
import { useDispatch } from 'react-redux'
import useAsyncHook from '../../../CustomHooks/useAsyncHook2'

const facilityTypes = [
  { id: 'HSP1', text: 'Hospital', value: 'Hospital' },
  { id: 'MD2', text: 'Medical Clinic', value: 'Medical Clinic' },
  { id: 'DC3', text: 'Dental Clinic', value: 'Dental Clinic' }
]

const AddGroup = props => {
  const { stateValue = {} } = props
  const [errors, setErrors] = React.useState({})
  const [facilityType, setFacilityType] = React.useState('HOSPITAL')
  const [form, setForm] = React.useState({
    desc: '',
    facility_type: 'Hospital',
    facilityNames: [],
    code: ''
  })
  const [validationModal, setValidationModal] = React.useState(false)
  const [facilityNames, setFacilityNames] = React.useState([])
  const [facilityNamesByType, setFacilityNamesByType] = React.useState([])
  const [tmpFacilityName, setTmpFacilityName] = React.useState({
    '_id': '',
    'name': '',
    'type': ''
  })
  const [searchTerm, setSearchTerm] = React.useState('')
  const [isFetching, setIsFetching] = React.useState(false)

  const dispatch = useDispatch()

  const [dataAdded, errorGetSave, loadingGetAdded, executeAsyncAdd] = useAsyncHook((data, token) => {
    const argsData = data.data
    return FacilityGroupingService.add(argsData)
  })

  const [getFacilityCount, errorGetFacilityType, loadingGetFacilityType, executeAsyncFacility] = useAsyncHook((data, token) => {
    const argsData = data.data
    return FacilityGroupingService.getFacilityType(argsData, token)
  })

  const [ getFacilityType, errorFacilitType, loadingFacilityType, executeAsyncFacilityType ] = useAsyncHook((data, token) => {
    const argsData = { ...data }

    return AffiliationEncoderService.getFacilityByTypeFacility(argsData, token)
  })

  const handleSubmit = (e) => {
    e.preventDefault()
    validate(true)
  }

  const validate = (toSubmit) => {
    const { errors, isValid } = AddFacilityGroup({ desc: form.desc, facility_type: form.facility_type, facility_names: form.facilityNames })
    if (isValid) {
      setErrors({})
      if (toSubmit) {
        save()
      }
    } else {
      setErrors(errors)
    }
  }

  const save = async () => {
    try {
      await executeAsyncAdd({
        data: form
      })
    } catch (error) {
      LoadingService.loading(false)
    }
  }

  React.useEffect(() => {
    try {
      if (loadingGetAdded) {
        setFacilityNames([])
        setForm({
          desc: '',
          facility_type: 'Hospital',
          facilityNames: [],
          code: ''
        })
        const newData = getNestedArrayOfObjects(dataAdded, ['data'])
        dispatch(SET_NEW_DATA(newData))
        dispatch(SET_OPEN_MODAL(false))
        dispatch(SET_ADD_SUCCESS(true))
      }
    } catch (e) {
      LoadingService.loading(false)
    }
  }, [dataAdded])

  const handleChange = ({ currentTarget: input }) => {
    const newError = { ...errors }
    delete newError['desc']
    setErrors(newError)
    const contactForm = { ...form }
    contactForm[input.name] = input.value
    setForm(contactForm)
  }

  const handleFacilityNameChange = async (value, name) => {
    const updateForm = { ...form }
    const newName = getNestedObjects(value, ['target', 'name'])
    setIsFetching(true)
    if (newName === 'facility_type') {
      const facilityValue = getNestedObjects(value, ['target', 'value'])
      updateForm[newName] = facilityValue
      setFacilityType(facilityValue)
      setCounterCode(facilityValue)
      setForm(updateForm)
    } else {
      setSearchTerm(value)
    }
  }

  React.useEffect(() => {
    getTheFacilityNames()
  }, [searchTerm, facilityType])

  const getTheFacilityNames = async () => {
    try {
      await executeAsyncFacilityType({
        name: searchTerm, type: facilityType
      })
    } catch (e) {
      console.log('error')
    }
  }

  const getCount = async () => {
    const updateForm = { ...form }
    let counter = 1
    const typeData = await getNestedObjects(getFacilityCount, ['data', 'resultValue'])
    const type = await getNestedObjects(getFacilityCount, ['data', 'facilityType'])

    counter = typeData + 1

    let typeText = ''
    switch (type) {
      case 'Hospital':
        typeText = 'HSPGR'
        break
      case 'Medical Clinic':
        typeText = 'MCLGRP'
        break
      case 'Dental Clinic':
        typeText = 'DCLGRP'
        break
    }
    const pad = '0000'
    const counterText = (pad + counter).slice(-pad.length)
    updateForm['code'] = `${typeText}${counterText}`
    setForm(updateForm)
  }

  React.useEffect(() => {
    if (loadingGetFacilityType) {
      getCount()
    }
  }, [getFacilityCount])

  React.useEffect(() => {
    try {
      const data = getNestedArrayOfObjects(getFacilityType, ['data'])

      if (data.length > 0 || loadingFacilityType) {
        setFacilityNamesByType(data)
        setIsFetching(false)
      } else {
        setForm(prevState =>
          ({ ...prevState,
            facilityNames: []}))
        setIsFetching(false)
        setFacilityNamesByType([])
      }
    } catch (e) {
      setIsFetching(false)
      console.log('error', e)
    }
  }, [getFacilityType])

  React.useEffect(() => {
    const typeOfFacility = [...form['facilityNames']]
    const facilityTypeValue = facilityType === 'Medical Clinic'
      ? 'CLINIC'
      : facilityType === 'Dental Clinic'
        ? 'DENTAL_CLINIC'
        : facilityType === 'Hospital'
          ? 'HOSPITAL'
          : facilityType

    const results = typeOfFacility.every(items => items.type === facilityTypeValue)

    if (!results && form['facilityNames'].length > 0) {
      setForm(prevState =>
        ({ ...prevState,
          facilityNames: []}))
    }
  }, [facilityType, form['facilityNames']])

  React.useEffect(() => {
    if (searchTerm.length === 0) {
      setIsFetching(false)
    }
  }, [])

  const addFacility = () => {
    const newFacility = [ ...form['facilityNames'] ]

    if (tmpFacilityName.name === '' || newFacility.some(facility => facility._id === tmpFacilityName._id)) {
      setValidationModal(true)
    } else {
      form['facilityNames'] = [...form.facilityNames, tmpFacilityName]
      setFacilityNames([...newFacility, tmpFacilityName])
      setTmpFacilityName({
        '_id': '',
        'name': ''
      })
    }
  }

  const HandleValidation = () => {
    setValidationModal(false)
    setTmpFacilityName({
      '_id': '',
      'name': ''
    })
  }

  const setCounterCode = async (type) => {
    await executeAsyncFacility({
      data: type
    })
  }

  const dataheader = [
    { prop: 'name' }
  ]

  const handleRemoveFacility = index => {
    const newArray = [...facilityNames]
    const nForm = {...form}
    const nFormFacilityName = [...nForm['facilityNames']]
    const res = newArray.filter((facilityNames, i) => i !== index)
    const res1 = nFormFacilityName.filter((facilityNames, i) => i !== index)
    setFacilityNames(res)
    nForm['facilityNames'] = res1
    setForm(nForm)
  }

  React.useEffect(() => {
    const nForm = {...form}
    switch (nForm.facility_type) {
      case 'Medical Clinic':
        setFacilityType('CLINIC')
        break
      case 'Dental Clinic':
        setFacilityType('DENTAL_CLINIC')
        break
      default:
        setFacilityType('HOSPITAL')
        nForm.code = setCounterCode('Hospital')
        setForm(nForm)
        break
    }
  }, [ form.facility_type, searchTerm ])

  React.useEffect(() => {
    const nForm = { ...form }
    nForm.code = setCounterCode('Hospital')
    setForm(nForm)
  }, [])

  const handleCancel = () => {
    setForm({
      desc: '',
      facility_type: 'HOSPITAL',
      facilityNames: [],
      code: ''
    })
    setValidationModal(false)
    setIsFetching(false)
    setFacilityNames([])
    setFacilityNamesByType([])
    setSearchTerm('')
    setTmpFacilityName({
      '_id': '',
      'name': ''
    })
    setErrors({})
    dispatch(SET_OPEN_MODAL(false))
  }

  const HandleSelectFacility = ({_id, name}) => {
    const newErrors = { ...errors }
    setTmpFacilityName({ _id, name, type: facilityType })
    delete newErrors['facility_names']
    setErrors(newErrors)
  }

  return (
    <React.Fragment>
      <Dialog fullWidth maxWidth={'md'} open={stateValue.openModal} aria-labelledby='dialogTitle'>
        <DialogTitle id='dialogTitle' style={{ color: '#9ea3ac', marginTop: '2%', width: '600px' }}>ADD GROUPINGS</DialogTitle>
        <DialogContent>
          <form className={classes.root} onSubmit={handleSubmit} noValidate autoComplete='off'>
            <div id='firstInput' className={classes.firstInput}>
              <div id='desc' style={{ 'display': 'inline-flex' }}>
                <div style={{width: '50%', position: 'relative'}}>
                  <TextField name='desc' onChange={handleChange} error={errors['desc']} label='Grouping Description*' />
                </div>
                <div style={{width: '50%', marginLeft: '10px'}}>
                  <DropDown name='facility_type' error={errors['facility_type']} onChange={handleFacilityNameChange} items={facilityTypes} value={form.facility_type} label='Type of Facility*' />
                </div>
              </div>
            </div>
            <div className={classes.twoInput} style={{ 'display': 'inline-flex', alignItems: 'center', margin: '10px 0' }}>
              <div id='facilityName' className={classes.facilityField}>
                <AutoSuggest
                  suggestions={facilityNamesByType}
                  error={errors['facility_names']}
                  value={tmpFacilityName['name'] ? tmpFacilityName['name'] : ''}
                  labelName='name'
                  label='Facility Name*'
                  name='facilityName'
                  searchTerm={searchTerm}
                  onChange={handleFacilityNameChange}
                  onSelect={HandleSelectFacility}
                  isFetching={isFetching}
                  setSearchTerm={setSearchTerm}
                  show={1} // length of search string to show auto suggestions
                />
              </div>
              <a style={{ paddingLeft: '10px', cursor: 'pointer', color: '#3ab77d', paddingTop: '10px' }} onClick={addFacility}>+Add Facility</a>
            </div>
            <div className={classes.twoInput}>
              <DisplayFacilitiesAdded
                dataheader={dataheader}
                dataValue={form.facilityNames.length > 0 ? form.facilityNames : []}
                handleRemove={handleRemoveFacility}
                hasError={errors.hasOwnProperty('facility_names')}
              />
            </div>
          </form>
        </DialogContent>
        <DialogActions className={classes.DialogActions}>
          <Button
            onClick={handleCancel}
            variant='contained'
            className={classes.Cancel}
          >
            Cancel
          </Button>
          <Button variant='contained' onClick={handleSubmit} className={classes.Add}>
            Create
          </Button>
        </DialogActions>
      </Dialog>
      <div>
        <CustomDialog id='cancelCustomDialog' isCancel={validationModal} setIsCancel={() => setValidationModal(false)}>
          <div id='cancelCustomDialogContainer' className={classes.dialogContainer}>
            <h3 id='cancelCustomDialogMessage'>Facility name empty or exist already</h3>
            <div id='cancelCustomDialogBtnGroup' className={classes.btnGroup}>
              <Button
                onClick={HandleValidation}
                id='cancelCustomDialogBtnYes'
                variant='contained'
                style={{ backgroundColor: '#48b77c', color: 'white', marginLeft: '95px' }}
              >
                OKAY
              </Button>
            </div>
          </div>
        </CustomDialog>
      </div>
      <CustomAlerSnackBar isSave={stateValue.addSucces} setIsSave={() => dispatch(SET_ADD_SUCCESS(false))} />
    </React.Fragment>
  )
}

export default AddGroup
