import PartnerDoctorService from 'Services/Api/Partner/PartnerDoctorService'
import { dateFormat } from 'Pages/Protected/Partner/Components/Utils/utils'

export default async function getDoctor (id, toUpdate = false) {
  let {
    data: { data }
  } = await PartnerDoctorService.getMedicalPartner(id)
  console.log('medical data id', data)
  if (!toUpdate) {
    return mapData(data)
  } else {
    return mapUpdateData(data)
  }
}

function mapData (data) {
  let {
    gender,
    specialization,
    name,
    license,
    birthdate,
    philhealth,
    treasury,
    id
  } = data
  let obj = {}
  let statusForm = {}
  obj.emailAddress = data['email_address'] ? data['email_address'] : ''
  obj.contactNumber = data['contact_number'] ? data['contact_number'] : ''
  obj.firstName = name.first ? name.first : ''
  obj.lastName = name.last ? name.last : ''
  obj.middleName = name.middle ? name.middle : ''
  obj.civilStatus = data['civil_status'] ? data['civil_status'] : ''
  obj.gender = gender
  obj.suffix = name.suffix ? name.suffix : ''
  obj.birthDate = birthdate ? dateFormat(birthdate) : ''
  obj.pmaNumber = license.pma ? license.pma : ''
  obj.prcNumber = license.prc ? license.prc['number'] : ''
  obj.prcExpiration = license.prc ? dateFormat(license.prc['expiration_date']) : ''
  obj.ptrNumber = license.ptr ? license.ptr : ''
  obj.s2Number = license.s2 ? license.s2 : ''
  obj.mainSpecialization = specialization.main ? specialization.main : ''
  obj.subSpecialization = specialization.sub ? specialization.sub : ''
  obj.majorSpecialization = specialization.major ? specialization.major : ''
  obj.otherSpecialization = specialization.other ? specialization.other : ''
  obj.philhealthNumber = philhealth.accreditation_number ? philhealth.accreditation_number : ''
  obj.philhealthExpiration = philhealth.expiration_date ? philhealth.expiration_date : ''
  obj.payee = treasury.payee ? treasury.payee : ''
  obj.medicalPartnerId = id
  obj.tinNumber = treasury.tin ? treasury.tin : ''
  statusForm.nonVatable = treasury.non_vatable ? treasury.non_vatable : ''
  statusForm.tinStatus = treasury.tin_status ? treasury.tin_status : ''
  statusForm.vatTaxRate = treasury.vat ? treasury.vat['tax_rate'] : ''
  return { obj, statusForm }
}

function mapUpdateData (data) {
  let {
    gender,
    specialization,
    name,
    license,
    birthdate,
    philhealth,
    coordinators,
    remarks,
    treasury,
    id,
    stage,
    mno,
    type,
    status
  } = data
  let sectionOne = {}
  let statusForm = {}
  let remarksForm = {}
  let sectionTwo = {}
  let affilInfos = {}

  sectionOne.medicalPartnerId = data['medical_partner_id']
  sectionOne.emailAddress = data['email_address'] ? data['email_address'] : ''
  sectionOne.contactNumber = data['contact_number'] ? data['contact_number'] : ''
  sectionOne.firstName = name.first ? name.first : ''
  sectionOne.lastName = name.last ? name.last : ''
  sectionOne.middleName = name.middle ? name.middle : ''
  sectionOne.civilStatus = data['civil_status'] ? data['civil_status'] : ''
  sectionOne.gender = gender
  sectionOne.suffix = name.suffix ? name.suffix : ''
  sectionOne.birthDate = birthdate

  sectionOne.mainSpecialization = specialization.main ? specialization.main : ''
  sectionOne.subSpecialization = specialization.sub ? specialization.sub : ''
  sectionOne.majorSpecialization = specialization.major ? specialization.major : ''
  sectionOne.otherSpecialization = specialization.other ? specialization.other : ''

  sectionOne.pmaNumber = license.pma ? license.pma : ''
  sectionOne.ptrNumber = license.ptr ? license.ptr : ''
  sectionOne.s2Number = license.s2 ? license.s2 : ''

  sectionOne.prcNumber = license.prc ? license.prc['number'] : ''
  sectionOne.prcExpiration = license.prc ? dateFormat(license.prc['expiration_date']) : ''
  sectionOne.philhealthNumber = philhealth.accreditation_number ? philhealth.accreditation_number : ''
  sectionOne.philhealthExpiration = philhealth.expiration_date ? philhealth.expiration_date : ''

  sectionOne.tinNumber = treasury.tin ? treasury.tin : ''
  sectionOne.payee = treasury.payee ? treasury.payee : ''

  affilInfos.hasAhmopi = data['has_ahmopi']
  affilInfos.affiliationInfos = data['affiliation_infos'] ? data['affiliation_infos'] : []

  sectionTwo.facilityName = data['facility_info'][0].name ? data['facility_info'][0].name : ''
  sectionTwo.facilityMpId = data['facility_info'][0].facility_id ? data['facility_info'][0].facility_id : ''
  sectionTwo.address = data['facility_info'][0]['full_address'] ? data['facility_info'][0]['full_address'] : ''
  sectionTwo.roomNumber = data['facility_info'][0].room_number ? data['facility_info'][0].room_number : ''
  sectionTwo.contactNumbers = data['facility_info'][0].contact_numbers ? data['facility_info'][0].contact_numbers : ''
  sectionTwo.clinicSchedule = data['facility_info'][0].clinic_schedule ? data['facility_info'][0].clinic_schedule : ''
  sectionTwo.mainCoordinatorName = coordinators && coordinators.main_coordinator_name ? coordinators.main_coordinator_name : ''
  sectionTwo.mainContactNum = coordinators && coordinators.main_contact_num ? coordinators.main_contact_num : ''
  sectionTwo.asstCoordinators = coordinators && coordinators.asst_coordinators ? coordinators.asst_coordinators : []

  remarksForm.applicantRemarks = remarks.applicant ? remarks.applicant : ''
  remarksForm.intellicareRemarks = remarks.intellicare ? remarks.intellicare : ''

  statusForm.tinStatus = treasury.tin_status ? treasury.tin_status : ''
  statusForm.nonVatable = treasury.non_vatable
    ? treasury.non_vatable
    : treasury.tin_status_value
      ? treasury.tin_status_value
      : ''
  statusForm.vatTaxRate = treasury.vat ? treasury.vat['tax_rate'] : ''
  let affil = {}

  affil.status = status
  affil.mno = mno
  affil.type = type
  affil.id = id
  affil.affiliationType = data['affiliation_type']
  affil.affiliationStatus = data['affiliation_status']['name']
  affil.stageName = stage.stage_name
  affil.dateSubmitted = stage.date_submitted
  affil.officeLocation = data['office_location']

  return { sectionOne, sectionTwo, statusForm, affiliationInfos: affilInfos, remarksForm, affil }
}
