import React from 'react'
import ReportServices from '../../../../../Services/Api/Reports/Reports'
import { LoadingService } from 'SharedComponents/LoadingComponent/LoadingComponent'
import classes from '../scss/medicalClinic.module.scss'
import Button from '@material-ui/core/Button'
import PreviewDownload from '../Helpers/PreviewDownload'
import moment from 'moment'
import ReportsTable from '../TableComponent'
import { getNestedObjects } from 'Pages/Protected/Partner/Components/Utils/utils'
import { sosHeaders } from '../Helpers/ReportsTableHeaderData'
import { previewSos } from '../Helpers/tableService'

const SpecialService = () => {
  const [openPreview, setOpenPreview] = React.useState(false)
  const [datas, setDatas] = React.useState([])
  const [tableData, setTableData] = React.useState([])
  const [filters, setFilters] = React.useState({})

  React.useEffect(() => {
    fetchData()

    return () => {
      setTableData([])
    }
  }, [datas])

  const fetchData = async () => {
    LoadingService.loading(true)
    try {
      const newDatas = [...datas]

      const newMapData = newDatas.map(items => {
        return {
          status: getNestedObjects(items, ['status']),
          id: getNestedObjects(items, ['id']),
          name: getNestedObjects(items, ['name']),
          street: getNestedObjects(items, ['address', 'street']),
          barangay: getNestedObjects(items, ['address', 'barangay']),
          city: getNestedObjects(items, ['address', 'city']),
          district_prov: getNestedObjects(items, ['address', 'district_prov']),
          region: getNestedObjects(items, ['address', 'region']),
          contact_number: getNestedObjects(items, ['contact'])
        }
      })

      setTableData(newMapData)
      LoadingService.loading(false)
    } catch (e) {
      LoadingService.loading(true)
      console.log('error', e)
    }
  }

  const headerProps = React.useMemo(() =>
    [
      {
        id: 'Status',
        accessor: 'status',
        sortable: true,
        searchable: true,
        size: '180px'
      },
      {
        id: 'Medical Partner ID',
        accessor: 'id',
        sortable: true,
        searchable: true,
        size: '200px'
      },
      {
        id: 'Service Name',
        accessor: 'name',
        sortable: true,
        searchable: true,
        size: '200px'
      },
      {
        id: 'Unit No./Street',
        accessor: 'street',
        sortable: true,
        searchable: true,
        size: '200px'
      },
      {
        id: 'Barangay',
        accessor: 'barangay',
        sortable: true,
        searchable: true,
        size: '200px'
      },
      {
        id: 'City/Municipality',
        accessor: 'city',
        sortable: true,
        searchable: true,
        size: '200px'
      },
      {
        id: 'Province',
        accessor: 'district_prov',
        sortable: true,
        searchable: true,
        size: '200px'
      },
      {
        id: 'Region',
        accessor: 'region',
        sortable: true,
        searchable: true,
        size: '200px'
      },
      {
        id: 'Contact Number',
        accessor: 'contact_number',
        sortable: false,
        searchable: false,
        size: '200px'
      }
    ], [])

  return (
    <React.Fragment>
      <div className={classes.root}>
        <div id='root' className={classes.tableContainer}>
          <ReportsTable
            getData={(data, token) => {
              const args = { ...data }
              return ReportServices.getServices(args, token)
            }}
            setDatas={setDatas}
            type={'OTHER_SERVICES'}
            headerProps={headerProps}
            setFilters={setFilters}
            datas={tableData}
          />
        </div>
      </div>
      <div className={classes.previewButton}>
        <Button onClick={() => setOpenPreview(true)} variant='contained' className={classes.DownloadPreviewBtn}>
          Download
        </Button>
        {openPreview && <PreviewDownload
          openPreview={openPreview}
          setOpenPreview={setOpenPreview}
          headerColumn={sosHeaders}
          filter={filters}
          filename={`${moment().format('YYYYMMDD')}-other-services.csv`}
          infiniteScrollService={previewSos}
          type='OTHER_SERVICES'
          downloadFunc={ReportServices.downloadSpecialOtherServices}
        />}
      </div>
    </React.Fragment>
  )
}

export default SpecialService
