import React, { useState, useMemo, useEffect, Fragment } from 'react'
import { useParams } from 'react-router-dom'
import { useSelector, useDispatch } from 'react-redux'

import FooterWithButtons from 'Pages/Protected/Partner/Components/DynamicFooterWithButton/FooterWithButtons'
import ProgressComponent from 'Pages/Protected/Partner/AffiliationFormEncoder/Components/ProgressComponent/ProgressComponent'

import { setAffiliationType } from 'Pages/Protected/Store/actions/affiliationType'

import classes from './HospitalPageOne.module.scss'
import { barangayAction, cityAction, provinceAction } from 'Pages/Protected/Store/actions/Partner/addressAction'
import AffiliationForm from '../../components/AffiliationForm/AffiliationForm'
import AffiliationEncoderService from 'Services/Api/Partner/affiliationEncoderService'
import { setAffiliationFormData } from 'Pages/Protected/Store/actions/Partner/hospitalAffiliationForm'
import HospitalService from './hospitalService'

let pathUrl = '/hospital-affiliation-requirements'

let affilConstValue = {
  stageName: 'ENCODING',
  status: 'Active',
  source: 'PNM',
  affiliationStatus: 'In-Progress'
}

function HospitalPageOne ({ history }) {
  const { id } = useParams()
  const dispatch = useDispatch()

  const { affiliationType } = useSelector(state => state.affiliationType)

  const [isSuccess, setIsSuccess] = useState(false)
  const [affilID, setAffilID] = React.useState(null)
  const [isSubmitted, setIsSubmitted] = useState(false)
  const [isValid, setValid] = useState(false)
  const [isTovalidate, setIsToValidate] = useState(false)
  const [hospitalForm, setHospitalForm] = useState({})
  const [loading, setIsloading] = useState(false)
  const [{ sectionOneData, radioButtonData, addressData, affilData, remarksData}, setDataStates] = useState({
    sectionOneData: {},
    radioButtonData: {},
    addressData: {},
    affilData: {},
    remarksData: {},
  })

  useEffect(() => {
    if (isTovalidate) {
      setTimeout(() => {
        setIsToValidate(false)  // reset validation trigger
      }, 200)
    }
  }, [isTovalidate])

  useEffect(() => {
    if (id || affiliationType.id) {
      const paramId = id ? id : affiliationType.id
      fetch(paramId)
    }
    if (affiliationType.hasOwnProperty('id')) {
      setAffilID(affiliationType['id'])
    }
    setSubmitted(affiliationType.stageName)
  }, [])

  const disabledButton = useMemo(() => {
    if (isValid) {
      return false
    } else {
      return true
    }
  }, [isValid])

  const handleSubmit = e => {
    e.preventDefault()
    if (!isSubmitted) {
      setIsToValidate(true)
      if (isValid) {
        save()
      }
    }
  }

  const handleResetForm = () => {
    console.log('handleResetForm')
  }

  const fetch = async (id) => {
    try {
      let {sectionFour, sectionOne, sectionThree, remarksForm, sectionTwo, affiliationType} = await HospitalService(id, setIsloading)
      setDataStates({
          sectionOneData: sectionOne,
          radioButtonData: sectionTwo,
          addressData: sectionThree,
          affilData: sectionFour,
          remarksData: remarksForm,
        })
        dispatch(barangayAction(await fetchBarangays(sectionThree['city'])))
        dispatch(cityAction(await fetchCities(sectionThree['district'])))
        dispatch(provinceAction(await fetchProvince(sectionThree['region'])))
        dispatch(setAffiliationType(affiliationType))
        setAffilID(id)
        setSubmitted(affiliationType.stageName)
        setIsloading(false)
    } catch (error) {
      console.log('error in fetching hospital affiliation data', error)
    }
  }

  const save = async () => {
    let affil = {...affiliationType, ...affilConstValue}
    if (!affilID) {
      store(affil)
    } else {
      update(affil)
    }
  }

  const utils = async () => {
    return await import('Pages/Protected/Partner/Components/Utils/utils')
  }

  const fetchBarangays = async brgy => await (await utils()).fetchBarangays(brgy)

  const fetchCities = async city => await (await utils()).fetchCities(city)

  const fetchProvince = async prov => await (await utils()).fetchProvinces(prov)

  const setSubmitted = stageName => {
    if (stageName === 'AFFILIATION_APPROVAL_FOR_OIC') {
      setIsSubmitted(true)
    }
  }

  const store = async affil => {
    console.log('save click rokc and roll', hospitalForm)
    try {
      const {
        data: {
          data: { id }
        }
      } = await AffiliationEncoderService.createFacilities({ ...affil,...hospitalForm })
      // affilType.id = id
      setAffilID(id)
      affil.id = id
      dispatch(setAffiliationType(affil))
      dispatch(setAffiliationFormData(hospitalForm))
      setIsSuccess(true)
    } catch (error) {
      console.log('error in saving hospital affiliation', error)
    }
  }

  const update = async affil => {
    const paramId = id ? id : affiliationType.id
    try {
      const newForm = { ...hospitalForm }
      const res = await AffiliationEncoderService.updateFacilities(paramId, {
        ...affil,
        ...newForm
      })
      dispatch(setAffiliationType(affil))
      dispatch(setAffiliationFormData(newForm))
      setIsSuccess(true)
    } catch (error) {
      console.log('error ocurred on updating', error)
    }

  }

  if (loading) return <ProgressComponent />
  return (
    <Fragment>
      <AffiliationForm
        history={history}
        setValid={setValid}
        isTovalidate={isTovalidate}
        setHospitalForm={setHospitalForm}
        sectionOneData={sectionOneData}
        sectionThreeData={addressData}
        statusData={radioButtonData}
        remarksData={remarksData}
        isSubmitted={isSubmitted}
        affilData={affilData}
      />
      <div className={classes.footerContainer}>
        <FooterWithButtons
          footerStatus={'Hospital'}
          onClickSave={handleSubmit}
          disableButton={isSubmitted ? false : disabledButton}
          isSuccess={isSuccess}
          setIsSuccess={setIsSuccess}
          isSubmitted={isSubmitted}
          handleReset={handleResetForm}
          pathUrl={pathUrl} // use for proceed button
          save={save}
          history={history}
        />
      </div>
    </Fragment>
  )
}

export default HospitalPageOne
