import React from 'react'
import ReportServices from '../../../../../Services/Api/Reports/Reports'
import PreviewDownload from '../Helpers/PreviewDownload'
import moment from 'moment'
import Button from '@material-ui/core/Button'
import classes from '../scss/medicalClinic.module.scss'
import { LoadingService } from 'SharedComponents/LoadingComponent/LoadingComponent'
import ReportsTable from '../TableComponent'
import { getNestedObjects, getNestedArrayOfObjects } from 'Pages/Protected/Partner/Components/Utils/utils'
import { hospitalHeaders } from '../Helpers/ReportsTableHeaderData'
import { getAffiliatedHospitalInfiniteScroll, modifyFilters } from '../Helpers/tableService'
import AffiliationEncoderService from 'Services/Api/Partner/affiliationEncoderService'

const mongoSelect = {
  _id: 0,
  status: 1,
  classification: 1,
  id: 1,
  name: 1,
  street: '$address.street',
  barangay: '$address.barangay',
  city: '$address.city',
  district: '$address.district',
  province: '$address.province',
  region: '$address.region',
  contactNo: { $arrayElemAt: ['$contacts', 0] }
}

const Hospital = () => {
  const [openPreview, setOpenPreview] = React.useState(false)
  const [datas, setDatas] = React.useState([])
  const [tableData, setTableData] = React.useState([])
  const [filters, setFilters] = React.useState({})
  const [modifiedFilter, setNewModifiedFilter] = React.useState({
    type: 'HOSPITAL',
    'affiliation_status.name': 'Approved'
  })

  React.useEffect(() => {
    fetchData()
  }, [datas])

  React.useEffect(() => {
    const nFilter = { ...modifiedFilter }
    let mFilter = modifyFilters(filters)
    const newFilter = Object.assign(nFilter, mFilter)
    setNewModifiedFilter(newFilter)
  }, [filters])

  const fetchData = async () => {
    LoadingService.loading(true)
    try {
      console.log('datasssss', datas)
      const newData = datas.map(items => {
        const address = getNestedObjects(items, ['address'])

        return {
          status: getNestedObjects(items, ['status']),
          classification: getNestedObjects(items, ['classification']),
          id: getNestedObjects(items, ['id']),
          name: getNestedObjects(items, ['name']),
          street: address.street,
          barangay: address.barangay,
          city: address.city,
          district: address.district,
          region: address.region,
          cNumber: getNestedArrayOfObjects(items, ['contacts']).length > 0
            ? getNestedObjects(items, ['contacts']).map(items => getNestedObjects(items, ['cNumber'])).join(' ,')
            : ''
        }
      })

      console.log('newDatasss', newData)

      setTableData(newData)
      LoadingService.loading(false)
    } catch (e) {
      console.log('error', e)
    }
  }

  const headerProps = React.useMemo(() =>
    [
      {
        id: 'Status',
        accessor: 'status',
        sortable: true,
        searchable: true,
        size: '180px'
      },
      {
        id: 'Facility Level',
        accessor: 'classification',
        sortable: true,
        searchable: true,
        size: '200px'
      },
      {
        id: 'Medical Partner ID',
        accessor: 'id',
        sortable: true,
        searchable: true,
        size: '200px'
      },
      {
        id: 'Medical Clinic Name',
        accessor: 'name',
        sortable: true,
        searchable: true,
        size: '200px'
      },
      {
        id: 'Unit No./Street',
        accessor: 'street',
        sortable: true,
        searchable: true,
        size: '200px'
      },
      {
        id: 'Barangay',
        accessor: 'barangay',
        sortable: true,
        searchable: true,
        size: '200px'
      },
      {
        id: 'City/Municipality',
        accessor: 'city',
        sortable: true,
        searchable: true,
        size: '200px'
      },
      {
        id: 'Province',
        accessor: 'district',
        sortable: true,
        searchable: true,
        size: '200px'
      },
      {
        id: 'Region',
        accessor: 'region',
        sortable: true,
        searchable: true,
        size: '200px'
      },
      {
        id: 'Contact Number',
        accessor: 'cNumber',
        sortable: false,
        searchable: false,
        size: '180px'
      }
    ], [])

  return (
    <React.Fragment>
      <div className={classes.root}>
        <div id='root' className={classes.tableContainer}>
          <ReportsTable
            getData={(data, token) => {
              const args = { ...data }
              return ReportServices.getFacilitiesByType(args, token)
            }}
            setDatas={setDatas}
            type={'HOSPITAL'}
            headerProps={headerProps}
            datas={tableData}
            setFilters={setFilters}
          />
        </div>
      </div>
      <div className={classes.previewButton}>
        <Button onClick={() => setOpenPreview(true)} variant='contained' className={classes.DownloadPreviewBtn}>
          Download
        </Button>
        {openPreview && <PreviewDownload
          openPreview={openPreview}
          setOpenPreview={setOpenPreview}
          headerColumn={hospitalHeaders}
          filename={`${moment().format('YYYYMMDD')}-hospital.csv`}
          mongoSelect={mongoSelect}
          filter={modifiedFilter}
          infiniteScrollService={getAffiliatedHospitalInfiniteScroll}
          downloadFunc={AffiliationEncoderService.download}
          type='HOSPITAL'
        />}

      </div>
    </React.Fragment>
  )
}

export default Hospital
