import React from 'react'
import TextField from '@material-ui/core/TextField'
import classes from './index.module.scss'
import EditIcon from '@material-ui/icons/Edit'
import FormHelperText from '@material-ui/core/FormHelperText'

const ChangeName = props => {
  const { setIsEdit, firstName, lastName, suffix, middleName, HandleChange, error, isEdit } = props

  console.log('propssssss', props)

  if (isEdit) {
    return (
      <div id='changeName' className={classes.changeName}>
        <div id='editIcon' className={classes.editIcon}>
          <EditIcon
            onClick={() => {
              setIsEdit(false)
            }}
          />
        </div>
        <div id='textFields' className={classes.textFields}>
          <TextField
            id='last_name'
            className={`${classes.TextField} ${error.lastName && classes.hasError}`}
            autoComplete='off'
            name={'lastName'}
            placeholder='Last Name'
            value={lastName}
            onChange={HandleChange}
          />
          { error && <FormHelperText id='error' className={classes.nameError} > {error.lastName} </FormHelperText>}
        </div>
        <div id='textFields' className={classes.textFields}>
          <TextField
            id='firstName'
            className={`${classes.TextField} ${error.firstName && classes.hasError}`}
            autoComplete='off'
            name={'firstName'}
            placeholder='First Name'
            value={firstName}
            onChange={HandleChange}
          />
          { error && <FormHelperText id='error' className={classes.nameError} > {error.firstName} </FormHelperText>}
        </div>
        <TextField
          id='suffix'
          className={classes.TextField}
          autoComplete='off'
          name={'suffix'}
          placeholder={'Suffix'}
          value={suffix.trim()}
          onChange={HandleChange}
        />
        <div id='textFields' className={classes.textFields}>
          <TextField
            id='middle_name'
            className={`${classes.TextField} ${error.middleName && classes.hasError}`}
            autoComplete='off'
            name={'middleName'}
            placeholder='Middle Name'
            value={middleName}
            onChange={HandleChange}
          />
          { error && <FormHelperText id='error' className={classes.nameError} > {error.middleName} </FormHelperText>}
        </div>
      </div>
    )
  } else {
    return (
      <div id='changeName' className={classes.changeName}>
        <div id='editIcon' className={classes.editIcon}>
          <EditIcon
            onClick={() => {
              setIsEdit(true)
            }}
          />
        </div>
        {`${lastName.toUpperCase()} , ${firstName.toUpperCase()} ${suffix.toUpperCase()} ${middleName.toUpperCase()}`}
      </div>
    )
  }
}

export default React.memo(ChangeName
  , (prevProps, nextProps) => {
    try {
      if (
        prevProps.setIsEdit === nextProps.setIsEdit &&
        prevProps.firstName === nextProps.firstName &&
      prevProps.lastName === nextProps.lastName &&
      prevProps.suffix === nextProps.suffix &&
      prevProps.middleName === nextProps.middleName &&
     prevProps.HandleChange === nextProps.HandleChange &&
      prevProps.error === nextProps.error &&
      prevProps.isEdit === nextProps.isEdit
      ) {
        return true
      }
      return false
    } catch (e) {
      return false
    }
  })
