import React from 'react'
import MaintenanceTable from '../Helper/Table'
import PfMultiplierService from '../../../../../Services/Api/Partner/PfMultiplierServices'
import AddMultiplier from './AddMultiplier'
import { useSelector } from 'react-redux'

const breadcrumbs = {
  Medical: {
    text: 'MEDICAL COMPLIANCE ASSISTANT MANAGER',
    to: '/'
  },
  Maintenance: {
    text: 'MAINTENANCE TABLE',
    to: '/'
  },
  Table: {
    text: 'PROFESSIONAL FEE MULTIPLIER MAINTENANCE',
    to: '/ProfessionalFeeMultiplierMaintenance'
  }
}

const PFMultiplier = props => {
  const stateValue = useSelector(state => state.MaintenanceTable)
  return (
    <React.Fragment>
      <AddMultiplier stateValue={stateValue} />
      <MaintenanceTable
        getData={(data, token) => {
          const args = { ...data }
          return PfMultiplierService.getPFmultiplier(args, token)
        }}
        updateData={(data, token) => {
          const args = { ...data }
          return PfMultiplierService.updateMultiplier(args)
        }}

        tableHeaderProps={[
          { name: 'PF Multiplier Code', id: 'code', inputFields: true, sortable: true, editable: false, size: '90%', inputType: 'none', type: 'text', multiline: false },
          { name: 'AHMOPI Society', id: 'amhopi_society', inputFields: true, sortable: true, editable: false, size: '100%', inputType: 'input', type: 'text', multiline: true },
          { name: 'Status', id: 'status', inputFields: false, sortable: false, editable: true, size: '100%', inputType: 'dropDown', type: 'text', multiline: false },
          { name: 'PF Type', id: 'pf_type', inputFields: false, sortable: false, editable: false, size: '100%', inputType: 'input', type: 'text', multiline: true },
          { name: 'Room Type', id: 'room_type', inputFields: false, sortable: false, editable: false, size: '100%', inputType: 'input', type: 'text', multiline: true },
          { name: 'Rate', id: 'rate', inputFields: false, sortable: false, editable: true, size: '100%', inputType: 'input', type: 'number', multiline: true }
        ]}
        open={props.open}
        setOpen={props.setOpen}
        headerTitle={'Professional Fee Maintenance'}
        breadcrumbs={breadcrumbs}
        addButtonTitle={'Add Multiplier'}
        updateButtonTitle={'Update'}
        paginationlabel={'Professional Fee Multipliers'}
      />

    </React.Fragment>
  )
}

export default PFMultiplier
