import React from 'react'
import MaintenanceTable from './MaintenanceTable/TableIndex'

const Index = props => {
  const [isLoaded, setIsLoaded] = React.useState(false)

  React.useEffect(() => {
    if (!isLoaded && props.open) {
      setIsLoaded(true)
    }
  }, [props.open])

  return (
    <React.Fragment>
      {isLoaded && (
        <MaintenanceTable {...props} />
      )}
    </React.Fragment>
  )
}

export default Index
