
import AffiliationApproverServices from 'Services/Api/Partner/AffiliationApproverServices'

const HospitalService = async (id, setIsloading) => {
  setIsloading(true)
  if (id) {
    try {
      const sectionOne = {}
      const sectionTwo = {}
      const sectionThree = {}
      const sectionFour = {}
      const remarksForm = {}
      const {
        data: { data: fetchAffil }
      } = await AffiliationApproverServices.getFacilityPartnerById(id)
      const { stage, affiliation_status } = fetchAffil
      let affiliationType = {}
      affiliationType.id = id
      affiliationType.dateRecived = fetchAffil['stage']['date_submitted']
      affiliationType.type = fetchAffil['type']
      affiliationType.mno = fetchAffil['mno']
      affiliationType.stageName = fetchAffil['stage'] ? fetchAffil['stage']['stage_name'] : ''
      affiliationType.dateSubmitted = stage['date_submitted'] ? stage['date_submitted'] : new Date().toISOString()
      affiliationType.status = fetchAffil['status']
      affiliationType.source = fetchAffil['source']
      affiliationType.officeLocation = fetchAffil['office_location']
      affiliationType.affiliationStatus = affiliation_status['name'] ? affiliation_status['name'] : ''
      const address = fetchAffil.address
      const operation = fetchAffil.operation
      const treasury = fetchAffil.treasury
      const doh = fetchAffil.license.doh
      const philHealth = fetchAffil.philhealth
      const remarks = fetchAffil.remarks
      sectionOne['name'] = fetchAffil['name']
      sectionOne['bedCapacity'] = fetchAffil['bed_capacity']
      sectionOne['years'] = operation['years']
      sectionOne['months'] = operation['months']
      sectionOne['email'] = fetchAffil['email']
      sectionOne['dohLicenseExp'] = doh['license_exp']
      sectionOne['philhealthExpiration'] = philHealth['expiration_date']
      sectionOne['philhealthNumber'] = philHealth['accreditation_number']
      sectionOne['dohNumber'] = doh['number']
      sectionOne['dohLicenseType'] = doh['license_type']
      sectionOne['payee'] = treasury['payee']
      sectionThree['unitFlrBldg'] = address['unit_flr_bldg']
      sectionThree['bldgName'] = address['building_name']
      sectionThree['street'] = address['street']
      sectionThree['zipCode'] = address['zip_code']
      sectionThree['subdivision'] = address['subdivision']
      sectionThree['barangay'] = address['barangay']
      sectionThree['city'] = address['city']
      sectionThree['district'] = address['district']
      sectionThree['region'] = address['region']
      sectionFour['contacts'] = fetchAffil['contacts']
      sectionTwo['classification'] = fetchAffil['classification']
      sectionTwo['processType'] = fetchAffil['process_type']
      sectionTwo['tinStatus'] = treasury['tin_status']
      sectionTwo['nonVatable'] = treasury['non_vatable']
      sectionTwo['vatTaxRate'] = treasury['vat']['tax_rate']
      remarksForm['internalRemarks'] = remarks['internal_remarks']
      remarksForm['externalRemarks'] = remarks['external_remarks']
      sectionFour['affiliationInfos'] = fetchAffil['affiliation_infos']
      return {sectionOne, sectionTwo, sectionThree, sectionFour, remarksForm, affiliationType}
    } catch (e) {
      setIsloading(false)
      console.log('error in hospital re encoding', e)
    }
  }
}

export default HospitalService
