import instance from './../instance'

const facilities = instance('/p/f')

export default class PfMultiplierService {
  static async add (data) {
    return facilities({
      url: `/partners/pf-multiplier/add`,
      method: 'POST',
      headers: { skip_generate_form_data: 1 },
      data
    })
  }

  static async get () {
    return facilities({
      url: `/partners/pf-multipliers`,
      method: 'GET'
    })
  }

  static async getAhmopiType (args, argCancelToken) {
    const params = {
      ahmopiType: args
    }

    return facilities({
      url: `/partners/get-pf-ahmopi-type`,
      method: 'GET',
      headers: { skip_generate_form_data: 1 },
      params,
      cancelToken: argCancelToken.cancelToken
    })
  }

  static getPFmultiplier (argParams, argCancelToken) {
    const params = {
      skip: argParams.offset,
      limit: argParams.limit,
      filter: argParams.filter
    }

    return facilities({
      url: `/partners/get-pf-multipliers`,
      method: 'GET',
      headers: { skip_generate_form_data: 1 },
      params,
      cancelToken: argCancelToken.cancelToken
    })
  }

  static updateMultiplier (args) {
    return facilities({
      url: `/partners/update-pf-multipliers`,
      method: 'PUT',
      headers: { skip_generate_form_data: 1 },
      data: {...args}
    })
  }

  static async update (data) {
    return facilities({
      url: `/partners/pf-multipliers/${data._id}`,
      method: 'PUT',
      headers: { skip_generate_form_data: 1 },
      data: {
        amhopiSociety: data.amhopi_society,
        status: data.status,
        pfType: data.pf_type,
        roomType: data.room_type,
        rate: data.rate,
        code: data.code
      }
    })
  }
}
