import React, { memo, useRef, useState, useEffect } from 'react'
import Paper from '@material-ui/core/Paper'
import InputBase from '@material-ui/core/InputBase'
import Typography from '@material-ui/core/Typography'

import classes from './EncoderInputFieldNumberFormat.module.scss'
import NumberFormat from 'react-number-format'
import onPasteInputHook from '../EncoderCustomHooks/useInputOnPasteHook'
import { inputTypes } from 'Pages/Protected/Partner/Components/JoiValidationInputField/utils'

let types = ['number', 'noSpecialChar', 'lettersOnly', 'integerOnly']
function NumberFormatCustom (props) {
  const numberInputRef = useRef()

  const { onChange, defaultValue, ...other } = props

  const handleChange = (e) => {
    const {
      target: { value }
    } = e
    numberInputRef.current.value = value
    onChange(e)
  }

  return (
    <NumberFormat
      {...other}
      suffix='-'
      ref={numberInputRef}
      value={defaultValue}
      onValueChange={(values) => {
        handleChange({
          target: {
            name: props.name,
            value: values.value
          }
        })
      }}
    />
  )
}

function CustomTextFieldNumberFormat (props) {
  const {
    label = '',
    disabled,
    required,
    inputRef,
    constainerStyle = {},
    error,
    onChange,
    defaultValue,
    format,
    type = 'number',
    name,
    ...other
  } = props

  useEffect(() => {
    setRegex(inputTypes(type))
  }, [])

  const [regex, setRegex] = useState({})

  const [handleOnPaste] = onPasteInputHook(regex.forOnPaste)

  return (
    <div className={classes.root} style={constainerStyle}>
      <Typography variant='subtitle2' className={classes.label}>
        {label}
      </Typography>
      <div className={classes.wrapper}>
        <React.Fragment>
          <Paper className={`${classes['text-field']} ${error && classes['has-error']}`}>
            <InputBase
              className={classes.input}
              disabled={disabled}
              autoComplete='off'
              name={name}
              onPaste={types.includes(type) ? handleOnPaste : null}
              defaultValue={defaultValue}
              inputProps={{
                format,
                defaultValue,
                onChange
              }}
              inputComponent={NumberFormatCustom}
              {...other}
            />
          </Paper>
          {error && <span className={classes.error}>{error}</span>}
        </React.Fragment>
      </div>
    </div>
  )
}

export default memo(CustomTextFieldNumberFormat)
