import React from 'react'
import classes from './index.module.scss'
import EditIcon from '@material-ui/icons/Edit'
import TextField from '@material-ui/core/TextField'
import FormHelperText from '@material-ui/core/FormHelperText'

const ChangeName = props => {
  const { isEdit, setIsEdit, HandleChange, form, error } = props

  if (isEdit) {
    return (
      <div id='changeName' className={classes.changeName}>
        <div id='editIcon' className={classes.editIcon}>
          <EditIcon
            onClick={() => {
              setIsEdit(false)
            }}
          />
        </div>
        <div id='textFields' className={classes.textFields}>
          <TextField
            id='inputName'
            className={`${classes.TextField} ${error.lastName && classes.hasError}`}
            autoComplete='off'
            placeholder='Last Name'
            name={'lastName'}
            value={form.lastName}
            onChange={HandleChange}
          />
          { error && <FormHelperText id='error' className={classes.nameError} > {error.lastName} </FormHelperText>}
        </div>
        <div id='textFields' className={classes.textFields}>
          <TextField
            id='inputName'
            className={`${classes.TextField} ${error.firstName && classes.hasError}`}
            autoComplete='off'
            name={'firstName'}
            placeholder='First Name'
            value={form.firstName}
            onChange={HandleChange}
          />
          { error && <FormHelperText id='error' className={classes.nameError} > {error.firstName} </FormHelperText>}
        </div>
        <TextField
          id='inputName'
          className={classes.TextField}
          autoComplete='off'
          placeholder='Suffix'
          name={'suffix'}
          value={form.suffix.trim()}
          onChange={HandleChange}
        />
        <div id='textFields' className={classes.textFields} >
          <TextField
            id='inputName'
            className={`${classes.TextField} ${error.middleName && classes.hasError}`}
            autoComplete='off'
            placeholder='Middle Name'
            name={'middleName'}
            value={form.middleName}
            onChange={HandleChange}
          />
          { error && <FormHelperText id='error' className={classes.nameError} > {error.middleName} </FormHelperText>}
        </div>
      </div>
    )
  } else {
    return (
      <div id='changeName' className={classes.changeName}>
        <div id='editIcon' className={classes.editIcon}>
          <EditIcon
            onClick={() => {
              setIsEdit(true)
            }}
          />
        </div>
        {`${form['lastName']} ${form['firstName']} ${form['suffix']} ${form['middleName']}`}
      </div>
    )
  }
}

export default ChangeName
