import React, { useState } from 'react'

import Table from '@material-ui/core/Table'
import TableBody from '@material-ui/core/TableBody'
import TableCell from '@material-ui/core/TableCell'
import TableContainer from '@material-ui/core/TableContainer'
import TableRow from '@material-ui/core/TableRow'
import classes from './UploadFIleTable.module.scss'
import Preview from '../../../../AffiliationFormEncoder/Pages/PreviewImage/PreviewImage'
import Axios from 'axios'
import { useDispatch } from 'react-redux'
import { setAffiliationType } from 'Pages/Protected/Store/actions/affiliationType'
import ProfileService from 'Services/Api/Partner/ProfileService'
import GetFile from '../../../../../../../Services/Api/Partner/GetFileUpload'

const URL = `${process.env.REACT_APP_SERVER_API}`

const createData = (description, uploadButton) => {
  return { description, uploadButton }
}

const DescriptionWithSubscript = ({ mainText, subscript }) => {
  return (
    <div id='DescriptionWithSubscript' className={classes.WithSubScript}>
      <text>{mainText} </text>
      <sub id='subscript-PhotoOfPhicID' className={classes.subscript}>
        {subscript}
      </sub>
    </div>
  )
}
const ActualImageAreas = () => {
  return (
    <div id='ActualImageArea' className={classes.WithSubScript}>
      Actual Image of Areas in the Facility
      <br />
      <small id='subscript-ActualImageArea' className={classes.subscript}>
        *All facility departments, Front Area/Front Image of the facility.
      </small>
    </div>
  )
}

const UploadFIleTable = props => {
  const { buttonText, onPreviewFile } = props
  let dispatch = useDispatch()

  React.useEffect(() => {
    fetchData()
  }, [])

  const forView = false
  const [mainFiles, setMainFiles] = useState([])

  const fetchData = async () => {
    try {
      const { data } = await ProfileService.getClinicById(props.paramId) // Axios.get(URL + `/p/f/partners/facilities/id/${props.paramId}`)
      const paramAffilId =
        data.data.afn_number === null || data.data.afn_number === undefined ? props.paramId : data.data.afn_number
      dispatch(setAffiliationType({ id: paramAffilId }))
      const filesData = await GetFile.getFile(paramAffilId)
      // const filesData = await Axios.get(`${URL}/p/c/partners/common/files/${paramAffilId}`) // ProfileService.getUploadFile(paramAffilId)
      for (let fileData of filesData.data.data) {
        let tmpFiles = fileData.files
        setMainFiles(previousState => ({
          ...previousState,
          [fileData.file_type]: tmpFiles.map(file =>
            Object.assign(file, {
              preview: `${URL}/uploads/${file.path}`,
              fileType: fileData.file_type
            })
          )
        }))
      }
    } catch (e) {
      console.log('error', e)
    }
  }

  const rows = [
    createData(
      <Preview
        onPreviewFile={onPreviewFile}
        desc={'Dental Clinic Profile'}
        fileType={'dental-clinic-profile'}
        mainFiles={mainFiles}
        setMainFiles={setMainFiles}
        buttonText={'Upload'}
        forView={forView}
      />
    ),
    createData(
      <Preview
        onPreviewFile={onPreviewFile}
        desc={'Updated List of Services offered with Rates'}
        fileType={'list-service-offered-with-rates'}
        mainFiles={mainFiles}
        setMainFiles={setMainFiles}
        buttonText={'Upload'}
        forView={forView}
      />
    ),
    createData(
      <Preview
        onPreviewFile={onPreviewFile}
        desc={
          <DescriptionWithSubscript mainText={'Location Map'} subscript={'*Preferably screenshot from google maps'} />
        }
        fileType={'location-map'}
        mainFiles={mainFiles}
        setMainFiles={setMainFiles}
        buttonText={'Upload'}
        forView={forView}
      />
    ),
    createData(
      <Preview
        onPreviewFile={onPreviewFile}
        desc={<ActualImageAreas />}
        fileType={'actual-image-of-areas-facility'}
        mainFiles={mainFiles}
        setMainFiles={setMainFiles}
        buttonText={'Upload'}
        forView={forView}
      />
    ),
    createData(
      <Preview
        onPreviewFile={onPreviewFile}
        desc={'Business Permit'}
        fileType={'business-permit'}
        mainFiles={mainFiles}
        setMainFiles={setMainFiles}
        buttonText={'Upload'}
        forView={forView}
      />
    ),
    createData(
      <Preview
        onPreviewFile={onPreviewFile}
        desc={'BIR COR 2303'}
        fileType={'bir-cor-2303'}
        mainFiles={mainFiles}
        setMainFiles={setMainFiles}
        buttonText={'Upload'}
        forView={forView}
      />
    ),
    createData(
      <Preview
        onPreviewFile={onPreviewFile}
        desc={'SEC/DTI Registration'}
        fileType={'set-dti-registration'}
        mainFiles={mainFiles}
        setMainFiles={setMainFiles}
        buttonText={'Upload'}
        forView={forView}
      />
    )
  ]

  return (
    <TableContainer id='tableContainer' className={classes.tableContainer}>
      <Table className={classes.table} size='small' aria-label='a dense table'>
        <TableBody id='table-body'>
          {rows.map((row, index) => (
            <TableRow id='TableRow' className={classes['table-row']} key={index}>
              <TableCell id='description-row' className={classes['description-row']} component='th' scope='row'>
                {row.description}
              </TableCell>
            </TableRow>
          ))}
        </TableBody>
      </Table>
    </TableContainer>
  )
}

export default UploadFIleTable
