import React from 'react'
import { TableRow, TableCell, Button, InputBase } from '@material-ui/core'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import InputHeader from '../Inputs/inputHeader'

import classes from './index.module.scss'

const HeaderComponents = props => {
  const { tableHeaderProps = [], dispatch = null, setSort = null, loadingGet = null, setfilter = null } = props

  const handlechange = React.useCallback(e => {
    const { name, value } = e.target
    dispatch(setfilter({
      field: name,
      value: value,
      items: tableHeaderProps
    }))
  }, [])

  React.useEffect(() => {
    return () => {
      dispatch(setfilter({
      }))
    }
  }, [])

  return (
    <React.Fragment>
      <TableRow className={classes.TableRow}>
        {
          tableHeaderProps.map((items, index) => {
            return (
              <TableCell
                component='th'
                scope='row'
                align='left'
                key={index}
                className={classes.TableCell}
                style={{width: items.size}}
              >
                <div id='root-div' className={classes.root}>
                  <div id='tableContainer' className={classes.tableContainer}>
                    <div id='header-name' className={classes.header} >
                      {items.name}
                      {items.sortable &&
                      <FontAwesomeIcon
                        className={classes.sortIcon}
                        onClick={() => { dispatch(setSort(items)) }}
                        icon='sort'
                        id={`sort-${index}`} /> }
                    </div>
                    <div id='textDescription' className={classes.textDescription}>
                      {items.inputFields
                        ? <InputHeader
                          name={items.id}
                          onChange={handlechange}
                          size={items.size}
                        />
                        : <div className={classes.noInputFields} />
                      }
                    </div>
                  </div>
                </div>
              </TableCell>
            )
          })
        }
      </TableRow>
    </React.Fragment>
  )
}

export default React.memo(HeaderComponents)
