import React from 'react'
import Modal from '@material-ui/core/Modal'
import InputComponent from '../../../../../../Components/InputComponent/InputComponent'
import { Button } from '@material-ui/core'
import RoomAndBoardService from 'Services/Api/Partner/RoomAndBoardService'
import useAsyncHook from '../../../../../../CustomHooks/useAsyncHook2'
import { LoadingService } from 'SharedComponents/LoadingComponent/LoadingComponent'
import CustomAlert from '../../../../../../Components/CustomSnackBar/CustomAlert'
import { SnackbarService } from 'SharedComponents/SnackbarComponent/SnackbarComponent'
import classes from './RoomType.module.scss'

const RoomType = props => {
  const { id, open = false, setOpen, roomTypes = [], triggerFunction = null } = props

  const [roomTypeList, setRoomTypeList] = React.useState([])
  const [roomCode, setRoomCode] = React.useState('')
  const [roomType, setRoomType] = React.useState('')
  const [description, setDescription] = React.useState('')
  const [remarks, setRemarks] = React.useState('')
  const [openAlert, setOpenAlert] = React.useState({
    message: '',
    status: false
  })

  const [roomTypeError, setRoomTypeError] = React.useState(false)
  const [descriptionError, setDescriptionError] = React.useState(false)

  const [data, error, loading, executeAsync] = useAsyncHook((data, token) => {
    const args = { ...data, id }
    return RoomAndBoardService.createRoomAndBoard(args, token)
  })

  React.useEffect(() => {
    const list = roomTypes.map(room => {
      return {
        text: room.desc,
        value: room.desc
      }
    })

    setRoomTypeList(list)
  }, [roomTypes.length])

  React.useEffect(() => {
    if (roomType) {
      const roomCode = roomTypes.find(room => room.desc === roomType)
      setRoomCode(roomCode.code)
    }
  }, [roomType])

  React.useEffect(() => {
    const loadTable = async () => {
      await triggerFunction()
    }
    if (data.status === 201) {
      setOpenAlert({
        status: true,
        message: 'Successfully saved'
      })
      loadTable()
    }
  }, [data])

  React.useEffect(() => {
    if (error.message === 'Duplicate') {
      SnackbarService.set({
        open: true,
        message: 'Duplicate Data',
        severity: 'error'
      })
    }
    if (error.message === 'Error') {
      SnackbarService.set({
        open: true,
        message: 'Error',
        severity: 'error'
      })
    }
  }, [error])

  React.useEffect(() => {
    if (loading) {
      LoadingService.loading(true)
    } else {
      LoadingService.loading(false)
    }
  }, loading)

  const onSubmitForm = async e => {
    e.preventDefault()
    if (validateForm()) {
      await executeAsync({
        roomCode,
        roomType,
        description,
        remarks
      })
    } else {
      if (!roomType) setRoomTypeError(true)
      if (!description) setDescriptionError(true)
    }
  }

  const validateForm = () => {
    if (!roomType || !description) return false
    return true
  }

  const closeModal = () => {
    setRoomCode('')
    setRoomType('')
    setDescription('')
    setRemarks('')
    setRoomTypeError(false)
    setDescriptionError(false)
    setOpen(false)
  }

  return (
    <React.Fragment>
      <Modal
        open={open}
        className={classes.root}
        aria-labelledby='simple-modal-title'
        aria-describedby='simple-modal-description'
      >
        <div id='roomTypeContainer' className={classes.roomTypeContainer}>
          <CustomAlert
            onActivate={openAlert.status}
            customFunc={() => {
              setOpenAlert({
                ...openAlert,
                status: false
              })
              closeModal()
            }}
          >
            {openAlert.message}
          </CustomAlert>

          <form className={classes.formGroup} onSubmit={onSubmitForm}>
            <h3>ADD ROOM TYPE</h3>
            <div className={classes.inputGroup}>
              <div className={classes.inputContainer}>
                <label style={{ color: '#1e2071' }}>Room Type*</label>
                <InputComponent
                  readOnly={false}
                  value={roomType}
                  type='select'
                  defaultMenu=''
                  menu={roomTypeList}
                  requiredMessage={roomTypeError ? '"Room Type" is not allowed to be empty' : ''}
                  requiredValid={!roomTypeError}
                  onChangeText={(name, val) => {
                    setRoomType(val)
                    if (val.length) {
                      setRoomTypeError(false)
                    }
                  }}
                />
              </div>
              <div className={classes.inputContainer} style={{ padding: '0 10px' }}>
                <label style={{ color: '#1e2071' }}>Room Description*</label>
                <InputComponent
                  readOnly={false}
                  value={description}
                  requiredMessage={descriptionError ? '"Room Description" is not allowed to be empty' : ''}
                  requiredValid={!descriptionError}
                  onChangeText={(_, val) => {
                    setDescription(val)
                    if (val.length) {
                      setDescriptionError(false)
                    }
                  }}
                />
              </div>
              <div className={classes.inputContainer}>
                <label style={{ color: '#1e2071' }}>Remarks</label>
                <InputComponent
                  readOnly={false}
                  value={remarks}
                  onChangeText={(_, val) => {
                    setRemarks(val)
                  }}
                />
              </div>
            </div>
            <div className={classes.buttonGroup}>
              <Button
                style={{
                  color: '#f5f7fb',
                  textTransform: 'none',
                  margin: '0 5px',
                  backgroundColor: '#fd5451'
                }}
                variant='contained'
                onClick={() => closeModal()}
              >
                Cancel
              </Button>
              <Button
                type='submit'
                style={{
                  color: '#f5f7fb',
                  textTransform: 'none',
                  backgroundColor: '#1ab1c4'
                }}
                variant='contained'
              >
                Create
              </Button>
            </div>
          </form>
        </div>
      </Modal>
    </React.Fragment>
  )
}

export default RoomType
