import React from 'react'
import MaintenanceTable from '../Helper/Table'
import FacilityGroupingService from '../../../../../Services/Api/FacilityGrouping/FacilityGrouping'
import AddFacilityGroup from './AddGroup'
import { useSelector } from 'react-redux'
import EditFacilityName from '../FacilityGroupingMaintenance2/EditFacilityName'

const breadcrumbs = {
  Medical: {
    text: 'MEDICAL COMPLIANCE ASSISTANT MANAGER',
    to: '/'
  },
  Maintenance: {
    text: 'MAINTENANCE TABLE',
    to: '/'
  },
  Table: {
    text: 'FACILITY GROUPING MAINTENANCE',
    to: '/FacilityGroupingMaintenance'
  }
}

const FacilityGrouping = props => {
  const stateValue = useSelector(state => state.MaintenanceTable)

  return (
    <React.Fragment>
      <AddFacilityGroup stateValue={stateValue} />
      <EditFacilityName stateValue={stateValue} />
      <MaintenanceTable
        getData={(data, token) => {
          const args = { ...data }
          return FacilityGroupingService.getFacilityGroups(args, token)
        }}
        updateData={(data, token) => {
          const args = { ...data }
          return FacilityGroupingService.updateFacilityGroup(args)
        }}

        tableHeaderProps={[
          { name: 'Group Code', id: 'code', inputFields: true, sortable: true, editable: false, size: '90%', inputType: 'none', type: 'text', multiline: false },
          { name: 'Group Description', id: 'desc', inputFields: true, sortable: true, editable: true, size: '100%', inputType: 'input', type: 'text', multiline: true },
          { name: 'Status', id: 'status', inputFields: false, sortable: false, editable: true, size: '80%', inputType: 'dropDown', type: 'text', multiline: false },
          { name: 'Facilities Included in the Group', id: 'facilityNames', inputFields: false, sortable: false, editable: true, size: '100%', inputType: 'input', type: 'text', multiline: true, customInput: true }
        ]}
        open={props.open}
        setOpen={props.setOpen}
        headerTitle={'Facility Grouping Maintenance'}
        breadcrumbs={breadcrumbs}
        addButtonTitle={'Add Group'}
        updateButtonTitle={'Update'}
        paginationlabel={'Facility Groupings'}
      />

    </React.Fragment>
  )
}

export default FacilityGrouping
