import React from 'react'
import DialogContent from '@material-ui/core/DialogContent'
import classes from '../scss/TableDisplay.module.scss'
import DialogActions from '@material-ui/core/DialogActions'
import Button from '@material-ui/core/Button'
import Dialog from '@material-ui/core/Dialog'

import { CSVLink } from 'react-csv'
import InfiniteScroll from 'react-infinite-scroll-component'
import TablePreview from './TablePreview'

const PreviewDownload = (props) => {
  const { openPreview, setOpenPreview, headerColumn = [], filename, type = 'HOSPITAL', mongoSelect = {}, filter, infiniteScrollService, downloadFunc, ahmopiType } = props
  const [csvDownload, setCsvDownload] = React.useState([])
  const [tablePreviewData, setTablePreviewData] = React.useState([])
  const [hasMore, setHasMore] = React.useState(true)
  const [totalCount, setTotalCounts] = React.useState(null)
  const [{ offset, limit }, setInfiniteScroll] = React.useState({
    offset: 0,
    limit: 15
  })

  React.useEffect(() => {
    csvFormat()
  }, [tablePreviewData])

  React.useEffect(() => {
    if (typeof infiniteScrollService === 'function') {
      infiniteScrollService(setTablePreviewData, tablePreviewData, 0, 15, setTotalCounts, filter, type, ahmopiType)
    }
  }, [])

  const csvFormat = async () => {
    try {
      if (typeof downloadFunc === 'function') {
        const { data: { data } } = await downloadFunc({ select: mongoSelect, filter: JSON.stringify(filter), type, ahmopiType })
        setCsvDownload(data)
      }
    } catch (error) {
      console.log('error sa download', error)
    }
  }

  const fetch = async () => {
    if (tablePreviewData.length >= totalCount || 0) {
      setHasMore(false)
      return
    }
    setInfiniteScroll({ offset: offset + limit, limit })
    await infiniteScrollService(setTablePreviewData, tablePreviewData, offset + limit, limit, setTotalCounts, filter, type, ahmopiType)
  }

  return (
    <Dialog fullWidth maxWidth={'xl'} open={openPreview} onClose={() => { setOpenPreview(false) }} aria-labelledby='dialogTitle'>
      <DialogContent style={{ minLength: 400 }}>
        <InfiniteScroll
          dataLength={tablePreviewData.length}
          next={fetch}
          hasMore={hasMore}
          loader={tablePreviewData.length <= 7 ? '' : <h4>Loading...</h4>}
          height={550}
          endMessage={
            <p style={{ textAlign: 'center' }}>
              <b>You have seen it all</b>
            </p>
          }
        >
          <TablePreview headers={headerColumn} dataRow={tablePreviewData} />
        </InfiniteScroll>
      </DialogContent>
      <DialogActions className={classes.DialogActions}>
        <Button
          onClick={() => {
            setOpenPreview(false)
          }}
          variant='contained'
          className={classes.Cancel}
        >
          Close
        </Button>
        <CSVLink data={csvDownload} header={headerColumn} filename={filename} onClick={() => setOpenPreview(false)} style={{ textDecoration: 'none' }}>
          <Button variant='contained' className={classes.Add}>
            Download
          </Button>
        </CSVLink>
      </DialogActions>
    </Dialog>
  )
}

export default PreviewDownload
