import React from 'react'
import Table from './TableData'
import orderBy from 'lodash/orderBy'

const AffiliationTable = props => {
  const { dataheader, dataValue, handleRemove, isSubmitted, hasError } = props

  const [data, setData] = React.useState(dataValue)
  const [isSubmiteStatus, setIsSubmitted] = React.useState(false)
  React.useEffect(() => {
    setData(data)
  }, [data])

  React.useEffect(() => {
    if (isSubmitted) {
      setIsSubmitted(isSubmitted)
    }
  }, [isSubmitted])

  return (
    <React.Fragment>
      <div style={{display: 'flex', flexDirection: 'column'}}>
        <label style={{ fontWeight: '700', marginBottom: '1.5em', marginLeft: '0.5em' }}>Facilities Included in the Group</label>
        <div style={{ display: 'flex', width: '100%', height: '280px' }} >
          <Table
            handleRemove={handleRemove}
            data={orderBy(dataValue)}
            header={dataheader}
            isSubmitted={isSubmiteStatus}
            hasError={hasError}
          />
        </div>
      </div>
    </React.Fragment>
  )
}

export default React.memo(AffiliationTable)
