import React from 'react'
import { Table, TableContainer, TableBody, TableHead, Button, FormControl, Select, MenuItem } from '@material-ui/core'
import DataComponents from '../DataComponents'
import GetHeader from '../HeaderComponents'
import HeaderWithBreadCrumbsNoStepper from '../../../../../Components/HeaderWithBreadCrumbsNoStepper/HeaderWithBreadCrumbsNoStepper'
import { BreadcrumbsService } from 'SharedComponents/BreadcrumbsComponent/BreadcrumbsComponent'
import classes from './index.module.scss'
import { useDispatch, useSelector } from 'react-redux'
import useAsyncHook from '../../../../../CustomHooks/useAsyncHook2'
import { getNestedObjects, getNestedArrayOfObjects } from '../../../../../Components/Utils/utils'
import {
  SET_INITIAL_VALUE,
  SET_DATA, SET_PAGE_DATA,
  SET_SORT, SET_FILTER,
  SET_IS_EDIT,
  SET_UPDATE,
  SET_SAVE,
  SET_SUCCESS,
  SET_OPEN_MODAL
} from '../../../../../../Store/actions/Partner/MaintenanceTable'
import Pagination from '@material-ui/lab/Pagination'
import DialogComponents from '../DialogButtons'
import useDebounce from '../../../../../CustomHooks/useDebounce'

const MaintenanceTable = props => {
  const {
    tableHeaderProps = [],
    addButtonTitle,
    updateButtonTitle,
    headerTitle,
    breadcrumbs,
    getData = null,
    updateData = null,
    paginationlabel = ''
  } = props

  const stateValue = useSelector(state => state.MaintenanceTable)
  const dispatch = useDispatch()
  const filterDebounce = useDebounce(stateValue.filter, 500)

  const [dataSave, errorGetSave, loadingGetSave, executeAsyncSave] = useAsyncHook(updateData)
  const [dataGet, errorGet, loadingGet, executeAsyncGet] = useAsyncHook(getData)

  React.useEffect(() => {
    BreadcrumbsService.setItems(breadcrumbs)
    const BreadItems = Object.keys(breadcrumbs).map(items => {
      return items
    })
    BreadcrumbsService.setSelectedItemsWithoutHome(BreadItems)
  }, [breadcrumbs])

  React.useEffect(() => {
    fetchData()
    dispatch(SET_OPEN_MODAL(false))
    dispatch(SET_FILTER({}))

    return () => {
      dispatch(SET_OPEN_MODAL(false))
      dispatch(SET_FILTER({}))
    }
  }, [])

  const fetchData = async () => {
    await executeAsyncGet({
      offset: (stateValue.page - 1) * stateValue.limit,
      limit: stateValue.limit,
      filter: stateValue.filter
    })
  }

  React.useMemo(() => {
    const newData = getNestedObjects(dataGet, ['data'])
    const count = getNestedObjects(newData, ['count'])
    const limit = getNestedObjects(newData, ['limit'])
    const skip = getNestedObjects(newData, ['skip'])
    const data = getNestedArrayOfObjects(newData, ['results'])

    if (loadingGet) {
      dispatch(SET_INITIAL_VALUE(data))
      dispatch(SET_DATA(data))
      dispatch(SET_PAGE_DATA({
        limit: limit,
        count: Math.ceil(count / limit),
        skip: skip,
        page: stateValue.page,
        totalItems: count
      }))
    }

    return () => {
      dispatch(SET_FILTER({}))
    }
  }, [dataGet])

  React.useEffect(() => {
    fetchData()
  }, [ stateValue.limit, stateValue.skip, stateValue.page, filterDebounce, stateValue.newData, stateValue.isEdit ])

  const saveData = async () => {
    await executeAsyncSave({
      data: stateValue.dataToSave
    })
  }

  React.useEffect(() => {
    if (loadingGetSave) {
      fetchData()
      dispatch(SET_SAVE(false))
      dispatch(SET_IS_EDIT(false))
      dispatch(SET_SUCCESS(true))
    }
  }, [dataSave])

  console.log('stateValuesssss', stateValue)

  const GetKeys = () => {
    return tableHeaderProps.map(items => {
      return items
    })
  }

  return (
    <React.Fragment>
      <div id='root-table' className={classes.root}>
        <HeaderWithBreadCrumbsNoStepper id='HeaderWithBreadCrumbsNoStpper' HeaderTitle={headerTitle} />
        <div id='button-div' className={classes.buttons}>
          <div id='add-button' className={classes.addButtonDiv}>
            {!stateValue.isEdit && <Button variant='contained'
              onClick={() => dispatch(SET_OPEN_MODAL(true))}
              className={classes.add}
            >{addButtonTitle} </Button> }
          </div>
          <div id='update-div' className={classes.updateButtonDiv}>
            <Button variant='contained'
              onClick={() => dispatch(SET_IS_EDIT(true))}
              className={classes.update}>{updateButtonTitle} </Button>
          </div>

        </div>
        <TableContainer id='table-root' className={classes.tableRoot}>
          <Table id='tableWrapper' className={classes.tableWrapper}>
            <TableHead id='tableHead' className={classes.tableHead}>
              {
                <GetHeader
                  tableHeaderProps={tableHeaderProps}
                  dispatch={dispatch}
                  setSort={SET_SORT}
                  loadingGet={loadingGet}
                  setfilter={SET_FILTER}
                />
              }
            </TableHead>
            <TableBody id='tableHead' className={classes.tableBody}>
              <React.Fragment>
                {
                  <DataComponents
                    tableDataProps={stateValue.data}
                    GetKeys={GetKeys}
                    dispatch={dispatch}
                    setUpdate={SET_UPDATE}
                    stateValue={stateValue}
                  />
                }
              </React.Fragment>
            </TableBody>
          </Table>
        </TableContainer>
        <div id='pagination' className={classes.pagination}>
          <div id='tableLabel' className={classes.tableLabel}>
            <span>
              {stateValue.data.length} - {stateValue.limit} out of{' '}
              {stateValue.totalItems}  <strong>{paginationlabel}</strong>
            </span>
          </div>
          <div className={classes.options}>

            <span id='showText' className={classes.showText}>
              <p>Show</p>
            </span>
            <FormControl size='small' variant='outlined'>
              <Select
                style={{ backgroundColor: 'white' }}
                value={stateValue.limit}
                onChange={e => dispatch(SET_PAGE_DATA({ page: stateValue.page, limit: Number(e.target.value) }))}
              >
                <MenuItem value={10}>10</MenuItem>
                <MenuItem value={15}>15</MenuItem>
                <MenuItem value={20}>20</MenuItem>
              </Select>
            </FormControl>
            <Pagination
              count={stateValue.count}
              page={stateValue.page}
              onChange={(e, value) => dispatch(SET_PAGE_DATA({ page: value, skip: value * stateValue.limit, limit: stateValue.limit, count: stateValue.count }))}
              showFirstButton
              showLastButton
            />
          </div>
        </div>
        <DialogComponents
          openButton={stateValue.isEdit}
          saveData={saveData}
          stateValue={stateValue}
        />
      </div>

    </React.Fragment>
  )
}

export default React.memo(MaintenanceTable)
