import React from 'react'

import Button from '@material-ui/core/Button'

// Components
import { BreadcrumbsService } from 'SharedComponents/BreadcrumbsComponent/BreadcrumbsComponent'
import HeaderWithBreadCrumbs from '../../../Components/HeaderWithBreadCrumbs/HeaderWithBreadCrumbs'
import FacilityInformation from '../Components/FacilityInformation/FacilityInformation'
import UploadFileTable from '../../../AffiliationFormEncoder/Pages/HospitalAffiliation/components/UploadFIleTable/UploadFIleTable'
import CustomDialogApprove from '../Components/CustomDialogApprove/CustomDialogApprove'
import DisapproveConfirmationDialog from '../Components/DisapproveDialog'
// Constant
import cntsPartnerType from 'Pages/Protected/Partner/Constants/partnerTypes'
// Styles
import classes from './HospitalAffiliation.module.scss'

import HospitalAffiliationContext from './context/HospitalAffiliationContext'
import AffiliationApproverServices from 'Services/Api/Partner/AffiliationApproverServices'
import FileViewer from '../../../Components/FileViewer/FileViewer'
// Constant
import cnts from 'Pages/Protected/Partner/Constants/stage'

const breadcrumbs = {
  Medical: {
    text: 'MEDICAL DATA SUPPORT',
    to: '/'
  },
  MedicalPartner: {
    text: 'MEDICAL PARTNER  AFFILIATION',
    to: '/'
  },
  Hospital: {
    text: 'HOSPITAL AFFILIATION',
    to: '/'
  }
}

const HospitalAffiliation = props => {
  let stepDetails = ['Fill-Out Form', 'Upload Documents', 'Submit']
  let headerTitle = 'Hospital Affiliation Form'
  BreadcrumbsService.setItems(breadcrumbs)
  const { paramId, isApproved, setIsApproved } = React.useContext(HospitalAffiliationContext)
  BreadcrumbsService.setSelectedItemsWithoutHome(['Medical', 'MedicalPartner', 'Hospital'])
  const [open, setOpen] = React.useState(false)
  const [isApprove, setIsApprove] = React.useState(false)
  const [onClickPreview, setOnClickPreview] = React.useState(false)
  const [previewData, setPreviewData] = React.useState({})

  const approveAffiliation = async () => {
    try {
      let data = {
        stageName: cnts.PARTNER.STAGE.PROFILE_GENERATED,
        dateSubmitted: new Date().toISOString(),
        newId: 'HSP' + Math.floor(Math.random() * 10000), // TODO: Generation should be in the backend
        type: cntsPartnerType.PARTNER.TYPE.HOSPITAL
      }

      const res = await AffiliationApproverServices.approveAffiliation(paramId, data)
      setIsApprove(false)
      setIsApproved(true)
      console.log('res posnse data', res)
      // props.history.push('/affiliation-approver')
    } catch (error) {
      console.log('error', error)
    }
  }

  const disapproved = async () => {
    try {
      const res = await AffiliationApproverServices.disapproved(paramId, cntsPartnerType.PARTNER.TYPE.HOSPITAL)
      props.history.push('/affiliation-approver')
      console.log(res)
    } catch (error) {
      console.log('error', error)
    }
  }

  const handleOnPreview = (event, file) => {
    event.preventDefault()
    setPreviewData({
      type: file.mimetype,
      file: file.preview
    })
    setOnClickPreview(true)
  }

  return (
    <React.Fragment>
      <CustomDialogApprove id='aa_custom_dialog' isApprove={isApprove} setIsApprove={() => setIsApprove(false)}>
        <div id='aa_custom_dialog_container' className={classes.dialogContainer}>
          <h3 id='aa_custom_dialog_message' style={{ textAlign: 'center' }}>
            Are you sure you want to approve the Hospital Affiliation form?
          </h3>
          <div id='aa_custom_dialog_btn_group' className={classes.btnGroup}>
            <Button
              onClick={() => setIsApprove(false)}
              id='aa_custom_dialog_btn_cancel'
              className={classes.btnCancel}
              variant='contained'
            >
              Cancel
            </Button>
            <Button
              id='aa_custom_dialog_btn_approve'
              className={classes.btnApprove}
              variant='contained'
              onClick={approveAffiliation}
            >
              Approve
            </Button>
          </div>
        </div>
      </CustomDialogApprove>
      <div id='root' className={classes.root}>
        {onClickPreview && (
          <FileViewer
            open={onClickPreview}
            setOpen={setOnClickPreview}
            type={previewData.type}
            file={previewData.file}
          />
        )}
        <HeaderWithBreadCrumbs
          id='HeaderWithBreadCrumbs'
          HeaderTitle={headerTitle}
          stepDetails={stepDetails}
          currentStep={2}
        />
        <div id='contentWrapper' className={classes.contentWrapper}>
          <FacilityInformation
            titleText={'Hospital Profile'}
            uploadFileTable={
              <UploadFileTable
                onPreviewFile={handleOnPreview}
                buttonText={'Upload'}
                affiliationTypeId={paramId}
                forView
              />
            }
          />
        </div>
        <div className={classes.FooterButtonContainer}>
          <span className={classes.FooterButtonWrapper}>
            <div>
              <Button
                variant='contained'
                color='secondary'
                className={classes.disapproveButton}
                onClick={() => setOpen(true)}
                disabled={isApproved}
                style={{ backgroundColor: isApproved ? '#dddfe3' : '' }}
              >
                Disapprove
              </Button>
            </div>
            <div />
            <div>
              <div>
                <Button variant='contained' className={classes.returnButton} onClick={() => props.setPage(false)}>
                  Return
                </Button>
              </div>
              <div>
                <Button
                  variant='contained'
                  disabled={isApproved}
                  style={{ backgroundColor: isApproved ? '#dddfe3' : '' }}
                  className={classes.approveButton}
                  onClick={() => setIsApprove(true)}
                >
                  Approve
                </Button>
              </div>
            </div>
          </span>
          <div
            style={{ float: 'right', marginBottom: '1%', marginRight: '1%', fontWeight: 'bold', position: 'sticky' }}
          >
            Facility Affiliation Form 2 out of 2
          </div>
        </div>
      </div>
      <DisapproveConfirmationDialog setOpen={setOpen} open={open} handleDisapproved={disapproved} />
    </React.Fragment>
  )
}

export default HospitalAffiliation
