import React from 'react'
import Button from '@material-ui/core/Button'
import classes from './index.module.scss'
import MedicalDentistInformation from './MedicalDentistInformation/PartnerInformation/PartnerInformation'
import UpdateMedicalDentistInformation from './MedicalDentistInformation/EditPartnerInformation/EditPartnerInformation'
import DentistProfileContext from './context'
import DentistParamsContext from '../Context/DentistParamsContext'
import { useParams } from 'react-router-dom'
import ProfileService from '../../../../../../../Services/Api/Partner/ProfileService'
import { useDispatch } from 'react-redux'
import { setListOfAffiliatedContacts } from '../../../../../Store/actions/Partner/listOfAffiliatedContacts'
import { getDoctorSpecialization, getNestedObjects, getNestedArrayOfObjects } from 'Pages/Protected/Partner/Components/Utils/utils'
import { setDoctorSpecialization } from 'Pages/Protected/Store/actions/Partner/autoSuggestAction'
import useAsyncHook from '../../../../CustomHooks/useAsyncHook2'
import { SetData } from './MedicalDentistInformation/Utilities/SetandSave'
import initialState from './MedicalDentistInformation/Utilities/InitialState'

const PartnerMedicalDentistInformation = props => {
  const dispatch = useDispatch()
  const {
    setParamId,
    isToUpdate,
    setIsToUpdate,
    setChangeName,
    isEdit,
    setIsEdit,
    setHeaderData,
    headerData
  } = React.useContext(DentistParamsContext)
  const [profile, setProfile] = React.useState({})
  const { setDentistName, setCurrentStatus, setAfnNumber } = props
  const { dentistId } = useParams()

  const [dataGet, errorGet, loadingGet, executeAsyncGet] = useAsyncHook((dataID, token) => {
    const args = dataID.dentistId
    return ProfileService.getDoctorsById(args, token)
  })

  const [formData, setFormData] = React.useState(initialState)

  React.useEffect(() => {
    setIsToUpdate(false)
  }, [])

  React.useEffect(() => {
    let mounted = true

    if (mounted) {
      fetchAsyncData()
      fetchSpecialization()
      setParamId(dentistId)
    }

    return () => { mounted = false }
  }, [dentistId])

  const fetchAsyncData = async () => {
    await executeAsyncGet({dentistId})
  }

  React.useEffect(() => {
    fetchData()
  }, [dataGet])

  const fetchData = async () => {
    try {
      const newData = getNestedObjects(dataGet, ['data'])

      let contactsArr = []

      const firstName = getNestedObjects(newData, ['name', 'first'])
      const middleName = getNestedObjects(newData, ['name', 'middle'])
      const lastName = getNestedObjects(newData, ['name', 'last'])
      const suffix = getNestedObjects(newData, ['name', 'suffix'])
      const status = getNestedObjects(newData, ['status'])

      let name = `${lastName}, ${firstName} ${middleName} ${suffix}`
      let fullName = `${firstName} ${middleName} ${lastName} ${suffix}`

      let obj = {}
      obj.cDesignation = ''
      obj.cNumber = getNestedObjects(newData, ['contact_number'])
      obj.cName = getNestedObjects(newData, ['full_name'])
      obj.cEmail = getNestedObjects(newData, ['email_address'])
      contactsArr.push(obj)

      const callFlow = getNestedObjects(newData, ['callFlow'])
      const contacts = getNestedArrayOfObjects(newData, ['contacts']) || contactsArr

      const contactObj = { callFlow, contacts }
      const newHeaderData = { ...headerData }
      newHeaderData['name'] = name
      newHeaderData['fullName'] = fullName

      setAfnNumber(newData.afn_number)
      dispatch(setListOfAffiliatedContacts(contactObj))
      setDentistName(fullName)
      const dateNow = Date.parse(Date())
      const dissaffiliationDate = getNestedObjects(newData, ['disaffiliation', 'date'])
      const dateOfDissaAffiliation = Date.parse(dissaffiliationDate)

      if (dateNow >= dateOfDissaAffiliation && dissaffiliationDate.length > 0 && dissaffiliationDate !== '1970-01-01T00:00:07.000Z') {
        const newDataValue = { ...newData }
        const result = SetData(formData, setFormData, newDataValue)
        await ProfileService.updateDoctorProfile(dentistId, result)
        newDataValue['status'] = 'Inactive'
        setCurrentStatus('Inactive')
        setProfile(newDataValue)
      } else {
        setCurrentStatus(status)
        setProfile(newData)
      }

      setHeaderData(newHeaderData)
    } catch (e) {
      console.log('error', e)
    }
  }

  const fetchSpecialization = async () => {
    try {
      let data = await getDoctorSpecialization()
      dispatch(setDoctorSpecialization(data))
    } catch (e) {
      console.log('error', e)
    }
  }

  return (
    <React.Fragment>
      <div id='editButton' className={classes.editButton}>
        <div id='buttonContainer'>
          <Button variant='contained' onClick={() => setIsToUpdate(true)} className={classes.buttonContainer}>
            <div id='insideText' className={classes.insideText}>
              <label>Edit Partner</label>
              <label>Information</label>
            </div>
          </Button>
        </div>
      </div>
      <DentistProfileContext.Provider
        value={{
          profile,
          setIsToUpdate,
          setProfile,
          setChangeName,
          isEdit,
          setIsEdit,
          setHeaderData,
          headerData,
          setCurrentStatus
        }}
      >
        {!isToUpdate && <MedicalDentistInformation />}
        {isToUpdate && <UpdateMedicalDentistInformation />}
      </DentistProfileContext.Provider>
    </React.Fragment>
  )
}

export default PartnerMedicalDentistInformation
