import React from 'react'
import Button from '@material-ui/core/Button'
import classes from './index.module.scss'
import CustomDialog from '../../../../../Components/CustomDialog/CustomDialog'
import CustomDialogSave from '../../../../../Components/CustomDialogSave/CustomDialogSave'
import CustomAlerSnackBar from '../../../../../Components/CustomSnackBar/CustomAlertSnackbar'
import { useDispatch } from 'react-redux'
import {
  SET_IS_EDIT,
  SET_SAVE,
  SET_CANCEL,
  SET_SUCCESS,
  SET_PREV_COPY
} from '../../../../../../Store/actions/Partner/MaintenanceTable.js'

const FooterButton = props => {
  const dispatch = useDispatch()
  const { openButton } = props

  const HandleCancel = () => {
    dispatch(SET_PREV_COPY())
    dispatch(SET_CANCEL(true))
  }

  return (
    <React.Fragment>
      {openButton &&
      <div className={classes.FooterButtonContainer}>
        <span className={classes.FooterButtonWrapper}>
          <div>
            <Button
              onClick={HandleCancel}
              variant='contained'
              color='secondary'
              className={classes.cancelButton}
            >
              Cancel
            </Button>
          </div>
          <div />
          <div>
            <div>
              <Button variant='contained' type='submit' className={classes.SaveBtn} onClick={() => dispatch(SET_SAVE(true))}>
              Save
              </Button>
            </div>
          </div>
        </span>
      </div>
      }
    </React.Fragment>

  )
}

const CancelDialog = props => {
  const dispatch = useDispatch()
  const { stateValue } = props

  return (
    <div>
      <CustomDialog id='cancelCustomDialog' isCancel={stateValue.isCancel} setIsCancel={() => dispatch(SET_CANCEL(false))}>
        <div id='cancelCustomDialogContainer' className={classes.dialogContainer}>
          <h3 id='cancelCustomDialogMessage'>All changes will not be saved. Are you sure you want to cancel?</h3>
          <div id='cancelCustomDialogBtnGroup' className={classes.btnGroup}>
            <Button
              onClick={() => dispatch(SET_CANCEL(false))}
              id='cancelCustomDialogBtnNo'
              className={classes.btnCancel}
              variant='contained'
              style={{ backgroundColor: '#fd5451', color: 'white', marginLeft: '160px', marginBottom: '10px' }}
            >
                No
            </Button>
            <Button
              onClick={() => {
                dispatch(SET_CANCEL(false))
                dispatch(SET_IS_EDIT(false))
              }}
              id='cancelCustomDialogBtnYes'
              variant='contained'
              style={{ backgroundColor: '#48b77c', color: 'white', marginLeft: '20px', marginBottom: '10px' }}
            >
                Yes
            </Button>
          </div>
        </div>
      </CustomDialog>
    </div>
  )
}

const UpdateDialog = props => {
  const { stateValue } = props
  const dispatch = useDispatch()
  // const stateValue = useSelector(state => state.MaintenanceTable)

  const { saveData } = props
  return (
    <div>
      <CustomDialogSave
        onClose={() => {
          dispatch(SET_SAVE(false))
        }}
        id='saveCustomDialog'
        isSave={stateValue.isSave}
      >
        <div id='saveCustomDialogContainer' className={classes.dialogContainer}>
          <h3 id='saveCustomDialogMessage'>Are you sure you want to update?</h3>
          <div id='saveCustomDialogBtnGroup' className={classes.btnGroup}>
            <Button
              onClick={() => dispatch(SET_SAVE(false))}
              id='saveCustomDialogBtnNo'
              variant='contained'
              style={{ backgroundColor: '#fd5451', color: 'white', marginLeft: '55px', marginBottom: '10px' }}
            >
                No
            </Button>
            <Button
              onClick={saveData}
              id='saveCustomDialogBtnYes'
              className={classes.btnCancel}
              variant='contained'
              style={{ backgroundColor: '#48b77c', color: 'white', marginLeft: '20px', marginBottom: '10px' }}
            >
                Yes
            </Button>
          </div>
        </div>
      </CustomDialogSave>
      <CustomAlerSnackBar isSave={stateValue.success} setIsSave={() => dispatch(SET_SUCCESS(false))} />
    </div>
  )
}

const DialogComponents = props => {
  return (
    <React.Fragment>
      <FooterButton {...props} />
      <CancelDialog {...props} />
      <UpdateDialog {...props} />
    </React.Fragment>
  )
}

export default DialogComponents
