import React, { useState, useMemo, useEffect, Fragment } from 'react'
import { useParams } from 'react-router-dom'
import { useSelector, useDispatch } from 'react-redux'

import PartnerDoctorService from 'Services/Api/Partner/PartnerDoctorService'

import FooterWithButtons from 'Pages/Protected/Partner/Components/DynamicFooterWithButton/FooterWithButtons'
import ProgressComponent from 'Pages/Protected/Partner/AffiliationFormEncoder/Components/ProgressComponent/ProgressComponent'
import AffiliationForm from '../../Components/AffiliationForm/AffiliationForm'

import { setAffiliationType } from 'Pages/Protected/Store/actions/affiliationType'
import classes from './doctorPageOne.module.scss'
import { setDoctorAffiliation } from 'Pages/Protected/Store/actions'
import getDoctor from '../../Components/Utilities/doctorEncoderSrvc'

let pathUrl = '/doctor-affiliation-requirements'
let affilConstValue = {
  stageName: 'ENCODING',
  status: 'Active',
  source: 'PNM',
  affiliationStatus: 'In-Progress',
  dateSubmitted: new Date().toISOString()
}

function DoctorPageOne ({ history }) {
  const { id } = useParams()
  const dispatch = useDispatch()

  const { affiliationType } = useSelector(state => state.affiliationType)

  const [isSuccess, setIsSuccess] = useState(false)
  const [affilID, setAffilID] = React.useState(null)
  const [isSubmitted, setIsSubmitted] = useState(false)
  const [isValid, setValid] = useState(false)
  const [isTovalidate, setIsToValidate] = useState(false)
  const [loading, setIsloading] = useState(false)
  const [form, setForm] = useState({})

  const [{ affiliationInfo, sectionOne, sectionTwo, statusForm, remarks }, setFetchData] = useState({
    sectionOne: {},
    sectionTwo: {},
    statusForm: {},
    affiliationInfo: {},
    remarks: {}
  })

  useEffect(() => {
    if (isTovalidate) {
      setTimeout(() => {
        setIsToValidate(false) // reset validation trigger
      }, 200)
    }
  }, [isTovalidate])

  useEffect(() => {
    if (id || affiliationType.id) {
      const paramId = id || affiliationType.id
      fetch(paramId)
    }
    if (affiliationType.hasOwnProperty('id')) {
      setAffilID(affiliationType['id'])
    }
    setSubmitted(affiliationType.stageName)
  }, [])

  const disabledButton = useMemo(() => {
    if (isValid) {
      return false
    } else {
      return true
    }
  }, [isValid])

  const handleSubmit = e => {
    e.preventDefault()
    if (!isSubmitted) {
      setIsToValidate(true)
      if (isValid) {
        save()
      }
    }
  }

  const handleResetForm = () => {
    console.log('handleResetForm')
  }

  const fetch = async id => {
    setIsloading(true)
    try {
      const { sectionOne, sectionTwo, statusForm, affiliationInfos, remarksForm, affil } = await getDoctor(id, true)
      setFetchData({
        sectionOne,
        sectionTwo,
        remarks: remarksForm,
        statusForm,
        affiliationInfo: affiliationInfos
      })
      dispatch(setAffiliationType(affil))
      if (affil['stageName'] === 'FOR_AFFILIATION_APPROVAL' || affil['affiliationStatus'] === 'Submitted') {
        setIsSubmitted(true)
      }
      dispatch(setDoctorAffiliation({...sectionOne, ...sectionTwo, ...statusForm, ...affiliationInfos, ...remarksForm}))
      setAffilID(affil.id)
      setIsloading(false)
    } catch (error) {
      console.log('error in fetching data', error)
      setIsloading(false)
    }
  }

  const save = async () => {
    let affil = Object.assign(affiliationType, affilConstValue)
    if (!affilID) {
      store(affil)
    } else {
      update(affil)
    }
  }

  const setSubmitted = stageName => {
    if (stageName === 'AFFILIATION_APPROVAL_FOR_OIC') {
      setIsSubmitted(true)
    }
  }

  const store = async affil => {
    try {
      const { data: { data: { id } } } = await PartnerDoctorService.createdPartnerDoctor({ ...affil, ...form })
      affiliationType.id = id
      setAffilID(id)
      dispatch(setAffiliationType(affil))
      dispatch(setDoctorAffiliation(form))
    } catch (error) {
      console.log('error in create doctor', error)
    }
    setIsSuccess(true)
  }

  const update = async affil => {
    const paramId = id || affiliationType.id
    try {
      let { data: { data } } = await PartnerDoctorService.updatePartnerDoctor(paramId, { ...affil, ...form })
      setIsSuccess(true)
    } catch (error) {
      console.log('update error', error)
    }
  }

  if (loading) return <ProgressComponent />
  return (
    <Fragment>
      <AffiliationForm
        history={history}
        setValid={setValid}
        isTovalidate={isTovalidate}
        setForm={setForm}
        isSubmitted={isSubmitted}
        affiliationType={affiliationType}
        sectionOneData={sectionOne}
        sectionTwoData={sectionTwo}
        statusData={statusForm}
        remarksData={remarks}
        affilInfoData={affiliationInfo}
      />
      <div className={classes.footerContainer}>
        <FooterWithButtons
          footerStatus={'Doctor'}
          onClickSave={handleSubmit}
          disableButton={isSubmitted ? false : disabledButton}
          isSuccess={isSuccess}
          setIsSuccess={setIsSuccess}
          isSubmitted={isSubmitted}
          handleReset={handleResetForm}
          pathUrl={pathUrl} // use for proceed button
          save={save}
          history={history}
        />
      </div>
    </Fragment>
  )
}

export default DoctorPageOne
