import React from 'react'
import DropDown from '../InputFields/DropDown'
import classes from './index.module.scss'
import { NonVatList } from '../../../Constants/dropDownItems'

const TinComponents = props => {
  const { error = '', name = '', items = [], onChange, value = '', nonVatValue = '', nonVatError = '', nonVatOnChange } = props

  // console.log('tin status value', value)
  // console.log('NonVatList', NonVatList)
  console.log('nonVatValue', nonVatValue)

  const TinStatusRender = () => {
    return value === 'Non-VATable'
      ? <div id='root' className={classes.root}>
        <div id='tin-value' className={classes.tinStatus}>
          <DropDown
            items={items}
            error={error}
            name={name}
            onChange={onChange}
            value={value}
          />
        </div>
        <div id='non-vat-value' className={classes.nonVatValue}>
          <DropDown
            items={NonVatList}
            error={nonVatError}
            name={'non_vatable'}
            onChange={nonVatOnChange}
            value={nonVatValue}
          />
        </div>
      </div>
      : <DropDown
        items={items}
        error={error}
        name={name}
        onChange={onChange}
        value={value}

      />
  }

  return (
    <React.Fragment>
      <TinStatusRender />
    </React.Fragment>
  )
}

export default React.memo(TinComponents, (prevProps, nextProps) => {
  try {
    if (prevProps.error === nextProps.error &&
            prevProps.name === nextProps.name &&
            prevProps.items === nextProps.items &&
            prevProps.onChange === nextProps.onChange &&
            prevProps.value === nextProps.value &&
            prevProps.nonVatValue === nextProps.nonVatValue &&
            prevProps.nonVatError === nextProps.nonVatError &&
            prevProps.nonVatOnChange === nextProps.nonVatOnChange) {
      return true
    }
    return false
  } catch (error) {
    return false
  }
})
