import React, { useContext } from 'react'
import Button from '@material-ui/core/Button'
import classes from './PartnerInformation.module.scss'
import DoctorInformation from './PartnerInformation'
import { useParams } from 'react-router-dom'
import DoctorProfileParamsContext from '../Context/DoctorProfileParamsContext'
import DoctorProfileContext from './context'
import { useDispatch } from 'react-redux'
import { setListOfAffiliatedContacts } from '../../../../../Store/actions/Partner/listOfAffiliatedContacts'
import EditDoctorInformation from './DoctorInformation/EditDoctorInformation'
import ProfileService from '../../../../../../../Services/Api/Partner/ProfileService'
import { getNestedObjects, getDoctorSpecialization, getNestedArrayOfObjects } from '../../../../Components/Utils/utils'
import { setDoctorSpecialization } from 'Pages/Protected/Store/actions/Partner/autoSuggestAction'
import useAsyncHook from '../../../../CustomHooks/useAsyncHook2'
import { Destructure } from './DoctorInformation/DeStructure'
import initialState from './InputsforEdit/InitialState'

const PartnerDoctorIndex = props => {
  const dispatch = useDispatch()
  const { doctorId } = useParams()
  const { setDoctorName, setCurrentStatus, setAfnNumber, setFacilityInfos } = props

  const {
    setParamId,
    isToUpdate,
    setIsToUpdate,
    setChangeName,
    isEdit,
    setIsEdit,
    setHeaderData,
    headerData
  } = useContext(DoctorProfileParamsContext)

  const [profile, setProfile] = React.useState({})
  const [formData] = React.useState(initialState)

  const [dataGet, errorGet, loadingGet, executeAsyncGet] = useAsyncHook((dataID, token) => {
    const args = dataID.doctorId
    return ProfileService.getDoctorsById(args, token)
  })

  React.useEffect(() => {
    setIsToUpdate(false)
    fetchAsyncData()
    fetchSpecialization()
    setParamId(doctorId)
  }, [doctorId])

  const fetchAsyncData = async () => {
    await executeAsyncGet({doctorId})
  }

  React.useEffect(() => {
    if (loadingGet) {
      fetchData()
    }
  }, [dataGet])

  const fetchData = async () => {
    try {
      const newData = getNestedObjects(dataGet, ['data'])

      let contactsArr = []
      let mainCoordinatorsName = getNestedObjects(newData, ['coordinators', 'main_coordinator_name'])
      let mainCoordinatorsNumber = getNestedObjects(newData, ['coordinators', 'main_contact_num'])
      let assistantCoordinators = getNestedArrayOfObjects(newData, ['coordinators', 'asst_coordinators'])

      assistantCoordinators.forEach(items => {
        let obj = {}
        obj.cDesignation = items ? (items.asstCoordinatorName ? 'Assistant Coordinators' : '') : ''
        obj.cNumber = getNestedObjects(items, ['asstContactNum'])
        obj.cName = getNestedObjects(items, ['asstCoordinatorName'])
        obj.cEmail = ''
        contactsArr.push(obj)
      })

      let mainObj = {}
      mainObj.cDesignation = mainCoordinatorsName ? 'Main Coordinators' : ''
      mainObj.cNumber = mainCoordinatorsNumber
      mainObj.cName = mainCoordinatorsName
      mainObj.cEmail = ''
      contactsArr.push(mainObj)

      let contacts = newData.contacts ? newData.contacts : contactsArr
      let callFlow = newData ? (newData.callFlow ? newData.callFlow : '') : ''

      const lastName = getNestedObjects(newData, ['name', 'last']).toUpperCase()
      const firstName = getNestedObjects(newData, ['name', 'first']).toUpperCase()
      const middleName = getNestedObjects(newData, ['name', 'middle']).toUpperCase()
      const suffix = getNestedObjects(newData, ['name', 'suffix']).toUpperCase()
      const newHeaderData = { ...headerData }

      newHeaderData['name'] = `${lastName}, ${firstName} ${suffix} ${middleName} `
      newHeaderData['fullName'] = `${firstName} ${middleName} ${lastName} ${suffix} `
      setHeaderData(newHeaderData)
      setAfnNumber(newData.afn_number)
      const contactObj = { callFlow, contacts }

      dispatch(setListOfAffiliatedContacts(contactObj))
      setFacilityInfos(newData.facility_info)
      const dateNow = Date.parse(Date())
      const dissaffiliationDate = getNestedObjects(newData, ['disaffiliation', 'date'])
      const dateOfDissaAffiliation = Date.parse(dissaffiliationDate)

      if (dateNow >= dateOfDissaAffiliation && dissaffiliationDate.length > 0 && dissaffiliationDate !== '1970-01-01T00:00:07.000Z') {
        const newDataValue = { ...newData }
        newDataValue['status'] = 'Inactive'
        const newForm = { ...formData }
        const formResult = Destructure(newDataValue, newForm)
        await ProfileService.updateDoctorProfile(doctorId, formResult)
        setProfile(newDataValue)
      } else {
        setProfile(newData)
      }
    } catch (e) {
      console.log('error', e)
    }
  }

  const fetchSpecialization = async () => {
    try {
      let data = await getDoctorSpecialization()
      dispatch(setDoctorSpecialization(data))
    } catch (e) {
      console.log('error', e)
    }
  }

  return (
    <React.Fragment>
      <div id='editButton' className={classes.editButton}>
        <div id='buttonContainer'>
          <Button variant='contained' onClick={() => setIsToUpdate(true)} className={classes.buttonContainer}>
            <div id='insideText' className={classes.insideText}>
              <label> Edit Partner </label>
              <label> Information </label>
            </div>
          </Button>
        </div>
      </div>
      <DoctorProfileContext.Provider
        value={{ profile, setIsToUpdate, setProfile, setChangeName, isEdit, setIsEdit, setHeaderData, headerData }}
      >
        {!isToUpdate && <DoctorInformation setDoctorName={setDoctorName} setCurrentStatus={setCurrentStatus} />}
        {isToUpdate && <EditDoctorInformation profile={profile} />}
      </DoctorProfileContext.Provider>
    </React.Fragment>
  )
}

export default PartnerDoctorIndex
