import React, { useContext } from 'react'

import Button from '@material-ui/core/Button'
import classes from './index.module.scss'
import MedicalClinicInformation from './MedicalClinicInformation/PartnerInformation/PartnerInformation'
import UpdateMedicalClinicInformation from './MedicalClinicInformation/EditPartnerInformation/EditPartnerInformation'
import ClinicProfileContext from './context'
import ClinicProfileParams from '../Context/ClinicProfileParams'
import { useParams } from 'react-router-dom'
import { useDispatch } from 'react-redux'
import { setListOfAffiliatedContacts } from '../../../../../Store/actions/Partner/listOfAffiliatedContacts'
import { getNestedObjects, getNestedArrayOfObjects } from '../../../../Components/Utils/utils'
import ProfileService from '../../../../../../../Services/Api/Partner/ProfileService'
import useAsyncHook from '../../../../CustomHooks/useAsyncHook2'
import { set } from './MedicalClinicInformation/Utilities/setAndSave'
import InitialState from './MedicalClinicInformation/Utilities/InitialState'

const PartnerMedicalClinicInformation = props => {
  const dispatch = useDispatch()
  const { medicalId } = useParams()
  const {
    setParamId,
    isToUpdate,
    setIsToUpdate,
    setChangeName,
    isEdit,
    setIsEdit,
    setHeaderData,
    setStatus,
    setCity
  } = useContext(ClinicProfileParams)
  // const [isToUpdate, setIsToUpdate] = React.useState(false)
  const [profile, setProfile] = React.useState({})
  const [discountTypeData, setDiscountType] = React.useState([])
  const [form] = React.useState(InitialState)

  const [dataGet, errorGet, loadingGet, executeAsyncGet] = useAsyncHook((dataID, token) => {
    const args = dataID.medicalId
    return ProfileService.getMedicalClinic(args, token)
  })

  const [dataGetDiscount, errorGet2, loadingGet2, executeAsyncGetDiscount] = useAsyncHook((dataID, token) => {
    const args = dataID.medicalId
    return ProfileService.getFacilityDiscount2(args, token)
  })

  React.useEffect(() => {
    let mounted = true

    if (mounted) {
      fetchAsyncData()
      setParamId(medicalId)
      setIsToUpdate(false)
    }

    return () => { mounted = false }
  }, [medicalId])

  const fetchAsyncData = async () => {
    await executeAsyncGet({medicalId})
  }

  React.useEffect(() => {
    if (loadingGet) {
      fetchData()
    }
  }, [dataGet])

  const fetchData = async () => {
    try {
      await executeAsyncGetDiscount({medicalId})

      const newData = getNestedObjects(dataGet, ['data'])

      const callFlow = getNestedObjects(newData, ['callFlow'])
      const contacts = getNestedArrayOfObjects(newData, ['contacts'])
      const contactObj = { callFlow, contacts }

      dispatch(setListOfAffiliatedContacts(contactObj))

      const name = getNestedObjects(dataGet, ['data', 'name'])
      const city = getNestedObjects(dataGet, ['data', 'address', 'city'])

      props.ClinicName(name)
      setCity(city)

      const dateNow = Date.parse(Date())
      const dissaffiliationDate = getNestedObjects(newData, ['disaffiliation', 'date'])
      const dateOfDissaAffiliation = Date.parse(dissaffiliationDate)
      const forDissafiliaiton = true

      if (dateNow >= dateOfDissaAffiliation && dissaffiliationDate.length > 0 && dissaffiliationDate !== '1970-01-01T00:00:07.000Z') {
        const newDataValue = { ...newData }
        const result = set(form, newDataValue, forDissafiliaiton)
        ProfileService.updateMedicalClinicProfile(medicalId, result)
        newDataValue['status'] = 'Inactive'
        setStatus('Inactive')
        props.setHeaderData((prevState) => ({
          ...prevState,
          status: 'Inactive',
          name: name }))
        setProfile(newDataValue)
      } else {
        const status = getNestedObjects(dataGet, ['data', 'status'])
        props.setHeaderData((prevState) => ({
          ...prevState,
          status: status.toUpperCase(),
          name: name }))
        setStatus(getNestedObjects(dataGet, ['data', 'status']))
        setProfile(newData)
      }

      setProfile(newData)
    } catch (e) {
      console.log(e)
    }
  }

  React.useEffect(() => {
    if (loadingGet2) {
      fetchDiscount()
    }
  }, [dataGetDiscount])

  const fetchDiscount = () => {
    const discountData = [ ...getNestedObjects(dataGetDiscount, ['data']) ]
    setDiscountType(discountData.map(items => getNestedArrayOfObjects(items, ['discount_type'])))
  }

  const discountType = React.useMemo(() => discountTypeData, [discountTypeData])

  return (
    <React.Fragment>
      <div id='editButton' className={classes.editButton}>
        <div id='buttonContainer'>
          <Button variant='contained' onClick={() => setIsToUpdate(true)} className={classes.buttonContainer}>
            <div id='insideText' className={classes.insideText}>
              <label>Edit Partner</label>
              <label>Information</label>
            </div>
          </Button>
        </div>
      </div>
      <ClinicProfileContext.Provider
        value={{
          profile,
          setIsToUpdate,
          setProfile,
          setChangeName,
          isEdit,
          setIsEdit,
          setHeaderData,
          setStatus,
          setCity,
          discountType
        }}
      >
        {!isToUpdate && <MedicalClinicInformation />}
        {isToUpdate && <UpdateMedicalClinicInformation />}
      </ClinicProfileContext.Provider>
    </React.Fragment>
  )
}

export default React.memo(PartnerMedicalClinicInformation)
