import { useDropzone } from 'react-dropzone'
import Button from '@material-ui/core/Button'
import classes from './PreviewImage.module.scss'
import UploadFileContext from '../../Components/context/fileUploadContext'
import React from 'react'
import { useSelector } from 'react-redux'
import axios from 'axios'
import AffiliationEncoderService from 'Services/Api/Partner/affiliationEncoderService'
import RemoveConfirmationDialog from './RemoveConfirmationDialog'

const API_URL = `${process.env.REACT_APP_SERVER_API}`
const thumbsContainer = {
  display: 'flex',
  flexDirection: 'row',
  flexWrap: 'wrap'
}

const thumb = {
  display: 'inline-flex',
  marginRight: 8
}

const thumbInner = {
  display: 'flex',
  minWidth: 0,
  overflow: 'hidden'
}

const img = {
  display: 'block',
  width: 120,
  height: 100
}

const removeImage = {
  position: 'absolute',
  height: '22px',
  width: '22px',
  backgroundColor: '#fd5451',
  borderRadius: '2px',
  marginLeft: '98px',
  cursor: 'pointer'
}

const Preview = props => {
  const { mainFiles, setMainFiles, buttonText, forView, form, onPreviewFile, setForm, errors } = props
  const { disabled, isClickedSave } = React.useContext(UploadFileContext)
  const [open, setOpen] = React.useState(false)
  const [toRemoveFile, setToRemoveFile] = React.useState({
    file: {},
    index: null,
    fileType: ''
  })
  const { affiliationType } = useSelector(state => state.affiliationType)
  const { acceptedFiles, getRootProps, getInputProps } = useDropzone({
    onDrop: async acceptedFiles => {
      const files = acceptedFiles.map(file =>
        Object.assign(file, {
          preview: URL.createObjectURL(file),
          fileType: props.fileType
        })
      )

      if (form && form.hasOwnProperty(props.fileType)) {
        const updateForm = { ...form }
        updateForm[props.fileType] = updateForm[props.fileType] + acceptedFiles.length
        setForm(updateForm)
      }

      const data = new FormData()
      for (let file of files) {
        data.append('files[]', file, file.name)
      }
      data.append('affiliation_id', affiliationType.id)
      data.append('file_type', props.fileType)
      console.log('Upload Data', affiliationType.id)
      const res = await AffiliationEncoderService.uploadFile(data)
      const resultFiles = res.data.data.files
      setMainFiles(previousState => ({
        ...previousState,
        [props.fileType]: resultFiles.map(file =>
          Object.assign(file, {
            preview: `${API_URL}/uploads/${file.path}`,
            fileType: props.fileType
          })
        )
      }))
    }
  })

  const removeFile = () => {
    const { file, fileType } = toRemoveFile
    const filePath = file.path.replace('/', '-')
    AffiliationEncoderService.deleteUploadFile(`${affiliationType.id}/${fileType}/${filePath}`)
    const newFiles = [...mainFiles[props.fileType]]
    newFiles.splice(newFiles.indexOf(file), 1)
    setMainFiles(previousState => ({
      ...previousState,
      [fileType]: newFiles
    }))

    if (form && form.hasOwnProperty(fileType)) {
      const updateForm = { ...form }
      updateForm[fileType] = updateForm[fileType] - 1
      setForm(updateForm)
    }
  }

  const handleClickRemove = (file, index, fileType) => {
    setOpen(true)
    const nFile = { ...toRemoveFile }
    nFile.file = file
    nFile.index = index
    nFile.fileType = fileType
    console.log('nFile=============', nFile)
    setToRemoveFile(nFile)
  }

  const removeBtn = (file, index, fileType) => {
    if (forView) {
      return null
    }
    return (
      // <button onClick={removeFile(file, index, fileType)} style={removeImage}>
      <button onClick={() => handleClickRemove(file, index, fileType)} style={removeImage}>
        x
      </button>
    )
  }
  let thumbs = null
  const imagePreview = file => {
    let image = ''
    if (file.mimetype.includes('image')) {
      image = <img src={file.preview} style={img} alt={props.fileType} />
    } else if (file.mimetype.includes('pdf')) {
      image = (
        <img
          src={
            'https://encrypted-tbn0.gstatic.com/images?q=tbn%3AANd9GcTrgQxKYUQ9Hr_Rt_aldzqACq5yYYCjRpYbB3h59-9OdWq6zAgB&usqp=CAU'
          }
          style={img}
          alt={props.fileType}
        />
      )
    } else {
      image = <img src={'https://img.icons8.com/cotton/2x/folder-invoices.png'} style={img} alt={props.fileType} />
    }
    return image
  }

  const onClickPreviewImageElectron = async (element, file) => {
    onPreviewFile(element, file)
  }

  if (mainFiles) {
    thumbs = mainFiles[props.fileType]
      ? mainFiles[props.fileType].map((file, index) => (
          <div style={thumb} key={file.name}>
            {!disabled && removeBtn(file, index, props.fileType)}
            <div style={thumbInner}>
              <a
                href={!window.ipcRenderer ? file.preview : ''}
                target={!window.ipcRenderer ? '_blank' : ''}
                onClick={e => onClickPreviewImageElectron(e, file)}
              >
                {imagePreview(file)}
              </a>
            </div>
          </div>
        ))
      : null
  }

  let buttonElement = null
  if (forView) {
    buttonElement = null
  } else {
    buttonElement = (
      <Button disabled={disabled} className={classes.buttonStyle} variant='outlined'>
        <text>{buttonText}</text>
      </Button>
    )
  }

  const hasError = fileType => {
    return errors && errors.hasOwnProperty(fileType) && isClickedSave ? classes.validationStyle : null
  }

  return (
    <div>
      <section style={{ display: 'flex', alignItems: 'flex-end' }}>
        <div className={hasError(props.fileType)}>{props.desc}</div>
        <span style={{ flexGrow: 1 }} />
        <div className={classes.buttonContainer}>
          <div {...getRootProps()}>
            <input {...getInputProps()} disabled={disabled} />
            {buttonElement}
          </div>
        </div>
      </section>
      <aside style={thumbsContainer}>{thumbs}</aside>
      <RemoveConfirmationDialog open={open} setOpen={setOpen} handleRemove={removeFile} />
    </div>
  )
}

export default Preview
