import instance from '../instance'
// import axios from 'axios'
const axios = instance('/p/f')

export default class RoomAndBoardServices {
  static getRoomAndBoard (argParams, argCancelToken) {
    const params = {
      id: argParams.id,
      offset: argParams.offset,
      limit: argParams.limit,
      filter: argParams.filter
    }
    return axios({
      url: `/partners/facilities/${params.id}/room-and-board`,
      method: 'GET',
      headers: { skip_generate_form_data: 1 },
      params,
      cancelToken: argCancelToken.cancelToken
    })
  }

  static saveRoomAndBoard (argParams, argCancelToken) {
    const data = { data: argParams.data }
    return axios({
      url: `/partners/facilities/${argParams.id}/room-and-board`,
      method: 'PUT',
      headers: { skip_generate_form_data: 1 },
      data
    })
  }

  static createRoomAndBoard (argParams, argCancelToken) {
    const data = { data: argParams }
    return axios({
      url: `/partners/facilities/${argParams.id}/room-and-board`,
      method: 'POST',
      headers: { skip_generate_form_data: 1 },
      data
    })
  }
}
