import isEmpty from 'lodash/isEmpty'
import Joi from '@hapi/joi'
import { isAfterDateNow } from '../../../../../Components/DatePicker/DatePickerUtils'

const notRequiredFields = [
  'sub',
  'other',
  'processType',
  'paymentTerms',
  'professionalFee',
  'withTax',
  'dateSubmitted',
  'dateOfAffiliation',
  'dateOfDisaffiliation',
  'ahmopi',
  'exclusiveTo',
  'facility',
  'status',
  'assignments',
  'philExp',
  'id',
  'facilityAssignment',
  'payee',
  'suffix'
]

const schema = Joi.object({
  firstName: Joi.string()
    .label('First name')
    .required(),
  lastName: Joi.string()
    .label('Last name')
    .required(),
  middleName: Joi.string()
    .label('Middle name')
    .required(),
  birthdate: Joi.date().label('Birth Date').required(),
  officeLocation: Joi.string().label('Office Location').required(),
  civilStatus: Joi.string()
    .label('Civil status')
    .required(),
  gender: Joi.string()
    .label('Gender')
    .required(),
  mainSpecialization: Joi.string()
    .label('Main specification')
    .required(),
  majorSpecialization: Joi.string()
    .label('Major Specialization')
    .required(),
  subSpecialization: Joi.string().label('Sub Specialization'),
  otherSpecialization: Joi.string().label('Other Specialization'),
  prcNumber: Joi.number()
    .label('PRC Number')
    .required(),
  prcExpiration: Joi.date()
    .label('PRC Expiration')
    .required(),
  philhealthNumber: Joi.string()
    .label('Philhealth Accreditation No. with Hyphen')
    .max(12)
    .min(12)
    .required(),
  philhealthExpiration: Joi.date().label('Phil Health Expiration Date'),
  pmaNumber: Joi.number().label('PMA Number').allow('').required(),
  stNumber: Joi.number().label('S2 Number').allow('').required(),
  ptrNumber: Joi.number().label('PTR Number').allow('').required(),
  tinNumber: Joi.string()
    .label('Tin Number with Hyphen')
    .max(12)
    .min(12)
    .required(),
  tinStatus: Joi.string()
    .label('Tin Status')
    .required(),
  non_vatable: Joi.string().label('Non-VaTable').required(),
  processType: Joi.string().label('Process Type'),
  paymentTerms: Joi.string().label('Payment of Terms'),
  serviceRate: Joi.string().label('Professional Fee'),
  ahmopi: Joi.string().label('ahmopi'),
  withTax: Joi.number().label('WithTax'),
  vatTaxRate: Joi.number().label('Tax Rate').required(),
  clinicSchedule: Joi.string()
    .label('Clinic Schedule')
    .required(),
  facilityName: Joi.string()
    .label('Medical Facility')
    .required(),
  address: Joi.string()
    .label('Address')
    .required(),
  dateOfAffiliation: Joi.date().label('Date of Affiliation'),
  dateOfDisaffiliation: Joi.date().label('Date of Disaffiliation'),
  reason: Joi.string()
    .label('On Hold Reason')
    .required()
})

export default function DoctorValidation (form) {
  let errors = {}
  const result = schema.validate(form, { abortEarly: false })

  if (result.error && result.error.details.length > 0) {
    for (const i of result.error.details) {
      errors[i.path[0]] = i.message

      if (typeof form['philhealthNumber'] !== 'undefined') {
        if (form['philhealthNumber'].length === 0) {
          delete errors['philhealthNumber']
        } else {
          if (form['philhealthNumber'].length === 12) {
            delete errors['philhealthNumber']
          }
        }
      }

      if (form['tinStatus'] === '12%') {
        delete errors['non_vatable']
      }

      if (form['tinNumber'].length === 12) {
        delete errors['tinNumber']
      }

      if (form['facilityAssignment'] === 'Assigned' && form['assignments'].length <= 0) {
        errors['assignments'] = 'Please select at least 1 Doctor Assignment'
      } else {
        delete errors['assignments']
      }

      if (form['birthdate'].length > 0) {
        if (isAfterDateNow(form['birthdate'])) {
          errors['birthdate'] = 'Invalid Date'
        } else {
          delete errors['birthdate']
        }
      }

      if (form['status'] !== 'On-Hold') {
        delete errors['reason']
      }

      const taxRatePattern = new RegExp(/^(\d{0,2}(\.\d{1,5})?|100(\.00?)?)$/)

      if (form['vatTaxRate'].length > 0) {
        if (!taxRatePattern.test(form['vatTaxRate'])) {
          errors['vatTaxRate'] = 'Tax Rate accepts only 5 decimals'
        } else {
          delete errors['vatTaxRate']
        }
      }

      if (notRequiredFields.includes(i.path[0])) {
        delete errors[i.path[0]]
      }
    }
  }

  console.log('errors', errors)

  return {
    errors,
    isValid: isEmpty(errors)
  }
}
