import React from 'react'
import classes from './EditDoctorInformation.module.scss'
import CustomAlertSnackbar from '../../../../../Components/CustomSnackBar/CustomAlertSnackbar'
import {
  PartnerInformationFooterButton,
  DialogBoxIfUpdate,
  DialogBoxIfCancel
} from '../../../../../Components/PartnerInformationFooterButton/PartnerInformationFooterButton'
import FacilityAssignmentForDoctors from '../../../../../Components/FacilityAssignmentForDoctors/FacilityAssignmentForDoctors'
import DoctorProfile from '../context'
import InitialState from '../InputsforEdit/InitialState'
import DoctorValidation from './DoctorValidation'
import ProfileService from '../../../../../../../../Services/Api/Partner/ProfileService'
import PartnerViewInformation from '../../../../../Components/PartnerViewInformation/PartnerViewInformation'
// import isEmpty from 'lodash/isEmpty'
import DoctorProfileData from '../InputsforEdit/InputsforEdit'
import ChangeDoctorName from '../ChangeName'
import { useParams } from 'react-router-dom'
import InputRefs from '../InputsforEdit/InputRefs'
import { Destructure, SavingData } from './DeStructure'
import { getNestedObjects } from '../../../../../Components/Utils/utils'

const EditDoctorInformation = () => {
  const { doctorId } = useParams()
  const [form, setForm] = React.useState(InitialState)
  const [errors, setErrors] = React.useState({})
  const [openModal, setOpenModal] = React.useState(false)
  const [isSave, setIsSave] = React.useState(false)
  const [isCancel, setIsCancel] = React.useState(false)
  const [isLoading, setIsLoading] = React.useState(false)
  const [change, setChange] = React.useState(false)
  const {
    profile,
    setIsToUpdate,
    setProfile,
    setChangeName,
    isEdit,
    setIsEdit,
    setHeaderData,
    headerData
  } = React.useContext(DoctorProfile)

  React.useEffect(() => {
    let mounted = true
    if (mounted && profile) {
      const newForms = { ...form }
      const result = Destructure(profile, newForms)
      setForm(result)
    }
    return () => { mounted = false }
  }, [])

  const HandleChange = e => {
    const { name, value } = e.target
    const doctorForm = { ...form }
    setChange(true)
    const newErrors = { ...errors }
    delete newErrors[name]
    setErrors(newErrors)
    doctorForm[name] = value.trim()
    setForm(doctorForm)
  }

  const HandleTinChange = e => {
    const {name, value} = e.target
    const doctorForm = { ...form }
    setChange(true)
    const newErrors = { ...errors }
    delete newErrors[name]
    setErrors(newErrors)
    doctorForm[name] = value
    setForm(doctorForm)
  }

  const ChangeNameFunc = e => {
    const { name, value } = e.target
    const doctorForm = { ...form }
    setChange(true)
    const newErrors = { ...errors }
    delete newErrors[name]
    doctorForm[name] = value
    setForm(doctorForm)
  }

  const HandleSave = async () => {
    setIsLoading(true)
    try {
      const formResult = SavingData(form)
      const {
        data: { data: newData }
      } = await ProfileService.updateDoctorProfile(doctorId, formResult)
      setIsLoading(false)
      handleSubmitClose(false)
      setIsSave(true)
      setProfile(newData)
      const newHeaderData = { ...headerData }
      const lastName = getNestedObjects(newData, ['name', 'last']).toUpperCase()
      const firstName = getNestedObjects(newData, ['name', 'first']).toUpperCase()
      const middleName = getNestedObjects(newData, ['name', 'middle']).toUpperCase()
      const suffix = getNestedObjects(newData, ['name', 'suffix']).toUpperCase()
      newHeaderData['name'] = `${lastName}, ${firstName} ${suffix} ${middleName} `
      newHeaderData['fullName'] = `${firstName} ${middleName} ${lastName} ${suffix}`
      setHeaderData(newHeaderData)
      setTimeout(() => {
        setIsToUpdate(false)
        setIsEdit(false)
      }, 1000)
    } catch (error) {
      setIsLoading(false)
    }
  }

  const validate = () => {
    const { errors, isValid } = DoctorValidation(form)
    if (isValid) {
      setOpenModal(true)
      setErrors({})
    } else {
      setErrors(errors)
    }
  }

  const handleSubmitClose = () => {
    setOpenModal(false)
  }
  const handleSubmit = e => {
    e.preventDefault()
    validate()
  }

  const refValue = InputRefs(profile, change, form)
  const Information = DoctorProfileData(refValue, HandleChange, form, errors, setForm, setErrors, HandleTinChange)

  React.useEffect(() => {
    let mounted = true
    if (mounted) {
      HandleChangeName(profile)
    }
    return () => { mounted = false }
  }, [form.firstName, form.lastName, form.middleName, form.suffix, errors, isEdit])

  React.useEffect(() => {
    let mounted = true
    if (mounted) {
      HandleChangeName(profile)
    }
    return () => { mounted = false }
  }, [isEdit])

  const HandleChangeName = () => {
    setChangeName(<ChangeDoctorName
      isEdit={isEdit}
      setIsEdit={setIsEdit}
      firstName={form['firstName']}
      lastName={form['lastName']}
      suffix={form['suffix']}
      middleName={form['middleName']}
      error={errors}
      HandleChange={ChangeNameFunc}
    />)
  }

  return (
    <React.Fragment>
      <form>
        <div id='root' className={classes.root}>
          <FacilityAssignmentForDoctors
            form={form}
            setForm={setForm}
            refValue={refValue}
            errors={errors}
            setErrors={setErrors}
          />
          <PartnerViewInformation
            data={Information}
            hideStar
            // isEdit
            listStyles={classes.listStyles}
            firstColumStyle={classes.firstRow}
            secondColumnStyle={classes.secondRow}
          />
        </div>

        <PartnerInformationFooterButton isLoading={isLoading} setIsCancel={setIsCancel} handleSubmit={handleSubmit} />
      </form>
      <div>
        <DialogBoxIfUpdate
          handleSubmitClose={handleSubmitClose}
          HandleSave={HandleSave}
          isLoading={isLoading}
          openModal={openModal}
        />
      </div>
      <div>
        <DialogBoxIfCancel isCancel={isCancel} setIsCancel={setIsCancel} setIsToUpdate={setIsToUpdate} />
        {isSave && <CustomAlertSnackbar isSave={isSave} setIsSave={setIsSave} />}
      </div>
    </React.Fragment>
  )
}

export default React.memo(
  EditDoctorInformation,

  (prevProps, nextProps) => {
    try {
      if (
        prevProps.profile === nextProps.profile
      ) {
        return true
      }
      return false
    } catch (err) {
      return false
    }
  }
)
