import React from 'react'

// Components

import { BreadcrumbsService } from 'SharedComponents/BreadcrumbsComponent/BreadcrumbsComponent'
import HeaderWithBreadCrumbs from '../../../../Components/HeaderWithBreadCrumbs/HeaderWithBreadCrumbs'
// import history from '../../../../Components/History/History'

import { useSelector } from 'react-redux'

// import FooterWithButtons from '../../../Components/FooterWithButtons/FooterWithButtons'
// Services
// Utilities
// Styles
import classes from './HospitalAffiliationForm.module.scss'
// Validator../components/ValidateForm/validateForms

// Constant
import HospitalPageOne from './HospitalPageOne/HospitalPageOne'

const breadcrumbs = {
  Medical: {
    text: 'MEDICAL DATA SUPPORT',
    to: '/'
  },
  MedicalPartner: {
    text: 'MEDICAL PARTNER  AFFILIATION',
    to: '/'
  },
  Hospital: {
    text: 'HOSPITAL AFFILIATION',
    to: '/hospital-affiliation-form'
  }
}

let stepDetails = ['Fill-Out Form', 'Upload Documents', 'Submit']
let headerTitle = 'Hospital Affiliation Form'

const HospitalAffiliation = ({history}) => {
  const { affiliationType } = useSelector(state => state.affiliationType)

  React.useEffect(() => {
    BreadcrumbsService.setItems(breadcrumbs)
    BreadcrumbsService.setSelectedItemsWithoutHome(['Medical', 'MedicalPartner', 'Hospital'])
  }, [])

  return (
    <div id='root' className={classes.root}>
      <HeaderWithBreadCrumbs
        id='HeaderWithBreadCrumbs'
        HeaderTitle={headerTitle}
        stepDetails={stepDetails}
        currentStep={1}
      />

      <div id='AffiliationForm-div'>
        <HospitalPageOne
          history={history}
        />
      </div>
    </div>
  )
}

export default HospitalAffiliation
