import React from 'react'
import classes from './PartnerInformation.module.scss'

import PartnerViewInformation from '../../../../../../Components/PartnerViewInformation/PartnerViewInformation'
import ClinicProfileContext from '../../context'

import { MonthdateFormat, getTinStatus, OfficeLocationDefault, formatPhilHealthNumber, VatOrNonVat, YearsMonths } from '../../../../../../Components/Utils/utils'
import SpinnerLoader from '../../../../../Components/Spinner/Spinner'
import isEmpty from 'lodash/isEmpty'
import getNestedObjects from '../../../../../../Components/getNestedObjects/getNestedObjects'

const DentalClinicInformation = () => {
  const { profile } = React.useContext(ClinicProfileContext)

  const street = getNestedObjects(profile, ['street'])
  const region = getNestedObjects(profile, ['region'])
  const district = getNestedObjects(profile, ['district'])
  const city = getNestedObjects(profile, ['city'])
  const barangay = getNestedObjects(profile, ['barangay'])
  const zipCode = getNestedObjects(profile, ['zipCode'])
  const philhealthExp = MonthdateFormat(profile.philhealthExp)
  const dateOfAffiliation = MonthdateFormat(profile.dateOfAffiliation && profile.dateOfAffiliation)
  const dateOfDisaffiliation = MonthdateFormat(profile.dateOfDisaffiliation && profile.dateOfDisaffiliation)
  const discount = getNestedObjects(profile, ['facilityDiscount'])
  const years = getNestedObjects(profile, ['years'])
  const months = getNestedObjects(profile, ['months'])
  const assignments = getNestedObjects(profile, ['assignments'])
  const tinStatus = getTinStatus(getNestedObjects(profile, ['tinStatus']))
  const nonVatValue = getNestedObjects(profile, ['non_vatable'])

  const Information = [
    { id: 301, title: 'TVS', value: getNestedObjects(profile, ['tvs']) },
    { id: 301234, title: 'Office Location', value: OfficeLocationDefault(getNestedObjects(profile, ['officeLocation'])) },
    {
      id: 304,
      title: 'Medical Partner ID',
      value: getNestedObjects(profile, ['id'])
    },
    {
      id: 305,
      title: 'Former Name',
      value: getNestedObjects(profile, ['formerName'])
    },
    {
      id: 306,
      title: 'Facility Address',
      value: `${street}, ${barangay}, ${city}, ${district}, ${region} ${zipCode}`
    },
    {
      id: 3066,
      title: 'Years Of Operation',
      value: YearsMonths(years, months)
    },
    {
      id: 308,
      title: 'PhilHealth Accreditation no.',
      value: formatPhilHealthNumber(getNestedObjects(profile, ['accreditationNumber'])),
      title2: 'PHILHEALTH EXP.',
      value2: philhealthExp
    },
    {
      id: 309,
      title: 'CHECK PAYEE',
      value: getNestedObjects(profile, ['payee'])
    },
    {
      id: 310,
      title: 'CREDIT LIMIT ',
      value: getNestedObjects(profile, ['creditLimit'])
    },
    {
      id: 311,
      title: 'PAYMENT TERMS',
      value: getNestedObjects(profile, ['paymentTerms'])
    },
    {
      id: 312,
      title: 'VAT STATUS',
      value: VatOrNonVat(tinStatus, nonVatValue)
    },
    { id: 313, title: 'TAX RATE', value: getNestedObjects(profile, ['vatTaxRate']) },
    {
      id: 314,
      title: 'FACILITY discount',
      value: discount
    },
    {
      id: 315,
      title: 'Date of affiliation',
      value: dateOfAffiliation
    },
    {
      id: 316,
      title: 'Date of disaffiliation',
      value: dateOfDisaffiliation
    },
    {
      id: 317,
      title: 'Remarks',
      value: getNestedObjects(profile, ['specialInstructions'])
    },
    {
      id: 318,
      title: 'PROCESS TYPE',
      value: getNestedObjects(profile, ['processType'])
    },
    {
      id: 319,
      title: 'Clinic Schedule',
      value: getNestedObjects(profile, ['clinicSchedule'])
    }
  ]

  if (!isEmpty(profile)) {
    return (
      <PartnerViewInformation
        data={Information}
        facilityLabel={assignments}
        type={'facility'}
        listStyles={classes.listStyles}
        firstColumStyle={classes.firstRow}
        secondColumnStyle={classes.secondRow}
        ifHastTitle2
        listStyles2={classes.listStyles2}
      />
    )
  } else {
    return (
      <div className={classes.loader}>
        <SpinnerLoader />
      </div>
    )
  }
}

export default DentalClinicInformation
