import React from 'react'
import moment from 'moment'
import { getNestedObjects } from '../../../../../Components/Utils/utils'

const InputRefs = (profile, change, form) => {
  const [dataRef, setDataRef] = React.useState({})

  const formRef = React.useRef({})
  const birthdate = React.useRef({})
  const medId = React.useRef({})
  const facilityName = React.useRef({})
  const facilityAddress = React.useRef({})
  const clinicSched = React.useRef({})
  const pmaNumber = React.useRef({})
  const ptr = React.useRef({})
  const s2 = React.useRef({})
  const prcNumber = React.useRef({})
  const prcExpiration = React.useRef({})
  const tin = React.useRef({})
  const tinStatus = React.useRef({})
  const philhealthNum = React.useRef({})
  const philhealthExpiration = React.useRef({})
  const disaffiliation = React.useRef({})
  const ahmopi = React.useRef({})
  const paymentTerms = React.useRef({})
  const withTax = React.useRef({})
  const professionalFee = React.useRef({})
  const exclusiveTo = React.useRef({})
  const reason = React.useRef({})
  const vatTaxRate = React.useRef({})

  React.useEffect(() => {
    const nameFields = {
      first_name: getNestedObjects(profile, ['name', 'first']),
      last_name: getNestedObjects(profile, ['name', 'last']),
      middle_name: getNestedObjects(profile, ['name', 'middle'])
    }
    setDataRef({ ...profile, ...nameFields })
  }, [profile])

  formRef.current = dataRef

  if (change) {
    birthdate.current = ''
    medId.current.value = formRef.current.id
    facilityName.current = ''
    facilityAddress.current = ''
    clinicSched.current = ''
    pmaNumber.current = ''
    ptr.current = ''
    s2.current = ''
    prcNumber.current = ''
    prcExpiration.current = ''
    tin.current = ''
    tinStatus.current = ''
    philhealthNum.current = ''
    philhealthExpiration.current = ''
    disaffiliation.current = ''
    ahmopi.current = ''
    paymentTerms.current = ''
    withTax.current = ''
    professionalFee.current = ''
    exclusiveTo.current = ''
    reason.current = ''
    vatTaxRate.current = ''
  } else {
    birthdate.current.value = formRef.current.birthdate
    medId.current.value = formRef.current.id
    facilityName.current.value = getNestedObjects(formRef, ['current', 'facility_info', '0', 'name'])
    facilityAddress.current.value = form['facilityAddress'] ? form['facilityAddress'] : ''
    clinicSched.current.value = form['clinicSchedule']
    pmaNumber.current.value = getNestedObjects(dataRef, ['license', 'pma'])
    ptr.current.value = getNestedObjects(dataRef, ['license', 'ptr'])
    s2.current.value = getNestedObjects(dataRef, ['license', 's2'])
    prcNumber.current.value = form['prcNumber']
    prcExpiration.current.value = getNestedObjects(dataRef, ['license', 'prc', 'expiration_date'])
    tin.current.value = getNestedObjects(dataRef, ['treasury', 'tin'])
    tinStatus.current.value = getNestedObjects(dataRef, ['treasury', 'tin_status'])
    philhealthNum.current.value = getNestedObjects(dataRef, ['philhealth', 'accreditation_number'])
    philhealthExpiration.current.value = getNestedObjects(dataRef, ['philhealth', 'expiration_date'])
    disaffiliation.current.value = dataRef.disaffiliation ? moment(dataRef.disaffiliation.date).format('L') : ''
    ahmopi.current.value = form['ahmopi']
    paymentTerms.current.value = getNestedObjects(dataRef, ['treasury', 'payment_terms'])
    withTax.current.value = form['withTax']
    professionalFee.current.value = getNestedObjects(dataRef, ['professional_fees', 'rate'])
    vatTaxRate.current.value = getNestedObjects(formRef, ['current', 'treasury', 'vat', 'tax_rate'])
    exclusiveTo.current.value = form['exclusiveTo']
    reason.current.value = form['reason']
  }

  return {
    birthdate,
    medId,
    facilityName,
    facilityAddress,
    clinicSched,
    pmaNumber,
    paymentTerms,
    withTax,
    vatTaxRate,
    professionalFee,
    exclusiveTo,
    ptr,
    s2,
    prcNumber,
    prcExpiration,
    tin,
    tinStatus,
    philhealthNum,
    philhealthExpiration,
    disaffiliation,
    ahmopi,
    reason
  }
}

export default InputRefs
