import { OfficeLocationDefault, getNestedArrayOfObjects, getNestedObjects, YearDateFormat } from '../../../../../Components/Utils/utils'

const SetData = (form, data) => {
  const newForm = { ...form }

  const philhealth = data ? (data.philhealth ? data.philhealth : {}) : {}
  const dateOAffiliation = data.affiliation_status ? data.affiliation_status.date_submitted : ''
  const facilityAssignments = getNestedArrayOfObjects(data, ['facility_assignment', 'assignments'])

  /** Setting of Edit Form values
     */
  newForm.officeLocation = OfficeLocationDefault(getNestedObjects(data, ['office_location']))
  newForm.id = getNestedObjects(data, ['id'])
  newForm.name = getNestedObjects(data, ['name'])
  newForm.formerName = getNestedObjects(data, ['former_name'])
  newForm.assignments = facilityAssignments
  newForm.reason = getNestedObjects(data, ['reason'])
  newForm.status = getNestedObjects(data, ['status'])
  newForm.exclusiveTo = getNestedObjects(data, ['facility_assignment', 'exclusive_to'])
  newForm.tvs = getNestedObjects(data, ['tvs'])
  newForm.years = getNestedObjects(data, ['operation', 'years'])
  newForm.months = getNestedObjects(data, ['operation', 'months'])
  newForm.street = getNestedObjects(data, ['address', 'street'])
  newForm.region = getNestedObjects(data, ['address', 'region'])
  newForm.district = getNestedObjects(data, ['address', 'district'])
  newForm.city = getNestedObjects(data, ['address', 'city'])
  newForm.barangay = getNestedObjects(data, ['address', 'barangay'])
  newForm.zipCode = getNestedObjects(data, ['address', 'zip_code'])
  newForm.classification = getNestedObjects(data, ['classification'])
  newForm.clinicSchedule = getNestedObjects(data, ['clinic_schedule'])
  newForm.philhealthNumber = getNestedObjects(data, ['philhealth', 'accreditation_number'])
  newForm.philhealthExpiration = YearDateFormat(philhealth.expiration_date)
  newForm.payee = getNestedObjects(data, ['treasury', 'payee'])
  newForm.processType = getNestedObjects(data, ['process_type'])
  newForm.creditLimit = getNestedObjects(data, ['treasury', 'credit_limit'])
  newForm.paymentTerms = getNestedObjects(data, ['treasury', 'payment_terms'])
  newForm.tinStatus = getNestedObjects(data, ['treasury', 'tin_status'])
  newForm.non_vatable = getNestedObjects(data, ['treasury', 'non_vatable'])
  newForm.vatTaxRate = getNestedObjects(data, ['treasury', 'vat', 'tax_rate'])
  newForm.facilityDiscount = getNestedObjects(data, ['treasury', 'discount'])
  newForm.dateOfAffiliation = dateOAffiliation ? YearDateFormat(dateOAffiliation) : ''
  newForm.dateOfDisaffiliation = data.disaffiliation
    ? data.disaffiliation.date
      ? YearDateFormat(data.disaffiliation.date)
      : ''
    : ''
  newForm.specialInstructions = getNestedObjects(data, ['treasury', 'special_instruction'])
  return newForm
}

export default SetData
