import React from 'react'
import { Switch, Route } from 'react-router-dom'
import { Provider } from 'react-redux'
import RoomType from './index'

// Store
import Store from '../../../Store'

const RoomTypeTest = () => {
  const [isOpenRoomTypeMaintenance, setIsOpenRoomTypeMaintenance] = React.useState(false)

  React.useEffect(() => {
    setIsOpenRoomTypeMaintenance(true)
    return () => {
      setIsOpenRoomTypeMaintenance(false)
    }
  }, [])

  return (
    <Provider store={Store}>
      <Switch>
        <Route path='/RoomTypeMaintenance' exact render={(props) => <RoomType {...props} open={isOpenRoomTypeMaintenance} setOpen={setIsOpenRoomTypeMaintenance} />} />
      </Switch>
    </Provider>
  )
}

export default RoomTypeTest
