
import { YearDateFormat, getNestedObjects, getNestedArrayOfObjects, OfficeLocationDefault } from '../../../../../Components/Utils/utils'

export async function GetDentalClinicProfile (data) {
  return mapData(data)
}

function mapData (profile) {
  if (profile) {
    const {
      contact,
      philhealth,
      disaffiliation,
      stage,
      status,
      discount,
      id,
      type,
      name,
      former_name,
      full_address,
      address,
      deviations,
      remarks,
      clinic_schedule,
      treasury,
      dental_associates,
      affiliation_infos,
      affiliated_partners,
      afn_number,
      classification,
      facility_assignment,
      affiliation_status,
      license,
      other_affiliations,
      room_number,
      rooms,
      special_instruction,
      process_type,
      tvs,
      reason
    } = profile

    const requiredObject = {}
    requiredObject.unitFlrBldg = getNestedObjects(profile, ['address', 'unit_flr_bldg']) // profile['address'] ? profile['address']['unit_flr_bldg'] : ''
    requiredObject.bldgName = getNestedObjects(profile, ['address', 'building_name']) // profile['address'] ? profile['address']['building_name'] : ''
    requiredObject.subdivision = getNestedObjects(profile, ['address', 'subdivision']) // profile['address'] ? profile['address']['subdivision'] : ''
    requiredObject.dohNumber = getNestedObjects(profile, ['license', 'doh', 'license_type']) // profile['license'] ? profile['license']['doh']['license_type'] : ''
    requiredObject.dohLicenseType = getNestedObjects(profile, ['license', 'doh', 'number']) // profile['license'] ? profile['license']['doh']['number'] : ''
    requiredObject.dohLicenseExp = getNestedObjects(profile, ['license', 'doh', 'license_exp']) // profile['license'] ? profile['license']['doh']['license_exp'] : ''
    requiredObject.stageName = getNestedObjects(profile, ['stage', 'stage_name']) // profile['stage']['stage_name']
    requiredObject.dateSubmitted = getNestedObjects(profile, ['stage', 'date_submitted']) || new Date().toISOString() // stage['date_submitted'] ? stage['date_submitted'] : new Date().toISOString()
    requiredObject.contacts = getNestedObjects(profile, ['contacts']) // profile['contacts']
    requiredObject.internalRemarks = getNestedObjects(profile, ['remarks', 'internal_remarks']) // profile['remarks'] ? profile['remarks']['internal_remarks'] : ''
    requiredObject.externalRemarks = getNestedObjects(profile, ['remarks', 'external_remarks']) // profile['remarks'] ? profile['remarks']['external_remarks'] : ''
    requiredObject.type = getNestedObjects(profile, ['type']) // profile['type']

    let obj = {}

    obj.formerName = former_name || ''
    obj.id = id
    obj.partnerName = name || ''
    obj.officeLocation = OfficeLocationDefault(getNestedObjects(profile, ['office_location']))
    obj.status = status || ''
    obj.address = full_address || ''
    obj.tinStatus = getNestedObjects(treasury, ['tin_status']).trim().length > 0 ? getNestedObjects(treasury, ['tin_status']) : ''
    obj.non_vatable = getNestedObjects(treasury, ['non_vatable'])
    obj.payee = getNestedObjects(treasury, ['payee'])
    obj.creditLimit = getNestedObjects(treasury, ['credit_limit'])
    obj.paymentTerms = getNestedObjects(treasury, ['payment_terms'])
    obj.vatTaxRate = getNestedObjects(treasury, ['vat', 'tax_rate'])
    obj.accreditationNumber = getNestedObjects(philhealth, ['accreditation_number'])
    obj.dateOfDisaffiliation = disaffiliation
      ? disaffiliation['date']
        ? YearDateFormat(disaffiliation['date'])
        : ''
      : ''
    obj.dateOfAffiliation = affiliation_status
      ? affiliation_status.date_submitted
        ? YearDateFormat(affiliation_status.date_submitted)
        : ''
      : ''
    obj.philhealthExp = philhealth
      ? philhealth['expiration_date']
        ? YearDateFormat(philhealth['expiration_date'])
        : ''
      : ''
    obj.years = getNestedObjects(profile, ['operation', 'years'])
    obj.months = getNestedObjects(profile, ['operation', 'months'])
    obj.street = getNestedObjects(address, ['street'])
    obj.region = getNestedObjects(address, ['region'])
    obj.district = getNestedObjects(address, ['district'])
    obj.city = getNestedObjects(address, ['city'])
    obj.barangay = getNestedObjects(address, ['barangay'])
    obj.zipCode = getNestedObjects(address, ['zip_code'])
    obj.clinicSchedule = clinic_schedule || ''
    obj.tvs = tvs || ''
    obj.facilityDiscount = getNestedObjects(treasury, ['discount'])
    obj.classification = classification
    obj.assignments = getNestedArrayOfObjects(facility_assignment, ['assignments'])
    obj.exclusiveTo = getNestedArrayOfObjects(facility_assignment, ['exclusive_to'])
    obj.processType = process_type || ''
    obj.specialInstructions = getNestedObjects(treasury, ['special_instruction'])
    obj.callFlow = getNestedObjects(profile, ['callFlow'])
    obj.contacts = getNestedArrayOfObjects(profile, ['contacts'])
    obj.reason = reason || ''

    return { profile: obj, additionalProfileInfo: requiredObject }
  } else {
    return false
  }
}
