import React from 'react'
import ReactTable from '../Helpers/TableDisplay'
import { dentalClinic } from 'Constant/Reports/ColumnsHeaders.json'
import { LoadingService } from 'SharedComponents/LoadingComponent/LoadingComponent'
import classes from '../scss/medicalClinic.module.scss'
import Button from '@material-ui/core/Button'
import PreviewDownload from '../Helpers/PreviewDownload'
import moment from 'moment'
import ReportServices from '../../../../../Services/Api/Reports/Reports'
import ReportsTable from '../TableComponent'
import { getNestedObjects, getNestedArrayOfObjects } from 'Pages/Protected/Partner/Components/Utils/utils'
import { DentalDentistHeaders } from '../Helpers/ReportsTableHeaderData'
import { getDentalDentistPreview, modifyFilters } from '../Helpers/tableService'
import { getConstantValue } from 'typescript'

const DentalClinic = () => {
  const [dentalClinics, setDentalClinics] = React.useState([])
  const [downloadData, setDownloadData] = React.useState([])
  const [openPreview, setOpenPreview] = React.useState(false)
  const [datas, setDatas] = React.useState([])
  const [tableData, setTableData] = React.useState([])
  const [filters, setFilters] = React.useState({})
  const [modifiedFilter, setNewModifiedFilter] = React.useState({})

  React.useEffect(() => {
    fetchData()
  }, [datas])

  React.useEffect(() => {
    const nFilter = { ...modifiedFilter }
    let mFilter = modifyFilters(filters, false)
    const newFilter = Object.assign(nFilter, mFilter)
    setNewModifiedFilter(newFilter)
  }, [filters])

  const fetchData = async () => {
    LoadingService.loading(true)
    try {
      const newDatas = [...datas]

      const newMapdata = newDatas.map(items => {
        return {
          status: getNestedObjects(items, ['status']),
          clinic_schedule: getNestedObjects(items, ['clinic_schedule']),
          dental_clinic_name: getNestedObjects(items, ['dental_clinic_name']),
          id: getNestedObjects(items, ['id']),
          dentist_name: getNestedObjects(items, ['dentist_name']),
          specialization_minor_major: getNestedObjects(items, ['specialization_minor_major']),
          room_number: getNestedObjects(items, ['room_number']),
          street: getNestedObjects(items, ['address', 'street']),
          barangay: getNestedObjects(items, ['address', 'barangay']),
          city: getNestedObjects(items, ['address', 'city']),
          district: getNestedObjects(items, ['address', 'district']),
          region: getNestedObjects(items, ['address', 'region']),
          contacts: Array.isArray(getNestedObjects(items, ['contacts'])) ? '' : getNestedObjects(items, ['contacts'])
        }
      })

      setTableData(newMapdata)
      LoadingService.loading(false)
    } catch (e) {
      console.log('error', e)
    }
  }

  const headerProps = React.useMemo(() =>
    [
      {
        id: 'Status',
        accessor: 'status',
        sortable: true,
        searchable: true,
        size: '180px'
      },
      {
        id: 'Medical Partner ID',
        accessor: 'id',
        sortable: true,
        searchable: true,
        size: '200px'
      },
      {
        id: 'Dentist Name',
        accessor: 'dentist_name',
        sortable: true,
        searchable: true,
        size: '200px'
      },
      {
        id: 'Dental Clinic Name',
        accessor: 'dental_clinic_name',
        sortable: true,
        searchable: true,
        size: '200px'
      },
      {
        id: 'Specialization',
        accessor: 'specialization_minor_major',
        sortable: true,
        searchable: true,
        size: '200px'
      },
      {
        id: 'Room No.',
        accessor: 'room_number',
        sortable: false,
        searchable: false,
        size: '200px'
      },
      {
        id: 'Schedule',
        accessor: 'clinic_schedule',
        sortable: false,
        searchable: false,
        size: '200px'
      },
      {
        id: 'Unit No./Street',
        accessor: 'street',
        sortable: true,
        searchable: true,
        size: '200px'
      },
      {
        id: 'Barangay',
        accessor: 'barangay',
        sortable: true,
        searchable: true,
        size: '200px'
      },
      {
        id: 'City/Municipality',
        accessor: 'city',
        sortable: true,
        searchable: true,
        size: '200px'
      },
      {
        id: 'Province',
        accessor: 'district',
        sortable: true,
        searchable: true,
        size: '200px'
      },
      {
        id: 'Region',
        accessor: 'region',
        sortable: true,
        searchable: true,
        size: '200px'
      },
      {
        id: 'Contact Number',
        accessor: 'contacts',
        sortable: false,
        searchable: false,
        size: '180px'
      }
    ], [])

  return (
    <React.Fragment>
      <div className={classes.root}>
        <div id='root' className={classes.tableContainer}>
          <ReportsTable
            getData={(data, token) => {
              const args = { ...data }
              return ReportServices.getDentalAndDentistByType(args, token)
            }}
            setDatas={setDatas}
            type={'DENTIST'}
            headerProps={headerProps}
            datas={tableData}
            setFilters={setFilters}
          />
        </div>
      </div>
      <div className={classes.previewButton}>
        <Button onClick={() => setOpenPreview(true)} variant='contained' className={classes.DownloadPreviewBtn}>
          Download
        </Button>
        {openPreview && <PreviewDownload
          openPreview={openPreview}
          setOpenPreview={setOpenPreview}
          downloadData={downloadData}
          headerColumn={DentalDentistHeaders}
          infiniteScrollService={getDentalDentistPreview}
          filename={`${moment().format('YYYYMMDD')}-dental-clinic.csv`}
          filter={filters}
          downloadFunc={ReportServices.download}
          type='DENTIST'
        />}
      </div>
    </React.Fragment>
  )
}

export default DentalClinic
