import isEmpty from 'lodash/isEmpty'
import Joi from '@hapi/joi'

const schema = Joi.object({
  facilityName: Joi.string().label('Facility Name').required(),
  facilityMpId: Joi.string().label('Medical Partner ID').required(),
  address: Joi.string().label('Address').required(),
  roomNumber: Joi.string().label('Room Number').required(),
  mainCoordinatorName: Joi.string().label('Main Coordinator Name').required(),
  mainContactNum: Joi.string().label('Main Coordinator Contact No.').required(),
  contactNumbers: Joi.string().label('Contact No.').required(),
  clinicSchedule: Joi.string().label('Clinic Schedule').required()
})

const notRequiredFields = [
  'asstCoordinatorName',
  'asstContactNum',
  'suffix',
  'asstCoordinators'
]

export default function DoctorFormValidation (form) {
  let newErrors = {}
  const result = schema.validate(form, { abortEarly: false })
  if (result.error && result.error.details.length > 0) {
    for (const i of result.error.details) {
      newErrors[i.path[0]] = i.message
      if (notRequiredFields.includes(i.path[0])) {
        delete newErrors[i.path[0]]
      }
    }
  }
  console.log('section two doctor form', newErrors)
  return {
    newErrors,
    isValid: isEmpty(newErrors)
  }
}
