import React from 'react'
import { TableRow, TableCell } from '@material-ui/core'
import classes from './index.module.scss'

const Body = props => {
  const { headerProps, datas } = props

  return (
    <React.Fragment>
      {
        datas.length > 0 &&
           datas.map((bodyItems, index) => {
             return (
               <TableRow className={classes.TableRow} key={index}>
                 {
                   headerProps.map((headerItems, headerIndex = index) => {
                     return (
                       <TableCell
                         component='th'
                         scope='row'
                         align='left'
                         key={`${headerIndex}-cell`}
                         className={classes.TableCell}
                         style={{ width: headerItems.size ? headerItems.size : null }}
                       >
                         <div id='data-cell' className={classes.data}>
                           {bodyItems[headerItems['accessor']]}
                         </div>
                       </TableCell>
                     )
                   })
                 }
               </TableRow>
             )
           })
      }
    </React.Fragment>
  )
}

export default React.memo(Body)
