import React, { useState, useMemo, useEffect, Fragment } from 'react'
import { useParams } from 'react-router-dom'
import { useSelector, useDispatch } from 'react-redux'

import AffiliationForm from '../../Components/AffiliationForm/AffiliationForm'
import FooterWithButtons from 'Pages/Protected/Partner/Components/DynamicFooterWithButton/FooterWithButtons'
import ProgressComponent from 'Pages/Protected/Partner/AffiliationFormEncoder/Components/ProgressComponent/ProgressComponent'

import { setAffiliationType, setDentistAffiliation } from 'Pages/Protected/Store/actions/affiliationType'

import classes from './DentalClinic.module.scss'
import { setDentalClinicAffilForm, setDentistProfile, setMedicalDentistId, setMedicalDentistError, setPrcNumberError } from 'Pages/Protected/Store/actions/Partner/dentalClinicAffilForm'
import { barangayAction, cityAction, provinceAction } from 'Pages/Protected/Store/actions/Partner/addressAction'

let pathUrl = '/dental-clinic-affiliation-requirements'
let affilConstValue = {
  stageName: 'ENCODING',
  status: 'Active',
  source: 'PNM',
  affiliationStatus: 'In-Progress'
}

function DentalClinicEncoderPageOne ({ history }) {
  const { id } = useParams()
  const dispatch = useDispatch()

  const { affiliationType, dentistAffiliationType } = useSelector(state => state.affiliationType)
  const { medicalDentistId, dentistPrcNumber } = useSelector(state => state.dentalClinicForm)

  const [isSuccess, setIsSuccess] = useState(false)
  const [affilID, setAffilID] = React.useState(null)
  const [isSubmitted, setIsSubmitted] = useState(false)
  const [isValid, setValid] = useState(false)
  const [isTovalidate, setIsToValidate] = useState(false)
  const [loading, setIsloading] = useState(false)
  const [dentistForm, setDentistForm] = useState({})
  const [dentalClinicForm, setDentalClinicForm] = useState({})
  const [{ dentalData, dentistData, statusData, remarksData, affiliatedData}, setDataStates] = useState({
    dentistData: {},
    dentalData: {},
    statusData: {},
    remarksData: {},
    affiliatedData: {},
  })


  useEffect(() => {
    if (isTovalidate) {
      setTimeout(() => {
        setIsToValidate(false)  // reset validation trigger
      }, 200)
    }
  }, [isTovalidate])


  useEffect(() => {
    if (id || affiliationType.id) {
      const paramId = id ? id : affiliationType.id
      fetch(paramId)
    }
    if (affiliationType.hasOwnProperty('id')) {
      setAffilID(affiliationType['id'])
    }
    if(affiliationType.officeLocation) {
      console.log('_________office location rock_________')
      const dentistAffil = {...dentistAffiliationType}
      dentistAffil.officeLocation = affiliationType.officeLocation
      dispatch(setDentistAffiliation(dentistAffil))
    }
    setSubmitted(affiliationType.stageName)
  }, [])


  const disabledButton = useMemo(() => {
    if (isValid) {
      return false
    } else {
      return true
    }
  }, [isValid])

  const handleSubmit = async e => {
    e.preventDefault()
    if(medicalDentistId) {
      let dentistIdIsValid = await (await apiUtils()).isDentistIdIsValid(medicalDentistId, false)
      !dentistIdIsValid ? dispatch(setMedicalDentistError('"Medical Dentist ID" Not Found')) : dispatch(setMedicalDentistError(''))
    }
    if(dentistPrcNumber) {
      let prcNumberError = await (await apiUtils()).isPrcNumberIsDuplicate(dentistPrcNumber)
      prcNumberError ? dispatch(setPrcNumberError('Duplicate Prc Number')) : dispatch(setPrcNumberError(''))
    }
    if (!isSubmitted) {
      setIsToValidate(true)
      if (isValid) {
        save()
      }
    }
  }

  const handleResetForm = () => {
    console.log('handleResetForm')
  }

  const fetch = async id => {
    try {
      let {dentalClinicData, dentistData: dentist, dentalAssociates, remarks, tinStatus, dentistId, affilType, dentistAffil} = await (await apiUtils()).fetchData(id, setIsloading)
      dispatch(setDentistAffiliation(dentistAffil))
      setDataStates({
        dentalData: dentalClinicData,
        dentistData: dentist,
        statusData: tinStatus,
        affiliatedData: dentalAssociates,
        remarksData: remarks
      })
      setAffilID(affilType.id)
      dispatch(setAffiliationType(affilType))
      setSubmitted(affilType.stageName)
      dispatch(setMedicalDentistId(dentistId))
      dispatch(setDentalClinicAffilForm(Object.assign({}, dentalClinicData, dentalAssociates, remarks)))
      dispatch(setDentistProfile(Object.assign({}, dentist, tinStatus)))
      dispatch(barangayAction(await fetchBarangays(dentalClinicData['city'])))
      dispatch(cityAction(await fetchCities(dentalClinicData['district'])))
      dispatch(provinceAction(await fetchProvince(dentalClinicData['region'])))
    } catch (error) {
      console.log('error in fetching data', error)
      setIsloading(false)
    }
  }

  const save = async () => {
    let affil = Object.assign(affiliationType, affilConstValue)
    if (!affilID) {
      store(affil)
    } else {
      update(affil)
    }

  }

  const apiUtils = async () => {
    return await import('../dentalClinicService')
  }

  const utils = async () => {
    return await import('Pages/Protected/Partner/Components/Utils/utils')
  }

  const fetchBarangays = async brgy => await (await utils()).fetchBarangays(brgy)

  const fetchCities = async city => await (await utils()).fetchCities(city)

  const fetchProvince = async prov => await (await utils()).fetchProvinces(prov)

  const setSubmitted = stageName => {
    if (stageName === 'AFFILIATION_APPROVAL_FOR_OIC') {
      setIsSubmitted(true)
    }
  }

  const store = async (affil) => {
    let dentistId = await saveDentistForm()
    let dentistFormData = { ...dentistForm }
    if(dentistId) {
      let {
        clinicId,
        affilType,
        dentalClinicForm: dentalForm
      } = await (await apiUtils()).createDentalClinicForm(dentalClinicForm, affil, setIsSuccess, dentistId)
      dentistFormData['id'] = dentistId
      dispatch(setAffiliationType(affilType))
      setAffilID(clinicId)
      dispatch(setDentalClinicAffilForm(dentalForm))
      dispatch(setDentistProfile(dentistFormData))
      dispatch(setMedicalDentistId(dentistId))
    }
  }

  const update = async (affil) => {
    let dentistId = await saveDentistForm()
    if(dentistId) {
    const paramId = id ? id : affiliationType.id
      dispatch(setMedicalDentistId(dentistId))
      const data = await (await apiUtils()).updateDentalForm(paramId, dentistId, affil, dentalClinicForm, setIsSuccess)
      console.log(console.log(data))
    }
  }

  const saveDentistForm = async () => {
    if(medicalDentistId) {
      let dentistIdIsValid = await (await apiUtils()).isDentistIdIsValid(medicalDentistId)
      if(!dentistIdIsValid) {
        setValid(false)
      } else {
          const dentistFormCopy = { ...dentistForm }
          dentistFormCopy['id'] = dentistIdIsValid
          let newDentistAffil = { ...dentistAffiliationType }
          newDentistAffil.officeLocation = affiliationType.officeLocation
          await (await apiUtils()).updateDentistForm(dentistIdIsValid, newDentistAffil, dentistFormCopy)
          return dentistIdIsValid
      }
    } else {
      let prcNumberIsDuplicate = await (await apiUtils()).isPrcNumberIsDuplicate(dentistPrcNumber)
      if(!prcNumberIsDuplicate) {
        const affil = Object.assign(dentistAffiliationType, affilConstValue)
        console.log('----------------affil sa create-----------------')
        console.log('affil', affil)
        console.log('create affil dentist aFfil', dentistAffiliationType)
        console.log('----------------affil sa create-----------------')
        const {id, dentistAffil } = await (await apiUtils()).createDentistForm(affil, dentistForm)
        dispatch(setDentistAffiliation(dentistAffil))
        return id
      } else {
        console.log('no prc duplicates bro -w-2-423-42-')
        dispatch(setPrcNumberError('Duplicate Prc Number'))
      }
    }
  }

  if (loading) return <ProgressComponent />


  return (
    <Fragment>
      <AffiliationForm
        history={history}
        setValid={setValid}
        isTovalidate={isTovalidate}
        setDentistData={setDentistForm}
        setDentalData={setDentalClinicForm}
        dentistData={dentistData}
        affiliatedData={affiliatedData}
        dentalData={dentalData}
        statusData={statusData}
        remarksData={remarksData}
        isSubmitted={isSubmitted}
      />
      <div className={classes.footerContainer}>
        <FooterWithButtons
          footerStatus={'Dental Clinic'}
          onClickSave={handleSubmit}
          disableButton={isSubmitted ? false : disabledButton}
          isSuccess={isSuccess}
          setIsSuccess={setIsSuccess}
          isSubmitted={isSubmitted}
          handleReset={handleResetForm}
          pathUrl={pathUrl} // use for proceed button
          save={save}
          history={history}
        />
      </div>
    </Fragment>
  )
}

export default DentalClinicEncoderPageOne
