import React from 'react'
import { makeStyles } from '@material-ui/core/styles'
import Table from '@material-ui/core/Table'
import TableBody from '@material-ui/core/TableBody'
import TableCell from '@material-ui/core/TableCell'
import TableContainer from '@material-ui/core/TableContainer'
import TableHead from '@material-ui/core/TableHead'
import TableRow from '@material-ui/core/TableRow'
import Paper from '@material-ui/core/Paper'

const useStyles = makeStyles({
  table: {
    // backgroundColor: '#fafafa'
  },
  tableCell: {
    margin: 5,
    border: 'solid #f5f7fb 2px'
  }
})

const TablePreview = ({ headers = [], dataRow = [] }) => {
  const classes = useStyles()
  return (
    <TableContainer component={Paper}>
      <Table className={classes.table} stickyHeader aria-label='simple table'>
        <TableHead>
          <TableRow>
            {headers.map((d, i) => <TableCell key={d.label + i}>{d.label}</TableCell>)}
          </TableRow>
        </TableHead>
        <TableBody>
          {dataRow.map((row, i) => (
            <TableRow key={row.name + i}>
              {headers.map((d, i) => <TableCell className={classes.tableCell} key={d.label + i}>{row[d.key]}</TableCell>)}
            </TableRow>
          ))}
        </TableBody>
      </Table>
    </TableContainer>
  )
}

export default TablePreview
