import React from 'react'
import { InputBase, Paper } from '@material-ui/core'
import classes from './index.module.scss'

const HeaderInputs = props => {
  const { name = '', onChange = null, size = '' } = props

  return (
    <Paper style={{ width: size }}>
      <InputBase
        name={name}
        onChange={onChange}
        className={classes.Input}
        multiline
      />
    </Paper>
  )
}

export default React.memo(HeaderInputs, (prevProps, nextProps) => {
  try {
    if (prevProps.name === nextProps.name && prevProps.onChange === nextProps.onChange && prevProps.size === nextProps.size) {
      return true
    }
    return false
  } catch (e) {
    console.log('error', e)
  }
})
