import React from 'react'
import TableList from '../../../Components/TableList'
import ContractManagerServices from 'Services/Api/Partner/ContractManagerServices'
import { useHistory } from 'react-router-dom'

const ForRenewViewPrintTable = props => {
  const { open, setOpen, onHandleDoubleClick = () => {}, type = '' } = props

  return (
    <TableList
      width='100%'
      getAsyncFunc={(data, token) => {
        const args = { ...data, type: type }
        return ContractManagerServices.getRenewContract(args, token)
      }}
      readOnlyHeaderTableProps={[
        {
          text: 'Status',
          field: 'status',
          enableSort: false,
          customStyle: { minWidth: '180px' }
        },
        {
          text: 'Medical Partner ID',
          type: 'text',
          field: 'id',
          customStyle: { minWidth: '200px' }
        },
        ...(type === 'doctors'
          ? [
              {
                text: 'Facility ID',
                enableSort: false,
                field: 'facilityID',
                customStyle: { minWidth: '200px' }
              }
            ]
          : []),
        {
          text: 'Expiration Date',
          type: 'date',
          field: 'expirationDate',
          customStyle: { minWidth: '210px' }
        },
        {
          text: 'Partner Type',
          type: 'text',
          field: 'type',
          customStyle: { minWidth: '300px' }
        },
        ...(type === 'doctors'
          ? [
              {
                text: 'Partner Name',
                type: 'text',
                field: 'full_name',
                customStyle: { minWidth: '300px' }
              }
            ]
          : [
              {
                text: 'Partner Name',
                type: 'text',
                field: 'name',
                customStyle: { minWidth: '300px' }
              }
            ]),
        {
          text: 'Location',
          type: 'text',
          field: 'location',
          customStyle: { minWidth: '300px' }
        }
      ]}
      readOnlyFooterButtonsProps={[
        {
          text: 'Close',
          style: { backgroundColor: '#fd5451' },
          onClick: () => setOpen(false)
        }
      ]}
      dataStructure={{
        id: { type: 'text' },
        status: { type: 'text' },
        ...(type === 'doctors' ? { facilityID: { type: 'text' } } : {}),
        expirationDate: { type: 'date' },
        type: { type: 'text' },
        ...(type === 'doctors' ? { full_name: { type: 'text' } } : { name: { type: 'text' } }),
        location: { type: 'text' }
      }}
      filter={{
        id: '',
        status: '',
        expirationDate: '',
        type: '',
        ...(type === 'doctors' ? { full_name: '' } : { name: '' }),
        location: ''
      }}
      sort={{
        id: false,
        status: false,
        expirationDate: false,
        type: false,
        ...(type === 'doctors' ? { full_name: false } : { name: false }),
        location: false
      }}
      doubleClick={{
        enable: true,
        onDoubleClick: obj => {
          onHandleDoubleClick(obj)
        }
      }}
      circularProgressWidth={7}
      openModal={open}
      setOpenModal={setOpen}
      paginationText={'Contracts'}
    />
  )
}

export default ForRenewViewPrintTable
