import instance from '../instance'
const facilities = instance('/p/f')

export default class FacilityGroupingService {
  static async add (data) {
    return facilities({
      url: `/partners/facility-group/add`,
      method: 'POST',
      headers: { skip_generate_form_data: 1 },
      data
    })
  }

  static async get () {
    return facilities({
      url: `/partners/facility-groups`,
      method: 'GET'
    })
  }

  static async getFacilityType (args, argCancelToken) {
    const params = {
      facilityType: args
    }

    return facilities({
      url: `/partners/get-facility-type`,
      method: 'GET',
      headers: { skip_generate_form_data: 1 },
      params,
      cancelToken: argCancelToken.cancelToken
    })
  }

  static async getFacilityGroups (argParams, argCancelToken) {
    const params = {
      skip: argParams.offset,
      limit: argParams.limit,
      filter: argParams.filter
    }

    return facilities({
      url: `/partners/get-facility-groups`,
      method: 'GET',
      headers: { skip_generate_form_data: 1 },
      params,
      cancelToken: argCancelToken.cancelToken
    })
  }

  static async updateFacilityGroup (args) {
    return facilities({
      url: `/partners/update-facility-group`,
      method: 'PUT',
      headers: { skip_generate_form_data: 1 },
      data: { ...args }
    })
  }

  static async update (data) {
    return facilities({
      url: `/partners/facility-group/${data._id}`,
      method: 'PUT',
      headers: { skip_generate_form_data: 1 },
      data: {
        code: data.code,
        desc: data.desc,
        facilityNames: data.facilityNames,
        facility_type: data.facility_type,
        status: data.status
      }
    })
  }
}
