import React, { useEffect, useState, useContext } from 'react'

// api service
import AffiliationApproverServices from 'Services/Api/Partner/AffiliationApproverServices'

// shared Components
import { LoadingService } from 'SharedComponents/LoadingComponent/LoadingComponent'
import AffiliationTableDisplay from '../../../Components/affilationTableDisplay.js/AffiliationTableDisplay'
import TwoTextArea from '../../../../../Components/TwoTextArea/TwoTextAreaWithDebounce'
import CustomTextFieldWithIcons from '../../../../../Components/CustomTextFieldWithIcons/CustomTextFieldWithIcons'

// marterial ui
import TextField from '../../../../../../../../SharedComponents/TextField/CustomTextField'
import Radio from '@material-ui/core/Radio'
import RadioGroup from '@material-ui/core/RadioGroup'
import FormControlLabel from '@material-ui/core/FormControlLabel'
import FormControl from '@material-ui/core/FormControl'
import FormLabel from '@material-ui/core/FormLabel'
import Paper from '@material-ui/core/Paper'

// context
import HospitalAffiliationContext from '../../context/HospitalAffiliationContext'

// style scss
import classes from './AffiliationForm.module.scss'

import { dateFormat } from 'Pages/Protected/Partner/Components/Utils/utils'
import { fieldHospitalApproverMapping } from '../../../utils/hospitalApproverUtils'

const URL = process.env.REACT_APP_SERVER_API

const affiliationTableHeader = [
  { label: 'Full Name', name: 'cName' },
  { label: 'Designation', name: 'cDesignation' },
  { label: 'Department', name: 'cDepartment' },
  { label: 'Email Address', name: 'cEmail' },
  { label: 'Contact Number', name: 'cNumber' }
]

const existingAffilitaionHmoHeader = [{ label: 'Existing Affiliated HMO/S', name: 'affiliatedGroup' }]

function AffiliationForm ({ setDisabled }) {
  const [formData, setFormData] = useState({})
  const [radioButtonsValue, setRadioButtonsValue] = useState({})
  const [listOfContacts, setContacts] = useState([])
  const [listOfAffiliatedGroup, setAffiliatedGroup] = useState([])
  const [remarks, setRemarks] = useState('')
  const { paramId, setForm, form, isApproved, inputRef, setInternalRemarks, internalRemarks } = useContext(
    HospitalAffiliationContext
  )
  useEffect(() => {
    console.log('form data sa form', form)
    if (isApproved) {
      setFormData(form)
      setRemarks(internalRemarks.internalRemarks)
      const obj = {}
      obj.ProcessType = form.process_type
      obj.TinStatus = form.treasury['tin_status'] ? form.treasury['tin_status'] : ''
      obj.Classification = form.classification
      setContacts(form.contacts)
      if (form.affiliation_infos) {
        setAffiliatedGroup(form.affiliation_infos)
      }
      setRadioButtonsValue(obj)
      if (inputRef.current) inputRef.current.value = internalRemarks.internalRemarks
    } else {
      fetchAffiliationData(paramId)
    }
  }, [])

  const fetchAffiliationData = async (paramId) => {
    LoadingService.loading(true)
    try {
      const obj = {}
      const {
        data: { data }
      } = await AffiliationApproverServices.getFacilityPartnerById(paramId)
      const mapData = fieldHospitalApproverMapping(data)
      setInternalRemarks(mapData)
      setRemarks(mapData.internalRemarks)
      if (inputRef.current) inputRef.current.value = mapData.internalRemarks
      if (data) {
        let { contacts, treasury, classification } = data
        setContacts(contacts)
        if (data.affiliation_infos) {
          setAffiliatedGroup(data.affiliation_infos)
        }
        obj.ProcessType = data.process_type
        obj.TinStatus = treasury['tin_status'] ? treasury['tin_status'] : ''
        obj.Classification = classification
        console.log('object hospital rock and roll', obj)

        setRadioButtonsValue(obj)
        setFormData(data)
        setForm(data)
        LoadingService.loading(false)
      }
    } catch (error) {
      console.log('errors', error)
      LoadingService.loading(false)
    }
  }

  return (
    <React.Fragment>
      <div className={classes.root}>
        <div className={classes.textContainer}>
          <div>FACILITY INFORMATION</div>
          <div>GENERAL INFORMATION</div>
        </div>
        <div className={classes.firstPartInputFields}>
          <div id='name'>
            <label id='hospitalLabel'>Hospital Name *</label>
            <TextField value={formData['name']} />
          </div>
          <div id='firstLayerFields' className={classes.firstLayerFields}>
            <div id='email'>
              <label id='emailLabel'>Email Address *</label>
              <TextField value={formData['email'] ? formData['email'] : ''} />
            </div>
            <div id='bedCapacity'>
              <label id='bedlabel'>Bed Capacity *</label>
              <TextField value={formData['bed_capacity'] || ''} />
            </div>
            <div id='years'>
              <label>Years Operation *</label>
              <CustomTextFieldWithIcons
                value={formData['operation'] && formData['operation'].years}
                styles={classes.customTag}
                tags={'years'}
              />
            </div>
            <div id='blankSpace'>
              <label>'</label>
              <CustomTextFieldWithIcons
                value={formData['operation'] && formData['operation'].months}
                styles={classes.customTag}
                tags={'months'}
              />
            </div>
          </div>
          <div id='secondLayerFields' className={classes.secondLayerFields}>
            <div id='dohNumber'>
              <label>DOH License Number</label>
              <TextField value={formData['license'] && formData['license']['doh']['number']} />
            </div>
            <div id='dohLicenseType' className={classes.dohLicenseType}>
              <label>DOH License Type</label>
              <TextField value={formData['license'] && formData['license']['doh']['license_type']} />
            </div>
            <div id='dohLicenseExp'>
              <label>DOH License Expiry Date</label>
              <div id='dohDatePicker'>
                <TextField value={formData['license'] && dateFormat(formData['license']['doh']['license_exp'])} />
              </div>
            </div>
          </div>
          <div id='thirdLayerFields' className={classes.thirdLayerFields}>
            <div id='philhealthNumber'>
              <label>PhilHealth Accreditation Number</label>
              <TextField value={formData['philhealth'] && formData['philhealth']['accreditation_number']} />
            </div>
            <div id='philhealthExpiration'>
              <label>PhilHealth Expiry Date</label>
              <div id='philHealthdiv'>
                <TextField value={formData['philhealth'] && dateFormat(formData['philhealth']['expiration_date'])} />
              </div>
            </div>
            <div id='checkPayee'>
              <label>Check Payee *</label>
              <TextField value={formData['treasury'] && formData['treasury']['payee']} />
            </div>
          </div>
        </div>
        <div className={classes.RadioButtonParts}>
          <div id='hospitalRadioButtons'>
            <FormControl component='fieldset'>
              <FormLabel className={classes.label} component='legend'>
                Hospital Classification *
              </FormLabel>
              <RadioGroup aria-label='gender' value={radioButtonsValue['Classification']}>
                <FormControlLabel
                  value='Primary'
                  checked={radioButtonsValue['Classification'] === 'Primary'}
                  disabled={radioButtonsValue['Classification'] !== 'Primary'}
                  control={<Radio color='primary' />}
                  label='Primary'
                />
                <FormControlLabel
                  value='Secondary'
                  checked={radioButtonsValue['Classification'] === 'Secondary'}
                  disabled={radioButtonsValue['Classification'] !== 'Secondary'}
                  control={<Radio color='primary' />}
                  label='Secondary'
                />
                <FormControlLabel
                  value='Tertiary'
                  checked={radioButtonsValue['Classification'] === 'Tertiary'}
                  disabled={radioButtonsValue['Classification'] !== 'Tertiary'}
                  control={<Radio color='primary' />}
                  label='Tertiary'
                />
              </RadioGroup>
            </FormControl>
          </div>
          <div id='tinStatusRadioButtons'>
            <FormControl component='fieldset'>
              <FormLabel className={classes.label} component='legend'>
                VAT Status *
              </FormLabel>
              <RadioGroup aria-label='gender' value={radioButtonsValue['TinStatus']}>
                <FormControlLabel
                  value='12%'
                  checked={radioButtonsValue['TinStatus'] === '12%'}
                  disabled={radioButtonsValue['TinStatus'] !== '12%'}
                  control={<Radio color='primary' />}
                  label='VATable'
                />
                <FormControlLabel
                  value='Non-VaTable'
                  checked={radioButtonsValue['TinStatus'] === 'Non-VaTable'}
                  disabled={radioButtonsValue['TinStatus'] !== 'Non-VaTable'}
                  control={<Radio color='primary' />}
                  label='Non-VATable'
                />
              </RadioGroup>
            </FormControl>
            <div id='tinStatusDropDown' className={classes.dropDown}>
              <TextField value={formData['treasury'] && formData['treasury']['non_vatable']} />
            </div>
          </div>
          <div id='ProcessTypeRadioButtons' className={classes.ProcessTypeRadioButtons}>
            <div id='vatTaxRate' className={classes.vatTaxRate}>
              <label>TAX Rate *</label>
              <div id='taxInput'>
                <CustomTextFieldWithIcons
                  value={formData['treasury'] && formData['treasury']['vat']['tax_rate']}
                  styles={classes.customTag}
                  tags={'%'}
                />
              </div>
            </div>
            <div id='radioFields' className={classes.ProcessType}>
              <FormControl component='fieldset'>
                <FormLabel className={classes.label} component='legend'>
                  Process Type *
                </FormLabel>
                <RadioGroup aria-label='gender' value={radioButtonsValue['ProcessType']}>
                  <FormControlLabel
                    value='Umbrella'
                    checked={radioButtonsValue['ProcessType'] === 'Umbrella'}
                    disabled={radioButtonsValue['ProcessType'] !== 'Umbrella'}
                    control={<Radio color='primary' />}
                    label='Umbrella'
                  />
                  <FormControlLabel
                    value='Regular'
                    checked={radioButtonsValue['ProcessType'] === 'Regular'}
                    disabled={radioButtonsValue['ProcessType'] !== 'Regular'}
                    control={<Radio color='primary' />}
                    label='Regular'
                  />
                </RadioGroup>
              </FormControl>
            </div>
          </div>
        </div>
        <div className={classes.SecondPartInputFields}>
          <div id='firstInputLayerFields' className={classes.firstInputLayerFields}>
            <div>
              <label>
                Hospital Address <br />
                Unit No./Floor/Bldg.No.
              </label>
              <TextField value={formData['address'] && formData['address']['unit_flr_bldg']} />
            </div>
            <div>
              <label>
                <div /> <br /> Building Name
              </label>
              <TextField value={formData['address'] && formData['address']['building_name']} />
            </div>
            <div>
              <label>
                <div /> <br /> Street Name *
              </label>
              <TextField value={formData['address'] && formData['address']['street']} />
            </div>
          </div>
          <div id='SedcondInputLayerFields' className={classes.SecondInputLayerFields}>
            <div>
              <label>Subdivision</label>
              <TextField value={formData['address'] && formData['address']['subdivision']} />
            </div>
            <div> *
              <label>Region *</label>
              <TextField value={formData['address'] && formData['address']['region']} />
            </div>
            <div>
              <label>District / Province *</label>
              <TextField value={formData['address'] && formData['address']['district']} />
            </div>
          </div>
          <div id='ThirdInputLayerFields' className={classes.ThirdInputLayerFields}>
            <div>
              <label>Barangay *</label>
              <TextField value={formData['address'] && formData['address']['barangay']} />
            </div>
            <div>
              <label>City / Municipality *</label>
              <TextField value={formData['address'] && formData['address']['city']} />
            </div>
            <div>
              <label>Zip Code *</label>
              <TextField value={formData['address'] && formData['address']['zip_code']} />
            </div>
          </div>
        </div>
        <div className={classes.ContactInformationBoard}>
          <div>
            <div>Contact Information</div>
            <Paper className={classes.ContactInformationTable} elevation={3}>
              <AffiliationTableDisplay header={affiliationTableHeader} data={listOfContacts} />
            </Paper>
          </div>
        </div>
        <div className={classes.AffiliateForm}>
          <div>Affiliate Information</div>
          <div>
            <Paper className={classes.AffiliationInformationTable} elevation={3}>
              <AffiliationTableDisplay header={existingAffilitaionHmoHeader} data={listOfAffiliatedGroup} />
            </Paper>
          </div>
        </div>
        <div className={classes.RemarksAndFooter}>
          <TwoTextArea
            val1={formData['remarks'] && formData['remarks']['external_remarks']}
            label1={'External Remarks'}
            label2={'Internal Remarks *'}
            inputRef={inputRef}
            setDisabled={setDisabled}
            isApproved={isApproved}
            remarks={remarks}
          />
        </div>
      </div>
    </React.Fragment>
  )
}

export default AffiliationForm
