import React from 'react'
import classes from './index.module.scss'

import Button from '@material-ui/core/Button'
import Dialog from '@material-ui/core/Dialog'
import DialogActions from '@material-ui/core/DialogActions'
import DialogContent from '@material-ui/core/DialogContent'
import TextField from '../../../Components/JoiValidationInputField'
import DialogTitle from '@material-ui/core/DialogTitle'
import { useDispatch } from 'react-redux'
import { SET_OPEN_MODAL, SET_NEW_DATA, SET_ADD_SUCCESS } from '../../../../Store/actions/Partner/MaintenanceTable'
import AddRoomTypeValidation from '../Validation'
import FormHelperText from '@material-ui/core/FormHelperText'
import useAsyncHook from '../../../CustomHooks/useAsyncHook2'
// import { LoadingService } from 'SharedComponents/LoadingComponent/LoadingComponent'
import RoomTypeService from '../../../../../../Services/Api/RoomType/RoomType'
import { getNestedArrayOfObjects, getNestedObjects } from '../../../Components/Utils/utils'
import CustomAlerSnackBar from '../../../Components/CustomSnackBar/CustomAlertSnackbar'

const AddRoomTypeComponents = props => {
  const { stateValue = {} } = props
  // const stateValue = useSelector(state => state.MaintenanceTable)
  const dispatch = useDispatch()

  const [errors, setErrors] = React.useState({})
  const [form, setForm] = React.useState({
    desc: '',
    remarks: '',
    code: ''
  })

  const [dataAdded, errorGetSave, loadingGetAdded, executeAsyncAdd] = useAsyncHook((data, token) => {
    const argsData = data.data
    return RoomTypeService.AddRoomType(argsData)
  })

  const HandleSubmit = e => {
    e.preventDefault()
    validate()
  }

  const validate = React.useCallback(() => {
    const { errors, isValid } = AddRoomTypeValidation(form)
    if (isValid) {
      setErrors({})
      save()
    } else {
      setErrors(errors)
    }
  }, [form])

  const HandleChange = e => {
    const { name, value } = e.target
    const ctr = stateValue.totalItems + 1
    const contactForm = { ...form }
    contactForm[name] = value
    setForm(contactForm)
    const pad = '0000'
    const counterText = (pad + ctr).slice(-pad.length)

    setForm((previousState) => ({
      ...previousState, 'code': 'RM' + counterText
    }))
  }

  const save = async () => {
    await executeAsyncAdd({
      data: form
    })
  }

  React.useEffect(() => {
    const message = getNestedObjects(dataAdded, ['message'])
    if (loadingGetAdded && message === 'Created successfully') {
      const newData = getNestedArrayOfObjects(dataAdded, ['data'])
      dispatch(SET_NEW_DATA(newData))
      dispatch(SET_OPEN_MODAL(false))
      dispatch(SET_ADD_SUCCESS(true))
    }
  }, [dataAdded])

  const HandleCancel = () => {
    dispatch(SET_OPEN_MODAL(false))
    setForm({
      desc: '',
      remarks: '',
      code: ''
    })
    setErrors({})
  }

  return (
    <React.Fragment>
      <Dialog fullWidth maxWidth={'md'} open={stateValue.openModal} aria-labelledby='dialogTitle'>
        <DialogTitle id='dialogTitle' style={{ color: '#9ea3ac', marginTop: '2%' }}>ADD ROOM TYPE</DialogTitle>
        <DialogContent>
          <form className={classes.root} onSubmit={HandleSubmit} noValidate autoComplete='off'>
            <div id='firstInput' className={classes.firstInput}>
              <div id='roomTypeDescription'>
                <TextField name='desc' onChange={HandleChange} label='Room Type Description*' />
                <FormHelperText style={{ color: 'red' }}>{errors['desc']} </FormHelperText>
              </div>
              <div id='remarks'>
                <TextField name='remarks' onChange={HandleChange} label='Remarks*' />
                <FormHelperText style={{ color: 'red' }}>{errors['remarks']} </FormHelperText>
              </div>
            </div>
          </form>
        </DialogContent>
        <DialogActions className={classes.DialogActions}>
          <Button
            onClick={HandleCancel}
            variant='contained'
            className={classes.Cancel}
          >
            CANCEL
          </Button>
          <Button variant='contained'
            onClick={HandleSubmit}
            className={classes.Add}>
            CREATE
          </Button>
        </DialogActions>
      </Dialog>
      <CustomAlerSnackBar isSave={stateValue.addSucces} setIsSave={() => dispatch(SET_ADD_SUCCESS(false))} />
    </React.Fragment>
  )
}

export default AddRoomTypeComponents
