import React from 'react'
import { Paper } from '@material-ui/core'
import StatusSelect from '../SelectInput'
import InputFields from '../InputField'
import CustomInputData from '../CustomInputData'
import classes from './index.module.scss'

const InputData = props => {
  const {
    name = '',
    value = '',
    onChange = null,
    size = '',
    inputType = '',
    type = '',
    facilityType = '',
    uniqueId = '',
    multiline = null,
    itemIndex = null,
    customInput = false
  } = props

  return (
    <div style={{ width: size || 'auto', height: 'auto' }}>
      <Paper id='paper' className={classes.paper} style={{width: size}}>
        {
          inputType === 'dropDown'
            ? <StatusSelect
              name={name}
              value={value}
              onChange={onChange}
              size={size}
            />
            : customInput
              ? <CustomInputData
                name={name}
                value={value}
                onChange={onChange}
                size={size}
                type={type}
                facilityType={facilityType}
                uniqueId={uniqueId}
                itemIndex={itemIndex}
                multiline={multiline}
              />
              : <InputFields
                {...props}
                type={type}
                multiline={multiline}
              />
        }

      </Paper>
    </div>

  )
}

export default React.memo(InputData, (prevProps, nextProps) => {
  try {
    if (prevProps.name === nextProps && prevProps.value === nextProps.value && prevProps.onChange === nextProps.onChange) {
      return true
    }
    return false
  } catch (e) {
    return false
  }
})
