import React, { useState, useEffect, memo } from 'react'

import RemarksComponent from 'Pages/Protected/Partner/AffiliationFormEncoder/Components/RemarksComponent'

import DoctorAffiliatedGroup from './DoctorAffiliatedGroup'

// components
import DoctorSectionOne from './SectionOne/DoctorSectionOne'
import TinStatusComponent from 'Pages/Protected/Partner/AffiliationFormEncoder/Components/TinStatusComponent'
import DoctorSectionTwo from './SectionTwo/DoctorSectionTwo'
import getDoctor from '../Utilities/doctorEncoderSrvc'
import classes from './AffiliationForm.module.scss'

const AffiliationForm = props => {
  const {
    isSubmitted,
    isTovalidate,
    affiliationType,
    setValid,
    setForm,
    sectionOneData,
    sectionTwoData,
    statusData,
    remarksData,
    affilInfoData
  } = props
  const [doctorSectionOneIsValid, setDoctorSectionOneIsValid] = useState(false)
  const [doctorSectionTwoIsValid, setDoctorSectionTwoIsValid] = useState(false)
  const [statusIsValid, setStatusIsValid] = useState(false)
  const [remarksIsValid, setIsRemarksIsValid] = useState(false)
  const [statusForm, setStatusForm] = useState({})
  const [doctorSectionOne, setDoctorSectionOne] = useState({})
  const [doctorSectionTwo, setDoctorSectionTwo] = useState({})
  const [affilInfo, setAffilInfo] = useState({})
  const [remarksForm, setRemarksForm] = useState({})
  const [isAddedType, setIsAddedType] = React.useState(false)
  const [formOneDefaultValue, setFormOneDefaultValue] = useState({})
  const [formStatusDefaultValue, setFormStatusDefaultValue] = useState({})
  const [medicalDoctorId, setMedicalDoctorId] = useState('')

  useEffect(() => {
    const isValid = doctorSectionOneIsValid && doctorSectionTwoIsValid && statusIsValid && remarksIsValid
    if (isValid) {
      setValid(true)
    } else {
      setValid(false)
    }
  }, [doctorSectionOneIsValid, doctorSectionTwoIsValid, statusIsValid, remarksIsValid])

  useEffect(() => {
    const statusForm = { ...formStatusDefaultValue }
    setFormStatusDefaultValue({...statusForm, ...statusData})
  }, [statusData])

  useEffect(() => {
    const formData = {...statusForm, ...doctorSectionOne, ...doctorSectionTwo, ...remarksForm, ...affilInfo}
    setForm(formData)
  }, [doctorSectionOne, statusForm, doctorSectionTwo, remarksForm, affilInfo])

  useEffect(() => {
    const formOne = {...formOneDefaultValue}
    setFormOneDefaultValue({ ...formOne, ...sectionOneData })

    if (affiliationType['affiliationType'] === 'additional_affiliation') {
      setIsAddedType(true)
    }
    if (affiliationType['id']) {
      setMedicalDoctorId(affiliationType['id'])
    }
  }, [])

  const handlePartnerId = async ({ id }) => {
    try {
      let { obj, statusForm } = await getDoctor(id)

      setFormOneDefaultValue(obj)
      setFormStatusDefaultValue(statusForm)
    } catch (error) {
      console.log('partner id error', error)
    }
  }

  return (
    <div id='root' className={classes.root}>
      <div id='topText' className={classes.topText}>
        DOCTOR INFORMATION
      </div>

      <DoctorSectionOne
        setIsValid={setDoctorSectionOneIsValid}
        isValidate={isTovalidate}
        isValid={doctorSectionOneIsValid}
        isAddedType={isAddedType}
        affiliationType={affiliationType}
        data={doctorSectionOne}
        setData={setDoctorSectionOne}
        setStatusForm={setStatusForm}
        handlePartnerId={handlePartnerId}
        defaultVal={formOneDefaultValue}
        partnerId={medicalDoctorId}
        isSubmitted={isSubmitted}
      />

      <TinStatusComponent
        data={statusForm}
        setData={setStatusForm}
        setIsValid={setStatusIsValid}
        isValid={statusIsValid}
        isValidate={isTovalidate}
        defaultVal={formStatusDefaultValue}
        isSubmitted={isSubmitted || isAddedType}
      />
      <DoctorSectionTwo
        setIsValid={setDoctorSectionTwoIsValid}
        isValidate={isTovalidate}
        isValid={doctorSectionTwoIsValid}
        data={doctorSectionTwo}
        setData={setDoctorSectionTwo}
        defaultVal={sectionTwoData}
        isSubmitted={isSubmitted}
      />
      <DoctorAffiliatedGroup isSubmitted={isSubmitted} data={affilInfo} setData={setAffilInfo} defaultVal={affilInfoData} />
      <RemarksComponent
        data={remarksForm}
        setData={setRemarksForm}
        isValid={remarksIsValid}
        setIsValid={setIsRemarksIsValid}
        isValidate={isTovalidate}
        defaultVal={remarksData}
        isSubmitted={isSubmitted}
        label1='Applicant Remarks'
        label2='Intellicare Remarks'
        name1='applicantRemarks'
        name2='intellicareRemarks'
        hasPaddingLeft={false}
      />
    </div>
  )
}

export default memo(AffiliationForm)
