import instance from '../instance'
// import axios from 'axios'

const axios = instance('/p/f')

export default class ListOfAffiliatedDoctors {
  static saveAffiliatedDoctors (argParams, argCancelToken) {
    const data = { data: argParams.data, id: argParams.id, type: argParams.type }
    return axios({
      url: '/partners/facilities/profile/affiliated-doctor',
      method: 'PUT',
      headers: { skip_generate_form_data: 1 },
      data
    })
  }

  static getAffiliatedDoctors (argParams, argCancelToken) {
    const params = {
      id: argParams.id,
      type: argParams.type,
      offset: argParams.offset,
      limit: argParams.limit,
      filter: argParams.filter
    }

    return axios({
      url: `/partners/facilities/profile/affiliated-doctor/${params.id}`,
      method: 'GET',
      headers: { skip_generate_form_data: 1 },
      params,
      cancelToken: argCancelToken.cancelToken
    })
  }
}
