import React, { memo, useState, useEffect } from 'react'
import classes from './DoctorSectionOne.module.scss'
import TextField from 'Pages/Protected/Partner/AffiliationFormEncoder/Components/EncoderTextField'
import EncoderInputFieldNumberFormat from 'Pages/Protected/Partner/AffiliationFormEncoder/Components/EncoderInputFieldNumberFormat'
import MedicalPartnerId from './components/MedicalPartnerID'
import DoctorSpecializations from './components/DoctorSpecialization'
import isEmpty from 'lodash/isEmpty'
import DatePicker from 'SharedComponents/DatePicker2/DatePicker2'
import DropDown from '../../../../../Components/NativeDropDown'
import {
  suffixList,
  genderList,
  civilStatusList
} from 'Pages/Protected/Partner/Constants/dropDownItems'

import validation from './validation'

const DoctorSectionOne = (props) => {
  const { data, isSubmitted = false, setData, setIsValid, isValidate, defaultVal, isAddedType = false, affiliationType = {}, handlePartnerId, partnerId } = props
  const [form, setForm] = useState({
    prcExpiration: null,
    philhealthExpiration: null,
    philhealthNumber: '',
    birthDate: null
  })

  const [firstPartSpecialization , setFirstPartSpecialization] = useState({
    mainSpecialization: '',
    subSpecialization: ''
  })

  const [secondPartSpecialization , setSecondPartSpecialization] = useState({
    majorSpecialization: '',
    otherSpecialization: ''
  })
  const [errors, setError] = useState({})

  useEffect(() => {
    const newData = { ...data }
    setData({...newData, ...form })
  }, [form])

  useEffect(() => {
    const newData = { ...form }
    setForm({...newData, ...firstPartSpecialization, ...secondPartSpecialization })
  }, [firstPartSpecialization, secondPartSpecialization])

  useEffect(() => {
    const newData = { ...form }
    const fps = { ...firstPartSpecialization }
    const sps = { ...secondPartSpecialization }
    fps.mainSpecialization = defaultVal['mainSpecialization']
    fps.subSpecialization = defaultVal['subSpecialization']
    sps.majorSpecialization = defaultVal['majorSpecialization']
    sps.otherSpecialization = defaultVal['otherSpecialization']
    setFirstPartSpecialization(fps)
    setSecondPartSpecialization(sps)
    setForm({...newData, ...defaultVal})
  }, [defaultVal])

  useEffect(() => {
    if(isValidate) {
      validate()
    }
  }, [isValidate])

  const validate = () => {
    const {newErrors, isValid } = validation(form, isAddedType)
    const nErrors = { ...errors }
    if (isValid) {
      return true
    } else {
      setError(Object.assign( nErrors, newErrors))
      return false
    }
  }

  const isValidForm = React.useMemo(() => {
    const nErrors = { ...errors }
    const { newErrors } = validation(form, isAddedType)
    let isValid = isEmpty(Object.assign(nErrors, newErrors))
    if (isValid) {
      return true
    } else {
      return false
    }
  }, [form, errors])

  useEffect(() => {
    setIsValid(isValidForm)
  }, [isValidForm])

  const handleDropdown = ({ target: {name, value} }) => {
    const nform = { ...form }
    nform[name] = value
    delete errors[name]
    setError(errors)
    setForm(nform)
  }

  const handleOnChange = ({target: { value, name }}) => {
    const e = {...errors}
    let nform = {...form}
    nform[name] = value
    delete e[name]
    setError(e)
    setForm(nform)
  }

  const handleDatePicker = async (date, name) => {
      const nForm = { ...form }
    let newError = { ...errors }
    let newDate = (await utils()).dateFormat(date)
    if (typeof date._i === 'number') {
      nForm[name] = null
      delete newError[name]
      setError(newError)
    } else if (!date._isValid) {
        newError[name] = 'Invalid Date'
        setError(newError)
      } else {
        nForm[name] = newDate
        delete newError[name]
        setError(newError)
      }
      setForm(nForm)

  }

  const utils = async () => {
    let util = await import('Pages/Protected/Partner/Components/DatePicker/DatePickerUtils')
    return util
  }

  return (
    <>
      <MedicalPartnerId data={form} isSubmitted={isSubmitted} setData={setForm} isAddedType={isAddedType} partnerId={partnerId} handlePartnerId={handlePartnerId} affiliationType={affiliationType} />
      <div id='GeneralInformation' className={classes.GeneralInformationField}>
      <div id='generalInfoText' className={classes.generalInfoText}>
        General Information
      </div>
      <div id='generalInfoFields' className={classes.generalInfoFields}>
        <div id='firstPartField' className={classes.firstPartField}>
          <div id='11' className={classes.InputContainer}>
            <label>First Name *</label>
            <TextField
              disabled={isSubmitted || isAddedType}
              name='firstName'
              id='firstName'
              error={errors['firstName']}
              defaultValue={defaultVal['firstName'] || form['firstName']}
              type={'noSpecialChar'}
              onChange={handleOnChange}
            />
          </div>
          <div id='12' className={classes.InputContainerTwo}>
            <div id='Suffix-div' className={classes.suffix}>
              <label>Suffix</label>
              <DropDown
                disabled={isSubmitted || isAddedType}
                name='suffix'
                items={suffixList}
                id='Suffix'
                error={errors['suffix']}
                value={form['suffix']}
                onChange={handleDropdown}
              />
            </div>
            <div className={classes.birthDate}>
              <label>Birthdate *</label>
              <DatePicker
                error={errors['birthDate']}
                id='birthDate'
                onChange={(date) => handleDatePicker(date, 'birthDate', true, true)}
                value={form['birthDate']}
                invalidDateMessage={false}
                maxDateMessage={false}
                minDateMessage={false}
                disabled={isSubmitted || isAddedType}
                disableFuture
              />
            </div>
          </div>
          <div id='13' className={classes.InputContainer}>
            <label>Email Address *</label>
            <TextField
              disabled={isSubmitted || isAddedType}
              type={'email'}
              id='emailAddress'
              error={errors['emailAddress']}
              name='emailAddress'
              onChange={handleOnChange}
              defaultValue={defaultVal['emailAddress'] || form['emailAddress']}
            />
          </div>
          <DoctorSpecializations
            isAddedType={isAddedType}
            form={firstPartSpecialization}
            setForm={setFirstPartSpecialization}
            setError={setError}
            errors={errors}
            disabled={isSubmitted || isAddedType}
          />
          <div id='16' className={classes.InputContainer}>
            <label>PMA Number</label>
            <TextField
              disabled={isSubmitted || isAddedType}
              type={'noSpecialChar'}
              defaultValue={defaultVal['pmaNumber'] || form['pmaNumber']}
              id='pmaNumber'
              name='pmaNumber'
              error={errors['pmaNumber']}
              onChange={handleOnChange}
            />
          </div>
          <div id='17' className={classes.InputContainer}>
            <label>PRC License No. *</label>
            <TextField
              disabled={isSubmitted || isAddedType}
              type={'noSpecialChar'}
              defaultValue={defaultVal['prcNumber'] || form['prcNumber']}
              id='prcNumber'
              name='prcNumber'
              error={errors['prcNumber']}
              onChange={handleOnChange}
            />
          </div>
          <div id='18' className={classes.InputContainer}>
            <label>TIN Number *</label>
            <EncoderInputFieldNumberFormat
              id='tinNumber'
              name='tinNumber'
              placeholder='000-000-000-000'
              defaultValue={form['tinNumber'] && form['tinNumber']}
              error={errors['tinNumber']}
              disabled={isSubmitted || isAddedType}
              format='###-###-###-###'
              onChange={handleOnChange}
            />
          </div>
        </div>
        <div id='secondPartField' className={classes.secondPartField}>
          <div id='21' className={classes.firstTextField}>
            <div id='middle-name' className={classes.middleName}>
              <label>Middle Name *</label>
              <TextField
                disabled={isSubmitted || isAddedType}
                error={errors['middleName']}
                id='middleName'
                name='middleName'
                type={'noSpecialChar'}
                defaultValue={defaultVal['middleName'] || form['middleName']}
                onChange={handleOnChange}
              />
            </div>
            <div id='last-name' className={classes.lastName}>
              <label>Last Name *</label>
              <TextField
                disabled={isSubmitted || isAddedType}
                error={errors['lastName']}
                id='lastName'
                name='lastName'
                type={'noSpecialChar'}
                defaultValue={defaultVal['lastName'] || form['lastName']}
                onChange={handleOnChange}
              />
            </div>
          </div>
          <div id='22' className={classes.secondTextField} style={{paddingBottom: errors.civilStatus || errors.gender ? 0 : '1.6em'}}>
            <div id='civil-status' className={classes.civilStatus}>
              <label>Civil Status *</label>
              <DropDown
                disabled={isSubmitted || isAddedType}
                items={civilStatusList}
                value={form['civilStatus']}
                error={errors['civilStatus']}
                id='civilStatus'
                name='civilStatus'
                onChange={handleDropdown}
              />
            </div>
            <div id='gender' className={classes.gender}>
              <label>Gender *</label>
              <DropDown
                disabled={isSubmitted || isAddedType}
                items={genderList}
                error={errors['gender']}
                id='gender'
                name='gender'
                value={form['gender']}
                onChange={handleDropdown}
              />
            </div>
          </div>
          <div id='23' className={classes.textField}>
            <label>Contact Number (phone/mobile) *</label>
            <TextField
              disabled={isSubmitted || isAddedType}
              type={'integerOnly'}
              error={errors['contactNumber']}
              id='contactNumber'
              name='contactNumber'
              defaultValue={defaultVal['contactNumber'] || form['contactNumber']}
              onChange={handleOnChange}
            />
          </div>
          <DoctorSpecializations
            label1 = 'Major Specialization *'
            label2 = 'Other Specialization'
            name1 = 'majorSpecialization'
            name2 = 'otherSpecialization'
            form={secondPartSpecialization}
            setForm={setSecondPartSpecialization}
            disabled={isSubmitted || isAddedType}
            isAddedType={isAddedType}
            isSecondRow
            setError={setError}
            errors={errors}
          />

           <div id='26' className={classes.ptrNumberAndS2Number}>
            <div id='PTR-number-label'>
              <label>PTR Number</label>
              <TextField
                disabled={isSubmitted || isAddedType}
                type={'noSpecialChar'}
                id='ptrNumber'
                name='ptrNumber'
                error={errors['ptrNumber']}
                defaultValue={defaultVal['ptrNumber'] || form['ptrNumber']}
                onChange={handleOnChange}
              />
            </div>
            <div id='S2-number-label'>
              <label>S2 Number</label>
              <TextField
                disabled={isSubmitted || isAddedType}
                type={'noSpecialChar'}
                defaultValue={defaultVal['s2Number'] || form['s2Number']}
                id='s2Number'
                name='s2Number'
                error={errors['s2Number']}
                onChange={handleOnChange}
              />
            </div>
          </div>
          <div id='27' className={classes.textFieldforPRCfield}>
            <div id='PRC-Expiry-date'>
              <label>PRC Expiry Date *</label>
              <DatePicker
                error={errors['prcExpiration']}
                id='prcExpiration'
                name='prcExpiration'
                onChange={(date) => handleDatePicker(date, 'prcExpiration')}
                value={form['prcExpiration']}
                invalidDateMessage={false}
                maxDateMessage={false}
                minDateMessage={false}
                disabled={isSubmitted || isAddedType}
              />
            </div>
            <div id='PhilHealth-Acrreditation-No'>
              <label>PhilHealth Accreditation No. </label>
              <EncoderInputFieldNumberFormat
                id='philhealthNumber'
                defaultValue={form['philhealthNumber']}
                error={errors['philhealthNumber']}
                name='philhealthNumber'
                disabled={isSubmitted || isAddedType}
                placeholder='0000-0000000-0'
                format='####-#######-#'
                onChange={handleOnChange}
              />
            </div>
            <div id='Philhealth-EXPIRY-DATE'>
              <label>Philhealth Expiry Date</label>
              <DatePicker
                error={errors['philhealthExpiration']}
                id='philhealthExpiration'
                name='philhealthExpiration'
                onChange={(date) => handleDatePicker(date, 'philhealthExpiration')}
                value={form['philhealthExpiration']}
                disabled={isSubmitted || isAddedType}
                invalidDateMessage={false}
                maxDateMessage={false}
                minDateMessage={false}
                clearable={false}
              />
            </div>
          </div>
          <div id='28' className={classes.textField}>
            <label>Check Payee *</label>
            <TextField
              disabled={isSubmitted || isAddedType}
              id='payee'
              name='payee'
              onChange={handleOnChange}
              // type={'noSpecialChar'}
              defaultValue={defaultVal['payee'] || form['payee']}
              error={errors['payee']}
            />
          </div>
        </div>
      </div>
    </div>
    </>
  )

}

export default memo(DoctorSectionOne)
