
import { YearDateFormat, fetchAddress, OfficeLocationDefault, fetchRegion, getNestedObjects, getNestedArrayOfObjects } from '../../../../../../Components/Utils/utils'

const Destructure = (form, profile, dateFormat, setStatus, setForm, setBarangays, setCities, setProvinces, setRegions, setIsFetching, setAddressFetch) => {
  const newForm = { ...form }

  const assignment = getNestedArrayOfObjects(profile, ['facility_assignment', 'assignments'])
  const medicalPartnerId = getNestedObjects(profile, ['id'])
  const hospitalName = getNestedObjects(profile, ['name'])
  newForm['medicalPartnerId'] = medicalPartnerId
  newForm['formerName'] = getNestedObjects(profile, ['former_name'])
  newForm['name'] = hospitalName
  newForm['officeLocation'] = OfficeLocationDefault(getNestedObjects(profile, ['office_location']))
  newForm['noOfBeds'] = +getNestedObjects(profile, ['bed_capacity'])
  newForm['dateOfDisaffiliation'] = profile['disaffiliation'] ? YearDateFormat(profile['disaffiliation']['date']) : ''
  newForm['dateOfAffiliation'] = profile['affiliation_status']
    ? YearDateFormat(profile['affiliation_status']['date_submitted'])
    : ''

  newForm['district'] = getNestedObjects(profile, ['address', 'district'])
  newForm['street'] = getNestedObjects(profile, ['address', 'street'])
  newForm['zipCode'] = getNestedObjects(profile, ['address', 'zip_code'])
  newForm['region'] = getNestedObjects(profile, ['address', 'region'])
  newForm['city'] = getNestedObjects(profile, ['address', 'city'])
  newForm['barangay'] = getNestedObjects(profile, ['address', 'barangay'])
  newForm['processType'] = getNestedObjects(profile, ['process_type'])
  newForm['classification'] = getNestedObjects(profile, ['classification'])
  newForm['philhealthNumber'] = getNestedObjects(profile, ['philhealth', 'accreditation_number']) // profile
  newForm['philhealthExpiration'] = profile
    ? profile['philhealth']
      ? profile['philhealth']['expiration_date']
        ? YearDateFormat(profile['philhealth']['expiration_date'])
        : ''
      : ''
    : ''
  newForm['tinStatus'] = getNestedObjects(profile, ['treasury', 'tin_status'])
  newForm['non_vatable'] = getNestedObjects(profile, ['treasury', 'non_vatable'])
  newForm['payee'] = getNestedObjects(profile, ['treasury', 'payee'])
  newForm['creditLimit'] = getNestedObjects(profile, ['treasury', 'credit_limit'])
  newForm['vatTaxRate'] = getNestedObjects(profile, ['treasury', 'vat', 'tax_rate'])
  newForm['facilityDiscount'] = getNestedObjects(profile, ['treasury', 'discount'])
  newForm['paymentTerms'] = getNestedObjects(profile, ['treasury', 'payment_terms'])
  newForm['specialInstructions'] = getNestedObjects(profile, ['treasury', 'special_instruction'])
  newForm['assignments'] = assignment
  newForm['status'] = getNestedObjects(profile, ['status'])
  newForm['exclusiveTo'] = getNestedObjects(profile, ['facility_assignment', 'exclusive_to'])
  newForm['tvs'] = getNestedObjects(profile, ['tvs'])
  newForm['reason'] = getNestedObjects(profile, ['reason'])

  newForm['facilityAssignment'] = assignment.length > 0 ? 'Assigned' : 'none'

  const boolean = typeof dateFormat

  if (boolean === 'boolean') {
  } else {
    setStatus(profile['status'])
    setForm(newForm)
    fetchAddress(newForm, setBarangays, setCities, setProvinces, setIsFetching, setAddressFetch)
    fetchRegion(setRegions)
  }

  return newForm
}

export default Destructure
