import React from 'react'
import { Switch, Route } from 'react-router-dom'
import { Provider } from 'react-redux'
import PFmultiplier from './index'

// Store
import Store from '../../../Store'

const PFmultiplierComponent = () => {
  const [isOpenPFmultiplier, setIsOpenPFmultiplier] = React.useState(false)

  React.useEffect(() => {
    setIsOpenPFmultiplier(true)
    return () => {
      setIsOpenPFmultiplier(false)
    }
  }, [])

  return (
    <Provider store={Store}>
      <Switch>
        <Route path='/PFMultiplier' exact render={(props) => <PFmultiplier {...props} open={isOpenPFmultiplier} setOpen={setIsOpenPFmultiplier} />} />
      </Switch>
    </Provider>
  )
}

export default PFmultiplierComponent
