import React from 'react'
import ReportServices from '../../../../../Services/Api/Reports/Reports'
import { LoadingService } from 'SharedComponents/LoadingComponent/LoadingComponent'
import classes from '../scss/medicalClinic.module.scss'
import Button from '@material-ui/core/Button'
import PreviewDownload from '../Helpers/PreviewDownload'
import moment from 'moment'
import DropDown from '../../Components/CustomDrowpdown'
import ReportsTable from '../TableComponent'
import { getNestedObjects } from 'Pages/Protected/Partner/Components/Utils/utils'
import { contractRenewalHeaders } from '../Helpers/ReportsTableHeaderData'
import { previewContractRenewal } from '../Helpers/tableService'

const ContractRenewal = () => {
  const [contractRenewal, setContractRenewal] = React.useState([])
  const [downloadData, setDownloadData] = React.useState([])
  const [openPreview, setOpenPreview] = React.useState(false)
  const [categoryData, setCategoryData] = React.useState('HOSPITAL')
  const [categoryType, setCatergoryType] = React.useState('HOSPITAL')
  const [datas, setDatas] = React.useState([])
  const [tableData, setTableData] = React.useState([])
  const [filters, setFilters] = React.useState({})

  React.useEffect(() => {
    fetchData()
  }, [datas])

  const fetchData = async () => {
    LoadingService.loading(true)

    try {
      const newDatas = [...datas]
      const newMapData = newDatas.map(items => {
        return {
          status: getNestedObjects(items, ['status']),
          id: getNestedObjects(items, ['id']),
          full_address: getNestedObjects(items, ['full_address']),
          expiration_date: getNestedObjects(items, ['expiration_date']),
          name: getNestedObjects(items, ['name'])
        }
      })
      setTableData(newMapData)
      LoadingService.loading(false)
    } catch (e) {
      console.log('error', e)
    }
  }
  const categories = [
    { id: 1, text: 'Hospital', value: 'Hospital' },
    { id: 2, text: 'Medical Clinic', value: 'Medical Clinic' },
    { id: 3, text: 'Medical Doctor', value: 'Medical Doctor' },
    { id: 4, text: 'Dental Clinic', value: 'Dental Clinic' },
    { id: 5, text: 'Medical Dentist', value: 'Medical Dentist' }
  ]

  React.useEffect(() => {
    switch (categoryData) {
      case 'Hospital':
        setCatergoryType('HOSPITAL')
        break
      case 'Medical Clinic':
        setCatergoryType('CLINIC')
        break
      case 'Medical Doctor':
        setCatergoryType('DOCTOR')
        break
      case 'Dental Clinic':
        setCatergoryType('DENTAL_CLINIC')
        break
      case 'Medical Dentist':
        setCatergoryType('DENTIST')
        break
      default:
        setCatergoryType('HOSPITAL')
        break
    }
  }, [categoryData])

  const handleDropdown = async ({ target: select }) => {
    setCategoryData(select.value)
  }

  const headerProps = React.useMemo(() =>
    [
      {
        id: 'Status',
        accessor: 'status',
        sortable: true,
        searchable: true,
        size: '180px'
      },
      {
        id: 'Medical Partner ID',
        accessor: 'id',
        sortable: true,
        searchable: true,
        size: '180px'
      },
      {
        id: 'Partner Name',
        accessor: 'name',
        sortable: true,
        searchable: true,
        size: '180px'
      },
      {
        id: 'Location',
        accessor: 'full_address',
        sortable: true,
        searchable: true,
        size: '200px'
      },
      {
        id: 'Date Of Expiration',
        accessor: 'expiration_date',
        sortable: true,
        searchable: true,
        size: '170px'
      }
    ], [])

  return (
    <React.Fragment>
      <div className={classes.root}>
        <div style={{ display: 'flex', flexDirection: 'column', width: '100%' }}>
          <div style={{ width: '200px', marginBottom: '3em' }}>
            <DropDown
              onChange={handleDropdown}
              items={categories}
              value={categoryData}
              label='Medical Partner Category'
            />
          </div>
          <div id='root' className={classes.tableContainer}>
            <ReportsTable
              getData={(data, token) => {
                const args = { ...data }
                return ReportServices.getContractRenewalData(args, token)
              }}
              setDatas={setDatas}
              type={categoryType}
              headerProps={headerProps}
              datas={tableData}
              setFilters={setFilters}
              contractRenewal
            />
          </div>
        </div>
      </div>
      <div className={classes.previewButton}>
        <Button onClick={() => setOpenPreview(true)} variant='contained' className={classes.DownloadPreviewBtn}>
          Download
        </Button>
        <PreviewDownload
          openPreview={openPreview}
          setOpenPreview={setOpenPreview}
          headerColumn={contractRenewalHeaders}
          filter={filters}
          filename={`${moment().format('YYYYMMDD')}-contract-renewal.csv`}
          infiniteScrollService={previewContractRenewal}
          type={categoryType}
          downloadFunc={ReportServices.downloadContractRenewal}
        />
      </div>
    </React.Fragment>
  )
}

export default ContractRenewal
