import React from 'react'
import { TableRow, TableCell } from '@material-ui/core'
import InputData from '../Inputs/inputData'
import classes from './index.module.scss'

const DataComponents = props => {
  const { dispatch = null, setUpdate = null, stateValue = {} } = props

  const { GetKeys = [], tableDataProps = '' } = props

  const handleChange = React.useCallback((e, index) => {
    const { name, value } = e.target

    dispatch(setUpdate({
      name,
      value,
      index
    }))
  }, [])

  return (
    <React.Fragment>
      {
        tableDataProps.map((bodyItems, index) => {
          const keys = GetKeys()
          const dataIndex = index

          return (
            <TableRow key={index} className={classes.TableRow}>
              {
                keys.map((items, index) => {
                  return (
                    <TableCell
                      component='th'
                      scope='row'
                      align='left'
                      key={`${index}-cell`}
                      className={classes.TableCell}
                      style={{width: items.size, overflowWrap: 'break-word'}}>
                      {stateValue.isEdit ? items.editable
                        ? <InputData
                          name={items.id}
                          value={bodyItems[items.id]}
                          onChange={e => handleChange(e, dataIndex)}
                          size={items.size}
                          inputType={items.inputType}
                          type={items.type}
                          facilityType={bodyItems.facility_type}
                          multiline={items.multiline}
                          customInput={items.customInput}
                          uniqueId={bodyItems._id}
                          itemIndex={dataIndex}
                        />
                        : <div id='data-cell' className={classes.data}>
                          { bodyItems[items.id]}
                        </div>
                        : <div id='data-cell-two' className={classes.data}>
                          {
                            bodyItems[items.id] instanceof Array
                              ? bodyItems[items.id].map(items => ` ${items.name}`).join(',')
                              : bodyItems[items.id]
                          }
                        </div>

                      }
                    </TableCell>

                  )
                })
              }

            </TableRow>
          )
        })
      }

    </React.Fragment>
  )
}

export default React.memo(DataComponents)
