import React, { useState } from 'react'

import Table from '@material-ui/core/Table'
import TableBody from '@material-ui/core/TableBody'
import TableCell from '@material-ui/core/TableCell'
import TableContainer from '@material-ui/core/TableContainer'
import TableRow from '@material-ui/core/TableRow'
import classes from './UploadFIleTable.module.scss'
import Preview from '../../../PreviewImage/PreviewImage'
import UploadFileContext from '../../../../Components/context/fileUploadContext'
import { useSelector } from 'react-redux'
import validateForm from './validation'
import AffiliationEncoderService from 'Services/Api/Partner/affiliationEncoderService'

const LocationMapWithSubscript = () => {
  return (
    <div id='locationMapText' className={classes.WithSubScript}>
      Location Map *{' '}
      <sub id='subscript-location' className={classes.subscript}>
        *Preferably screenshot from google maps
      </sub>
    </div>
  )
}

const AcutalImageAreas = () => {
  return (
    <div id='ActualImageArea' className={classes.WithSubScript}>
      Actual Image of Areas in the Facility
      <br />
      <small id='subscript-ActualImageArea' className={classes.subscript}>
        *All facility department (Ancilliary Dept, Lab, Radiology, ER, Rehab, Others), All room types, Front Area/Front
        Image of the facility.
      </small>
    </div>
  )
}

const ApePackages = () => {
  return (
    <div id='ApePackages' className={classes.WithSubScript}>
      APE Packages
      <sub id='subscript-ApePackages' className={classes.subscript}>
        *Basic 5, ECG, PapSmear and Drugtest
      </sub>
    </div>
  )
}

const createData = (description, uploadButton) => {
  return { description, uploadButton }
}

const URL = `${process.env.REACT_APP_SERVER_API}`

const UploadFIleTable = props => {
  const { forView, setDisable, disable, setIsApproved, isClickedSave, hasPhilHealth, onPreviewFile } = props
  const [mainFiles, setMainFiles] = useState([])
  const { affiliationType } = useSelector(state => state.affiliationType)
  const [errors, setErrors] = React.useState({})
  const [form, setForm] = useState({
    'medical-clinic-profile': 0,
    'service-offered-with-rates': 0,
    'list-of-doctors-accredited': 0,
    'location-map': 0,
    'check-payable-letter': 0,
    'business-permit': 0,
    'bir-cor-2303': 0,
    'doh-permit': 0,
    'sec-dti-registration': 0,
    'philhealth-accreditation': 0
  })

  const validate = () => {
    const { errors, isValid } = validateForm(form, hasPhilHealth)
    if (isValid) {
      setErrors({})
      if (setDisable) {
        setDisable(false)
      }
    } else {
      setErrors(errors)
      if (setDisable) {
        setDisable(true)
      }
    }
  }

  React.useEffect(() => {
    if (
      affiliationType['affiliationStatus'] === 'Submitted' ||
      affiliationType['stageName'] === 'AFFILIATION_APPROVAL_FOR_OIC'
    ) {
      setDisable(true)
      setIsApproved(true)
    } else {
      validate()
    }
  }, [form, mainFiles, affiliationType.affiliationStatus, hasPhilHealth])

  React.useEffect(() => {
    if (props.affiliationTypeId) {
      fetchData(props.affiliationTypeId)
    }

    if (affiliationType.id) {
      fetchData()
    }
  }, [])

  const fetchData = async affiliationTypeId => {
    try {
      let urlRequest = affiliationType.id
      if (affiliationTypeId) {
        urlRequest = affiliationTypeId
      }
      const filesData = await AffiliationEncoderService.getUploadFile(urlRequest)
      const updateForm = { ...form }
      for (let fileData of filesData.data.data) {
        let tmpFiles = fileData.files
        setMainFiles(previousState => ({
          ...previousState,
          [fileData.file_type]: tmpFiles.map(file =>
            Object.assign(file, {
              preview: `${URL}/uploads/${file.path}`,
              fileType: fileData.file_type
            })
          )
        }))
        if (form && form.hasOwnProperty(fileData.file_type) && tmpFiles.length > 0) {
          updateForm[fileData.file_type] = tmpFiles.length
        }
      }
      setForm(updateForm)
    } catch (e) {
      console.log('error', e)
    }
  }

  const rows = [
    createData(
      <Preview
        onPreviewFile={onPreviewFile}
        desc={'Medical Clinic Profile *'}
        fileType={'medical-clinic-profile'}
        mainFiles={mainFiles}
        setMainFiles={setMainFiles}
        buttonText={'Upload'}
        forView={forView}
        form={form}
        setForm={setForm}
        errors={errors}
      />
    ),
    createData(
      <Preview
        onPreviewFile={onPreviewFile}
        desc={'Updated List of Services Offered with Rates *'}
        fileType={'service-offered-with-rates'}
        mainFiles={mainFiles}
        setMainFiles={setMainFiles}
        buttonText={'Upload'}
        form={form}
        forView={forView}
        setForm={setForm}
        errors={errors}
      />
    ),
    createData(
      <Preview
        onPreviewFile={onPreviewFile}
        desc={'List of Doctors to be Accredited *'}
        fileType={'list-of-doctors-accredited'}
        mainFiles={mainFiles}
        setMainFiles={setMainFiles}
        buttonText={'Upload'}
        form={form}
        forView={forView}
        setForm={setForm}
        errors={errors}
      />
    ),
    createData(
      <Preview
        onPreviewFile={onPreviewFile}
        desc={'Medical Directory'}
        fileType={'medical-directory'}
        mainFiles={mainFiles}
        setMainFiles={setMainFiles}
        buttonText={'Upload'}
        forView={forView}
        form={form}
        setForm={setForm}
        errors={errors}
      />
    ),
    createData(
      <Preview
        onPreviewFile={onPreviewFile}
        desc={<LocationMapWithSubscript />}
        fileType={'location-map'}
        mainFiles={mainFiles}
        setMainFiles={setMainFiles}
        buttonText={'Upload'}
        forView={forView}
        form={form}
        setForm={setForm}
        errors={errors}
      />
    ),
    createData(
      <Preview
        onPreviewFile={onPreviewFile}
        desc={'Check Payable Letter *'}
        fileType={'check-payable-letter'}
        mainFiles={mainFiles}
        setMainFiles={setMainFiles}
        buttonText={'Upload'}
        forView={forView}
        form={form}
        setForm={setForm}
        errors={errors}
      />
    ),
    createData(
      <Preview
        onPreviewFile={onPreviewFile}
        desc={<AcutalImageAreas />}
        fileType={'image-address'}
        mainFiles={mainFiles}
        setMainFiles={setMainFiles}
        buttonText={'Upload'}
        forView={forView}
        form={form}
        setForm={setForm}
        errors={errors}
      />
    ),
    createData(
      <Preview
        onPreviewFile={onPreviewFile}
        desc={<ApePackages />}
        fileType={'ape-package'}
        mainFiles={mainFiles}
        setMainFiles={setMainFiles}
        buttonText={'Upload'}
        forView={forView}
        form={form}
        setForm={setForm}
        errors={errors}
      />
    ),
    createData(
      <Preview
        onPreviewFile={onPreviewFile}
        desc={'Mobile APE'}
        fileType={'mobile-ape-packages'}
        mainFiles={mainFiles}
        setMainFiles={setMainFiles}
        buttonText={'Upload'}
        forView={forView}
        form={form}
        setForm={setForm}
        errors={errors}
      />
    ),
    createData(
      <Preview
        onPreviewFile={onPreviewFile}
        desc={'ECU Packages'}
        fileType={'ecu-package'}
        mainFiles={mainFiles}
        setMainFiles={setMainFiles}
        buttonText={'Upload'}
        forView={forView}
        form={form}
        setForm={setForm}
        errors={errors}
      />
    ),
    createData(
      <Preview
        onPreviewFile={onPreviewFile}
        desc={'Business Permit *'}
        fileType={'business-permit'}
        mainFiles={mainFiles}
        setMainFiles={setMainFiles}
        buttonText={'Upload'}
        forView={forView}
        form={form}
        setForm={setForm}
        errors={errors}
      />
    ),
    createData(
      <Preview
        onPreviewFile={onPreviewFile}
        desc={'BIR COR 2303 *'}
        fileType={'bir-cor-2303'}
        mainFiles={mainFiles}
        setMainFiles={setMainFiles}
        buttonText={'Upload'}
        forView={forView}
        form={form}
        setForm={setForm}
        errors={errors}
      />
    ),
    createData(
      <Preview
        onPreviewFile={onPreviewFile}
        desc={'DOH PERMIT *'}
        fileType={'doh-permit'}
        mainFiles={mainFiles}
        setMainFiles={setMainFiles}
        buttonText={'Upload'}
        forView={forView}
        form={form}
        setForm={setForm}
        errors={errors}
      />
    ),
    createData(
      <Preview
        onPreviewFile={onPreviewFile}
        desc={'SEC/DTI Registration *'}
        fileType={'sec-dti-registration'}
        mainFiles={mainFiles}
        setMainFiles={setMainFiles}
        buttonText={'Upload'}
        form={form}
        forView={forView}
        setForm={setForm}
        errors={errors}
      />
    ),
    createData(
      <Preview
        onPreviewFile={onPreviewFile}
        desc={'PhilHealth Accreditation'}
        fileType={'philhealth-accreditation'}
        mainFiles={mainFiles}
        setMainFiles={setMainFiles}
        buttonText={'Upload'}
        forView={forView}
        form={form}
        setForm={setForm}
        errors={errors}
      />
    )
  ]
  return (
    <TableContainer id='tableContainer' className={classes.tableContainer}>
      <UploadFileContext.Provider value={{ disabled: disable, isClickedSave }}>
        <Table className={classes.table} size='small' aria-label='a dense table'>
          <TableBody id='table-body'>
            {rows.map((row, index) => (
              <TableRow id='TableRow' className={classes['table-row']} key={index}>
                <TableCell id='description-row' className={classes['description-row']} component='th' scope='row'>
                  {row.description}
                </TableCell>
              </TableRow>
            ))}
          </TableBody>
        </Table>
      </UploadFileContext.Provider>
    </TableContainer>
  )
}

export default UploadFIleTable
