import React from 'react'
import Paper from '@material-ui/core/Paper'
import { useSelector } from 'react-redux'
import Button from '@material-ui/core/Button'
import classes from './FacilityInformation.module.scss'
import DialogBoxConfirmation from '../DialogBoxConfirmation/DialogBoxConfirmation'
import CustomAlertSnackBar from '../CustomSnackbar/CustomAlertSnackbar'
import AffiliationEncoderService from 'Services/Api/Partner/affiliationEncoderService'

const FacilityInformation = (props) => {
  const { uploadFileTable, dialogText, disable, setDisable, setIsClickSave, setFormStepperCount } = props
  const [isSave, setIsSave] = React.useState(false)
  const { medClinicForms } = useSelector((state) => state.medClinicAffiliationForm)
  const { affiliationType } = useSelector((state) => state.affiliationType)
  const { affiliationStatus, stageName } = affiliationType
  const [saveButtonDisabled, setSaveButtonDisabled] = React.useState(false)

  React.useEffect(() => {
    if (affiliationStatus === 'Submitted' || stageName === 'AFFILIATION_APPROVAL_FOR_OIC') {
      setDisable(true)
      setSaveButtonDisabled(true)
    }
  }, [affiliationStatus])

  const PrevPage = (e) => {
    e.preventDefault()
    props.history.goBack()
  }

  const handleSubmit = async () => {
    setIsClickSave(true)
    if (!disable) {
      try {
        let res = await AffiliationEncoderService.updateFacilities(affiliationType.id, {
          ...affiliationType,
          ...medClinicForms
        })
        setIsSave(true)
      } catch (error) {
        console.log('error in save', error)
      }
    }
  }

  return (
    <React.Fragment>
      <CustomAlertSnackBar isSave={isSave} setIsSave={setIsSave} />
      <div id='bodyContainer' className={classes.bodyContainer}>
        <div id='text-title' className={classes['text-title']}>
          <text>APPLICATION REQUIREMENTS</text>
        </div>
        <div id='tableContainer' className={classes.tableContainer}>
          <div id='uploadFileTable' className={classes.uploadFileTable}>
            <label id='tableTopText' className={classes.tableTopText}>
              <text>*Please Provide the following documents in order for your application to be processed.</text>
            </label>
            <Paper id='tableBox' className={classes.tableBox}>
              <div id='documentDesriptionText' className={classes.documentDesriptionText}>
                Document Description
              </div>
              <Paper id='uploadingOfDocuments' className={classes.uploadingOfDocuments}>
                {uploadFileTable}
              </Paper>
            </Paper>
          </div>
        </div>
        <div id='buttonContainer' className={classes.buttonContainer}>
          <div id='buttonDiv' className={classes.buttonDiv}>
            <div id='buttonBox' className={classes.buttonBox}>
              <div id='first-child'>
                <Button variant='contained' className={classes.ReturnButton} onClick={PrevPage}>
                  Return
                </Button>
              </div>
              <div id='second-child'>
                <Button
                  variant='contained'
                  disabled={saveButtonDisabled}
                  className={saveButtonDisabled ? classes.disabledButton : classes.SaveButton}
                  onClick={handleSubmit}
                >
                  <div style={{ fontSize: '12px' }}> Save </div>
                </Button>
              </div>
              <div id='third-child'>
                <DialogBoxConfirmation
                  setFormStepperCount={setFormStepperCount}
                  dialogText={dialogText}
                  setIsSave={setIsSave}
                  disable={disable}
                  setDisable={setDisable}
                />
              </div>
            </div>
          </div>
        </div>
        <div id='footer-division'>
          <label id='footer' className={classes.Footer}>
            Medical Clinic Affiliation Form 2 out of 2
          </label>
        </div>
      </div>
    </React.Fragment>
  )
}

export default FacilityInformation
