import React, { memo, useState, useEffect } from 'react'
import { generate } from 'shortid'
import AutoSuggest from 'Pages/Protected/Partner/Components/AutoSuggest/customInput'
import ClearIcon from '@material-ui/icons/Clear'
import classes from './DoctorSectionTwo.module.scss'
import TextField from 'Pages/Protected/Partner/AffiliationFormEncoder/Components/EncoderTextField'
import AffiliationEncoderService from 'Services/Api/Partner/affiliationEncoderService'
import CustomizedTextArea from '../../../../../../Components/CustomizeTextArea/CustomTextAreaWithRef'
import validation from './validation'
import isEmpty from 'lodash/isEmpty'

export const DoctorSectionTwo = props => {
  const { data, isSubmitted = false, setData, setIsValid, isValidate, defaultVal } = props
  const [form, setForm] = useState({
    facilityMpId: '',
    facilityName: ''
  })

  const [facilityInfo, setFacilityInfo] = useState({
    facilityMpId: '',
    facilityName: ''
  })
  const [errors, setError] = useState({})
  const [asstCoordinators, setAsstCoordinators] = React.useState([])
  const [facilityNames, setFaciltyNames] = React.useState([])
  const [isFetching, setIsFetching] = React.useState(false)
  const [facilityIds, setFacilityIds] = React.useState([])
  const [facilityNameSearchTerm, setFacilityNameSearchTerm] = React.useState('')
  const [facilityIdSearchTerm, setFacilityIdSearchTerm] = React.useState('')
  const [isFetchingIDs, setIsFetchingIDS] = React.useState(false)

  useEffect(() => {
    let nForm = { ...form }
    if (facilityInfo.facilityMpId === '' || facilityInfo.facilityName === '') {
      nForm.facilityMpId = ''
      nForm.facilityName = ''
      nForm.address = ''
      setForm(nForm)
      setIsValid(false)
    }
  }, [facilityInfo])

  useEffect(() => {
    const newData = { ...form }
    const assCor = [...asstCoordinators]
    const nData = { ...facilityInfo }
    const defaultVAlCoor = (defaultVal.asstCoordinators instanceof Array) ? defaultVal.asstCoordinators : []
    nData['facilityMpId'] = defaultVal && defaultVal['facilityMpId']
    nData['facilityName'] = defaultVal && defaultVal['facilityName']
    setFacilityInfo(nData)
    setForm({...newData, ...defaultVal})
    setAsstCoordinators([...assCor, ...defaultVAlCoor])
  }, [defaultVal])

  useEffect(() => {
    const newData = { ...data }
    setData(Object.assign(newData, form))
  }, [form])

  useEffect(() => {
    if (isValidate) {
      validate()
    }
  }, [isValidate])

  const validate = () => {
    const { newErrors } = validation(form)
    const nErrors = { ...errors }
    let isValid = isEmpty(Object.assign(nErrors, newErrors))
    console.log('is Valid sa validate', isValid)
    if (isValid) {
      return true
    } else {
      setError(Object.assign(nErrors, newErrors))
      return false
    }
  }

  const isValidForm = React.useMemo(() => {
    const nErrors = { ...errors }
    const { newErrors } = validation(form)
    let isValid = isEmpty(Object.assign(nErrors, newErrors))
    console.log('is Valid form', isValid)
    if (isValid) {
      return true
    } else {
      return false
    }
  }, [form, errors])

  useEffect(() => {
    setIsValid(isValidForm)
  }, [isValidForm])

  const handleOnChange = ({target: { value, name }}) => {
    const e = {...errors}
    let nform = {...form}
    nform[name] = value
    delete e[name]
    setError(e)
    setForm(nform)
  }

  const handleDoctorFacility = async (id, name) => {
    const nForm = { ...form }
    const nErrors = { ...errors }
    const nFacilityInfo = { ...facilityInfo }
    try {
      const {
        data: { data }
      } = await AffiliationEncoderService.getFacilityForDoctor({ id, name })
      let newForm = { ...nForm, ...data }
      if (data) {
        const { facilityName, facilityMpId } = data
        nFacilityInfo.facilityMpId = facilityMpId
        nFacilityInfo.facilityName = facilityName
        setFacilityInfo(nFacilityInfo)
        setForm(newForm)
        delete nErrors['facilityName']
        delete nErrors['facilityMpId']
        delete nErrors['address']
        setError(nErrors)
      }
    } catch (error) {
      console.log('handle facility doctor get element by id', error)
    }
  }

  const HandleChangeFacility = ({id, name}) => {
    if (name) {
      handleDoctorFacility(null, name)
    } else {
      handleDoctorFacility(id, null)
    }
  }

  const handleFacilityNameChange = async (value, name) => {
    const nForm = { ...facilityInfo }
    nForm['facilityName'] = value
    setFacilityInfo(nForm)
    setFacilityNameSearchTerm(value)
    setIsFetching(true)
    if (typeof value === 'string' && value.length >= 1) {
      try {
        const {
          data: { data }
        } = await AffiliationEncoderService.getFacilityName({ name: value })
        setFaciltyNames(data)
        setIsFetching(false)
      } catch (error) {
        setIsFetching(false)
        console.log('error occured', error)
      }
    }
  }

  const handleFacilityIdChange = async (value, name) => {
    const nForm = { ...facilityInfo }
    nForm['facilityMpId'] = value
    setFacilityInfo(nForm)
    setFacilityIdSearchTerm(value)
    setIsFetchingIDS(true)
    if (typeof value === 'string' && value.length >= 6) {
      try {
        const {
          data: { data }
        } = await AffiliationEncoderService.getFacilityIds({ id: value })
        setFacilityIds(data)
        setIsFetchingIDS(false)
      } catch (error) {
        setIsFetchingIDS(false)
        console.log('error occured', error)
      }
    }
  }

  const HandleAddAssistant = (value, name, index, id) => {
    let newForm = { ...form }
    let asst = [...asstCoordinators]
    for (let i of asst) {
      if (i.id === id) {
        i[name] = value
      }
    }
    newForm['asstCoordinators'] = asst
    setAsstCoordinators(asst)
    setForm(newForm)
  }

  const handleRemoveAssistCoor = id => {
    let assist = [...asstCoordinators]
    let newForm = { ...form }
    let newArr = assist.filter(contact => contact.id !== id)
    newForm['asstCoordinators'] = newArr
    setAsstCoordinators(newArr)
    setForm(newForm)
  }

  const handleAddAssistantCoor = () => {
    if (asstCoordinators.length <= 4) {
      setAsstCoordinators(currentAssistant => [
        ...currentAssistant,
        {
          id: generate(),
          asstCoordinatorName: '',
          asstContactNum: null
        }
      ])
    }
  }

  return (
    <div id='facilityInformationField' className={classes.facilityInformationField}>
      <div id='facilityInformation'>
        Facility Information <label id='facilityTobeAffiliated'>(Facility to be Affiliated)</label>
      </div>
      <div id='textFieldArea' className={classes.textFieldArea}>
        <div id='firstLayerInputs' className={classes['firstLayerInputs']}>
          <div id='facilityInput' className={classes.facilityInput}>
            <label>Facility Name *</label>
            <AutoSuggest
              error={errors['facilityName']}
              suggestions={facilityNames}
              disabled={isSubmitted}
              labelName='name'
              value={form['facilityName']}
              onChange={handleFacilityNameChange}
              name='name'
              searchTerm={facilityNameSearchTerm}
              onSelect={HandleChangeFacility}
              isFetching={isFetching}
              setSearchTerm={setFacilityNameSearchTerm}
            />
          </div>
          <div id='medicalPartnerId' className={classes.medicalPartnerId}>
            <label id='medicalPartnerId-label'> Medical Partner ID (Facility) *</label>
            <AutoSuggest
              suggestions={facilityIds}
              error={errors['facilityMpId']}
              disabled={isSubmitted}
              value={form['facilityMpId']}
              labelName='id'
              onChange={handleFacilityIdChange}
              name='id'
              searchTerm={facilityIdSearchTerm}
              onSelect={HandleChangeFacility}
              isFetching={isFetchingIDs}
              setSearchTerm={setFacilityIdSearchTerm}
              show={6}
            />
          </div>
        </div>
        <div id='SecondLayerInputs' className={classes['SecondLayerInputs']}>
          <div id='addressInput' className={classes.addressInput}>
            <CustomizedTextArea label={'Address *'}>
              <textarea
                id='Address'
                disabled={isSubmitted || true}
                className={classes.AddressField}
                name='address'
                type={'noSpecialChar'}
                onChange={handleOnChange}
                error={errors['address']}
                value={form['address']}
                style={{border: errors.address ? 'solid red 1px' : null}}
              />
            </CustomizedTextArea>
            {errors.address && <span className={classes.error}>{errors.address}</span>}
          </div>
        </div>
        <div id='ThirdLayerInputs' className={classes['ThirdLayerInputs']}>
          <div id='room-number-div' className={classes.roomNumber}>
            <label>Room Number *</label>
            <TextField
              disabled={isSubmitted}
              type={'noSpecialChar'}
              id='roomNumber'
              name='roomNumber'
              defaultValue={form['roomNumber']}
              error={errors['roomNumber']}
              onChange={handleOnChange}
            />
          </div>
          <div id='contact-number-fields' className={classes.contactNumber}>
            <label>Contact Number/s *</label>
            <TextField
              disabled={isSubmitted}
              id='contactNumber'
              name='contactNumbers'
              error={errors['contactNumbers']}
              // type={'number'}
              onChange={handleOnChange}
              defaultValue={form['contactNumbers']}
            />
          </div>
          <div id='Clinic-Schedule-div' className={classes['Clinic-Schedule-div']}>
            <label>Clinic Schedule *</label>
            <TextField
              disabled={isSubmitted}
              id='clinicSchedule'
              name='clinicSchedule'
              onChange={handleOnChange}
              defaultValue={form['clinicSchedule']}
              error={errors['clinicSchedule']}
            />
          </div>
        </div>
        <div id='FourthLayerInputs' className={classes['FourthLayerInputs']}>
          <div id='coordinators-input' className={classes['coordinators-input']}>
            <label>Coordinators <br /> Name of Main Coordinator*</label>
            <div>
              <TextField
                disabled={isSubmitted}
                id='mainCoordinator'
                name='mainCoordinatorName'
                error={errors['mainCoordinatorName']}
                type={'noSpecialChar'}
                defaultValue={form['mainCoordinatorName']}
                onChange={handleOnChange}
              />
            </div>
          </div>

          <div id='Contact-Number-division' className={classes['Contact-Number']}>
            <div>
              <label><br />Contact Number *</label>
              <TextField
                disabled={isSubmitted}
                id='mainCoordinatorContactNumber'
                name='mainContactNum'
                onChange={handleOnChange}
                error={errors['mainContactNum']}
                defaultValue={form['mainContactNum']}
              />
            </div>
          </div>
        </div>
        <div id='FifthLayerInputs' className={classes['FifthLayerInputs']}>
          <div id='namenameOfAssitantFields' className={classes.namenameOfAssitantFields}>
            <div id='nameOfAssitantInputs'>
              <label>Name of Assistant Coordinator</label>
            </div>
            <div id='Contact-Number-One-div' className={classes['Contact-Number-One-div']}>
              <label>Contact Number </label>
            </div>
          </div>
          <div id='contactFields' className={classes.AdditionalNameOfAssistantFields}>
            {asstCoordinators.map((items, index) => {
              return (
                <div key={items.id} id='AddAssistantWrapper' className={classes.AddAssistantWrapper}>
                  <div id='AddNewAssistantCoordinatorField'>
                    <TextField
                      disabled={isSubmitted}
                      id={`${index}-AddNewAssistantCoordinator`}
                      type={'noSpecialChar'}
                      name='asstCoordinatorName'
                      error={errors['asstCoordinatorName']}
                      defaultValue={items.asstCoordinatorName}
                      onChange={({ target: { value, name } }) =>
                        HandleAddAssistant(value, name, index, items.id)
                      }
                    />
                  </div>
                  <div>
                    <div id='Contact-Number-two-div' className={classes['Contact-Number-two-div']}>
                      <TextField
                        disabled={isSubmitted}
                        defaultValue={items.asstContactNum}
                        id={`${index}-AddNewAssistantCoorNumber`}
                        name='asstContactNum'
                        onChange={({ target: { value, name } }) =>
                          HandleAddAssistant(value, name, index, items.id)
                        }
                      />
                    </div>
                  </div>
                  {!isSubmitted && (
                    <span id='deleteIcon' className={items.id === 1 ? classes.hideDelete : classes.delete}>
                      <ClearIcon onClick={() => handleRemoveAssistCoor(items.id)} />
                    </span>
                  )}
                </div>
              )
            })}
          </div>
        </div>
      </div>
      <div id='addButtonAssitant' className={classes.addButtonAssitant}>
        <label onClick={isSubmitted ? null : handleAddAssistantCoor}>+Add Assistant Coordinator</label>
      </div>
    </div>
  )
}

export default memo(DoctorSectionTwo)
