import React, { memo } from 'react'
import classes from './doctorSpecialization.module.scss'
import DoctorSpecialization from 'Pages/Protected/Partner/Components/DoctorSpecialization'

const DoctorSpecializations = props => {
  const {
    disabled,
    isAddedType,
    errors = {},
    setError,
    label1 = 'Main Specialization *',
    label2 = 'Sub-Specialization',
    name1 = 'mainSpecialization',
    name2 = 'subSpecialization',
    isSecondRow = false,
    form = {},
    setForm
  } = props

  const handleDropdown = ({ target: {name, value} }) => {
    const nform = { ...form }
    nform[name] = value
    delete errors[name]
    setError(errors)
    setForm(nform)
  }
  return (
    <>
      <div id='14' className={isSecondRow ? classes.DropDowntextField : classes.InputContainerDropDown }>
      <label>{label1}</label>
        <DoctorSpecialization
          disabled={disabled}
          value={form[name1] && form[name1]}
          id='Main Specialization'
          error={errors[name1]}
          name={name1}
          onChange={handleDropdown}
        />
      </div>
      <div id='24' className={isSecondRow ? classes.DropDowntextField : classes.InputContainerDropDown }>
        <label>{label2}</label>
        <DoctorSpecialization
          disabled={disabled}
          error={errors[name2]}
          value={form[name2] && form[name2]}
          id='major'
          name={name2}
          onChange={handleDropdown}
        />
      </div>
    </>
  )
}

export default memo(DoctorSpecializations)
