import React, { useEffect, useRef, memo, useState } from 'react'
import classes from './Remarks.module.scss'

const RemarksComponent = (props) => {
  const {
    setData,
    isSubmitted = false,
    data,
    isValidate,
    setIsValid,
    hasPaddingLeft = true,
    defaultVal = {},
    label1 = 'External Remarks',
    label2 = 'Internal Remarks',
    name1 = 'externalRemarks',
    name2 = 'internalRemarks'
  } = props
  const [error, setError] = useState('')
  const [form, setForm] = useState({})

  const internalRef = useRef()
  const externalRef = useRef()

  useEffect(() => {
    const nForm = { ...form }
    nForm[name1] = defaultVal[name1] ? defaultVal[name1] : ''
    nForm[name2] = defaultVal[name2] ? defaultVal[name2] : ''
    setForm(nForm)
    externalRef.current.value = defaultVal[name1] ? defaultVal[name1] : ''
    internalRef.current.value = defaultVal[name2] ? defaultVal[name2] : ''
  }, [defaultVal])

  useEffect(() => {
    const nForm = { ...form }
    if (!nForm[name2]) {
      setIsValid(false)
    } else {
      setIsValid(true)
    }
  }, [form[name2]])

  useEffect(() => {
    const newData = { ...data }
    setData(Object.assign(newData, form))
  }, [form])

  useEffect(() => {
    if (isValidate) {
      validate()
    }
  }, [isValidate])

  const validate = () => {
    if (!form[name2]) {
      setError(`"${label2}" is required`)
    } else {
      setError('')
    }
  }

  const handleChange = ({target: {value, name}}) => {
    const nForm = { ...form }
    if (name === name1) {
      internalRef.current = value
      nForm[name] = value
    } else {
      internalRef.current = value
      nForm[name] = value
      setError('')
    }
    setForm(nForm)
  }

  return (
    <div id='remarksField' className={classes.remarksField} style={{ paddingLeft: hasPaddingLeft ? '3em' : 0 }}>
      <div className={classes.RemarksField}>
        <span>
          <div>
            <label>{label1}</label>
            <textarea
              name={name1}
              disabled={isSubmitted}
              onChange={handleChange}
              ref={externalRef}
            />
          </div>
          <div>
            <label>{label2} *</label>
            <textarea
              name={name2}
              error={error}
              disabled={isSubmitted}
              ref={internalRef}
              onChange={handleChange}
            />
            <label className={classes.error}>{error} </label>
          </div>
        </span>
      </div>
    </div>
  )
}

export default memo(RemarksComponent)
