import React from 'react'
import Button from '@material-ui/core/Button'
import classes from './index.module.scss'
import { useDispatch, useSelector } from 'react-redux'
import {
  SET_OPEN_CUSTOM_INPUT,
  SET_FACILITY_NAMES
} from '../../../../../../../Store/actions/Partner/MaintenanceTable'

const CustomInput = props => {
  const dispatch = useDispatch()
  const { itemIndex = '', value = '', uniqueId = '', size = '', type = '', facilityType = '', multiline = false } = props

  const newValue = value.map(items => ` ${items.name}`).join(',')
  const indexValue = itemIndex.toString()

  const HandleInput = () => {
    dispatch(SET_OPEN_CUSTOM_INPUT(true))
    dispatch(SET_FACILITY_NAMES({ facilityGroup: [ ...value ], facilityType, _id: uniqueId, indexValue }))
  }

  return (
    <React.Fragment>
      <Button variant='contained' id='button' onClick={HandleInput} className={classes.button} style={{width: size}}>
        <div id='button-content' className={classes.buttonContent}>
          {newValue}
        </div>
      </Button>
    </React.Fragment>
  )
}

export default CustomInput
