import React from 'react'
import useAsyncHook from '../../../CustomHooks/useAsyncHook2'
import classes from './index.module.scss'
import GetOfficeLocation from '../../../../../../Services/Api/Partner/GetOfficeLocation'
import { getNestedArrayOfObjects } from '../../../Components/Utils/utils'
import DropDown from '../InputFields/DropDown'

const OfficeLocationComponents = props => {
  const { name = '', value = '', onChange = null, error = '' } = props
  const [dataGet, errorGet, loadingGet, executeAsyncGet] = useAsyncHook((data, token) => GetOfficeLocation.getOfficeLocation(token))
  const [ officeLocData, setOfficeLocData ] = React.useState([])

  React.useEffect(() => {
    fetchOfficeLocation()
  }, [])

  React.useEffect(() => {
    if (loadingGet) {
      const Result = getNestedArrayOfObjects(dataGet, ['data'])
      const newArr = Result.map(items => {
        return {
          text: items.name,
          value: items.name
        }
      })
      setOfficeLocData(newArr)
    }
  }, [dataGet])

  const memoizeData = React.useMemo(() => officeLocData, [officeLocData])
  const newValue = value

  const fetchOfficeLocation = async () => {
    await executeAsyncGet({})
  }

  return (
    <React.Fragment>
      {memoizeData.length === 0 && <label style={{fontSize: '12px'}}>{'*Fetching...'}</label> }
      <DropDown
        items={memoizeData}
        name={name}
        disable={!(memoizeData.length > 0)}
        value={newValue}
        onChange={onChange}
        noBlankValue
        error={error}
      />
    </React.Fragment>
  )
}

export default React.memo(OfficeLocationComponents, (prevProps, nextProps) => {
  try {
    if (prevProps.name === nextProps.name && prevProps.value === nextProps.value && prevProps.error === nextProps.error &&
        prevProps.onChange === nextProps.onChange) {
      return true
    }
    return false
  } catch (e) {
    return false
  }
})
