import instance from '../instance'
// import axios from 'axios'

const axiosPd = instance('/p/d')

export default class ListOfAffiliatedFacilities {
  static saveAffiliatedFacilities (argParams, argCancelToken) {
    const data = { data: argParams.data, id: argParams.id, type: argParams.type }
    return axiosPd({
      url: '/partners/doctors/profile/affiliated-facilities',
      method: 'PUT',
      headers: { skip_generate_form_data: 1 },
      data
    })
  }

  static getAffiliatedFacilities (argParams, argCancelToken) {
    const params = {
      id: argParams.id,
      type: argParams.type,
      offset: argParams.offset,
      limit: argParams.limit,
      filter: argParams.filter
    }

    return axiosPd({
      url: `/partners/doctors/profile/affiliated-facilities/${params.id}`,
      method: 'GET',
      headers: { skip_generate_form_data: 1 },
      params,
      cancelToken: argCancelToken.cancelToken
    })
  }
}
