import instance from '../instance'
const axios = instance('/p/l')

export default class OfficeLocation {
  static async getOfficeLocation (argCancelToken) {
    return axios({
      url: 'partner/lookup/getOfficeLocation',
      method: 'GET',
      headers: { skip_generate_form_data: 1 },
      cancelToken: argCancelToken.cancelToken
    })
  }
}
