import React from 'react'
import classes from './index.module.scss'
import { InputBase } from '@material-ui/core'

const InputComponents = props => {
  const { type = '', name = '', value = '', onChange = null, multiline = null } = props

  return (
    <InputBase
      name={name}
      value={value}
      onChange={onChange}
      onKeyDown={e => DisableCharacters(e, type)}
      className={classes.inputBase}
      multiline={multiline}
      type={type}
    />
  )
}

const specialChar = RegExp(/[!@#$%^&*(),.?":;'`~_{}|<>/[]/)
const numbers = RegExp(/[!@#$%^&*(),?":;'=`~_{}|<>/[]/)
const forEmail = RegExp(/[!#$%^&*(),?":;'`~{}|<>]([[]]*)/)
const numbersOnly = RegExp(/^[\d\b]+$/)
const lettersOnly = RegExp(/^[a-z]+$/)

const DisableCharacters = (e, type) => {
  switch (type) {
    case 'number':
      if (
        e.key === 'e' ||
          e.key === 'E' ||
          e.keyCode === 189 ||
          e.keyCode === 111 ||
          e.keyCode === 106 ||
          e.keyCode === 107 ||
          e.keyCode === 109 ||
          e.keyCode === 221 ||
          lettersOnly.test(e.key) ||
          numbers.test(e.key)
      ) {
        return e.preventDefault()
      } else {
        return {}
      }

    case 'noSpecialChar':
      if (
        specialChar.test(e.key) ||
          e.keyCode === 187 ||
          e.keyCode === 191 ||
          e.keyCode === 189 ||
          e.keyCode === 219 ||
          e.keyCode === 221 ||
          e.keyCode === 220
      ) {
        return e.preventDefault()
      } else {
        return {}
      }

    case 'dashAndNum':
      if (
        specialChar.test(e.key) ||
          lettersOnly.test(e.key) ||
          e.keyCode === 187 ||
          e.keyCode === 219 ||
          e.keyCode === 191 ||
          e.keyCode === 221 ||
          e.keyCode === 220
      ) {
        return e.preventDefault()
      } else {
        return {}
      }

    case 'lettersOnly':
      if (
        specialChar.test(e.key) ||
          numbersOnly.test(e.key) ||
          e.keyCode === 187 ||
          e.keyCode === 189 ||
          e.keyCode === 219 ||
          e.keyCode === 191 ||
          e.keyCode === 221 ||
          e.keyCode === 220
      ) {
        return e.preventDefault()
      } else {
        return {}
      }
    case 'email':
      if (
        forEmail.test(e.key) ||
          e.keyCode === 187 ||
          e.keyCode === 189 ||
          e.keyCode === 219 ||
          e.keyCode === 191 ||
          e.keyCode === 221 ||
          e.keyCode === 220
      ) {
        return e.preventDefault()
      } else {
        return {}
      }

    default:
      return {}
  }
}

export default InputComponents
