import React from 'react'
import { InputBase, Paper } from '@material-ui/core'
import classes from './index.module.scss'

const InputHeader = props => {
  const { name = '', onChange = null, size = '' } = props
  return (
    <div id='inputHeader' className={classes.rootInput}>
      <Paper id='paperInput' style={{ width: size, height: 'auto' }}>
        <InputBase
          type='text'
          name={name}
          className={classes.inputBase}
          onChange={onChange}
          autoComplete='off'
        />
      </Paper>
    </div>
  )
}

export default React.memo(InputHeader, (prevProps, nextProps) => {
  try {
    if (prevProps.name === nextProps.name && prevProps.value === nextProps.value) {
      return true
    }
    return false
  } catch (e) {
    return false
  }
})
