import React, { useState, useEffect, memo } from 'react'
import classes from './AffiliationForm.module.scss'
import HospitalSectionOne from './sectionOne'
import HospitalSectionTwo from './SectionTwo'
import HospitalSectionThree from './sectionThree'
import HospitalSectionFour from './SectionFour'
import RemarksComponent from 'Pages/Protected/Partner/AffiliationFormEncoder/Components/RemarksComponent'

const AffiliationForm = (props) => {
  const {
    isSubmitted,
    setValid,
    affilData,
    isTovalidate,
    sectionOneData,
    sectionThreeData,
    statusData,
    remarksData,
    setHospitalForm
  } = props

  const [sectionOneForm, setSectionOneForm] = useState({})

  const [sectionTwoForm, setSectionTwoForm] = useState({})

  const [sectionThreeForm, setSectionThreeForm] = useState({})

  const [remarksForm, setRemarksForm] = useState({})

  const [affilForm, setAffilForm] = useState({})

  const [sectionOneIsValid, setSectionOneIsValid] = useState(false)

  const [sectionThreeIsValid, setSectionThreeIsValid] = useState(false)

  const [affilFormIsValid, setAffilFormIsValid] = useState(false)

  const [sectionTwoIsValid, setSectionTwoIsValid] = useState(false)

  const [remarksIsValid, setIsRemarksIsValid] = useState(false)

  useEffect(() => {
    const isValid = sectionOneIsValid && sectionThreeIsValid && sectionTwoIsValid && remarksIsValid && affilFormIsValid
    if (isValid) {
      setValid(true)
    } else {
      setValid(false)
    }
  }, [sectionOneIsValid, sectionTwoIsValid, sectionThreeIsValid, remarksIsValid, affilFormIsValid])

  useEffect(() => {
    let hospitalData = Object.assign({}, sectionOneForm, sectionTwoForm, sectionThreeForm, affilForm, remarksForm)
    setHospitalForm(hospitalData)
  }, [sectionOneForm, sectionTwoForm, sectionThreeForm, affilForm, remarksForm])

  return (
    <div id='root' className={classes.root}>
      <div className={classes.textContainer}>
        <div>FACILITY INFORMATION</div>
        <div>General Information</div>
      </div>
      <HospitalSectionOne
        data={sectionOneForm}
        setData={setSectionOneForm}
        setIsValid={setSectionOneIsValid}
        isValid={sectionOneIsValid}
        isValidate={isTovalidate}
        isSubmitted={isSubmitted}
        defaultVal={sectionOneData}
      />
      <HospitalSectionTwo
        data={sectionTwoForm}
        setData={setSectionTwoForm}
        setIsValid={setSectionTwoIsValid}
        isValid={sectionTwoIsValid}
        isValidate={isTovalidate}
        isSubmitted={isSubmitted}
        defaultVal={statusData}
      />

      <HospitalSectionThree
        data={sectionThreeForm}
        setData={setSectionThreeForm}
        setIsValid={setSectionThreeIsValid}
        isValid={sectionThreeIsValid}
        isValidate={isTovalidate}
        isSubmitted={isSubmitted}
        defaultVal={sectionThreeData}
      />

      <HospitalSectionFour
        data={affilForm}
        setData={setAffilForm}
        setIsValid={setAffilFormIsValid}
        isValid={affilFormIsValid}
        isValidate={isTovalidate}
        isSubmitted={isSubmitted}
        defaultVal={affilData}
      />

      <RemarksComponent
        data={remarksForm}
        setData={setRemarksForm}
        hasPaddingLeft={false}
        isValid={remarksIsValid}
        setIsValid={setIsRemarksIsValid}
        isValidate={isTovalidate}
        isSubmitted={isSubmitted}
        defaultVal={remarksData}
      />
    </div>
  )
}

export default memo(AffiliationForm)
