import React from 'react'
import { TableRow, TableCell } from '@material-ui/core'
import classes from './index.module.scss'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import HeaderInputs from './headerInput'
import { useDispatch } from 'react-redux'
import { SET_FILTER, SET_SORT } from '../Store/actions'

const Header = props => {
  const { headerProps = [] } = props
  const dispatch = useDispatch()

  const handlechange = React.useCallback(e => {
    const { name, value } = e.target
    dispatch(SET_FILTER({
      name, value
    }))
  }, [])

  React.useEffect(() => {
    return () => {
      dispatch(SET_FILTER({}))
    }
  }, [])

  return (
    <React.Fragment>
      <TableRow className={classes.TableRow}>
        {
          headerProps.map((items, index) => {
            const size = items.size ? items.size : null
            return (
              <TableCell
                component='th'
                scope='row'
                align='left'
                key={index}
                className={classes.TableCell}
                style={{width: size}}
              >
                <div id='root-div' className={classes.root}>
                  <div id='tableContainer' className={classes.tableContainer}>
                    <div id='header-name' className={classes.header} >
                      {items['id']}
                      {items.sortable &&
                      <FontAwesomeIcon
                        className={classes.sortIcon}
                        onClick={() => { dispatch(SET_SORT(items.accessor)) }}
                        icon='sort'
                        id={`sort-${index}`} /> }
                    </div>
                    <div id='textDescription' className={classes.textDescription}>
                      {items.searchable
                        ? <HeaderInputs
                          name={items.accessor}
                          onChange={handlechange}
                          size={size}
                        />
                        : <div className={classes.noInputFields} style={{width: size}} />
                      }
                    </div>
                  </div>
                </div>
              </TableCell>
            )
          })
        }
      </TableRow>
    </React.Fragment>
  )
}

export default Header
